.n-p2p-setting
    background: rgba(33, 59, 86, 0.39)
    border: 1px solid #89B0FD
    border-radius: 20px
    width: 100%
    min-height: 200px
    &__header 
        padding: 16px 24px 12px
        @media (max-width: 1104px)
            padding: 16px 12px 12px
    &__title
        color: #fff
        font-weight: 600
        font-size: 16px
        line-height: 20px
    & .n-profile-table__p2p-head
        position: relative
        width: calc( 100% + 14px )
        left: -7px
        border-radius: 0
        background: rgba(33, 59, 86, 0.39)
        border-top: 1px solid #89B0FD
        border-bottom: 1px solid #89B0FD
        backdrop-filter: blur(10px)
        @media (max-width: 768px)
            width: 100%
            border-radius: 10px
            left: 0
            border: 1px solid #89B0FD
        @media (max-width: 450px)
            grid-template-rows: repeat(3, auto)
            grid-template-columns: 33px auto auto 1fr 
    &__user 
        display: flex
        align-items: center
        @media (max-width: 1104px)
            width: 100%
            margin-bottom: 16px
    & .n-profile-table__p2p-name
        @media (max-width: 450px)
            grid-column: 2/5
    & .n-toolbar__avatar
        width: 33px
        height: 33px
        border-radius: 50px
        margin-right: 12px
    & .n-toolbar__mail
        font-weight: 600 
        font-size: 16px
        line-height: 1.25
    & .n-profile-table__p2p-head .n-toolbar__status
        margin-left: 9px
        list-style-type: none
        display: flex
        @media (max-width: 450px)
            margin-left: 8px
            margin-top: 8px
            grid-row: 2/3
            grid-column: 3/5
        @media (max-width: 380px)
            margin-left: 0px
            grid-row: 3/4
            grid-column: 1/5
    & .n-profile-table__p2p-status 
            grid-row: 2/3
            grid-column: 1/3
    & .n-toolbar__status-item
        position: relative
        font-size: 12px
        line-height: 21px
        padding-left: 23px
        margin-left: 13.5px
        &:first-child
            margin-left: 0
        @media (max-width: 768px)
            font-size: 10px
            margin-left: 5px
        &::before
            position: absolute
            content: ""
            width: 18px
            height: 18px
            background-image: url(../img/profile/uncheck.svg)
            background-repeat: no-repeat
            background-position: center
            background-size: cover
            left: 0
            top: 1px
        &--check::before
            background-image: url(../img/profile/check.svg)
    .n-profile-table__p2p-info-text 
        font-weight: 500
    .n-profile-table__p2p-info-text span
        font-size: 14px
        font-weight: 500
    & .n-table__blok::before, & .n-table__blok::after
        display: none
    & .n-table__blok
        width: calc( 100% + 14px )
        position: relative
        left: -7px
    & .n-table__head 
        border-radius: 0
    & .n-table__head .n-table__line, .n-table__line:nth-child(2n)
        background: #213151
    & .n-table__line
        @media (max-width: 1104px)
            width: calc( 100% + 14px )
            left: -7px
            position: relative
    & .n-table__cell
        font-size: 14px
    & .n-table__cell:first-child
        border-left: #213151
        border-radius: 0
        text-align: center
    & .n-table__cell:last-child
        border-right: #213151
        border-radius: 0
    & .n-table__body
        padding: 0 7px
    & .n-table__body .n-table__cell
        border-bottom: 1px solid #182c45
    & .n-table__head .n-table__cell
        border-bottom: 9px solid #182c45
    & .n-table__title.n-table__cell
        font-size: 14px
    & .n-profile-table__label-hint
        cursor: help
        display: inline-block
        position: relative
        margin-left: 8px
        top: 0px
        content: ""
        width: 16px
        height: 16px
        background-repeat: no-repeat
        background-position: center
        background-image: url(../img/profile/hint.svg) 
        &:hover, &:active 
            & .n-profile-table__label-popup
                display: block
    & .n-profile-table__label-popup
        display: none
        position: absolute
        width: 348px
        padding: 15px 9px
        background: #213151
        border: 1px solid #40A7EA
        border-radius: 8px
        bottom: calc(100% + 23px)
        left: -166px
        text-align: center
        &::after
            content: ""
            position: absolute
            bottom: -9px
            left: calc(50% - 32.5px)
            width: 65px
            height: 9px
            background-repeat: no-repeat
            background-position: center
            background-image: url(../img/profile/beak.svg) 
    & .n-profile-table__p2p 
        padding: 0 7px 16px
    & .n-profile-table__p2p-payments 
        max-width: 863px
        @media (max-width: 1200px)
            max-width: 700px
            margin-left: auto
            margin-right: auto
        @media (max-width: 1104px)
            max-width: 100%
        & .slick-list
            max-width: 100%
    & .n-profile-table__p2p-review-text
        font-size: 14px
    & .n-profile-table__p2p-select-label
        color: #fff
    & .n-profile-table__p2p-btn
        border: 2px solid #44AAEC
        @media (max-width: 768px)
            color: #40A7EA
            background: transparent
            -webkit-text-fill-color: initial
.body-wrapper.light 
    .n-p2p-setting__title
        color: #142546
    .n-p2p-setting
        background: rgba(237, 250, 255, 0.39)
    .n-p2p-setting .n-table__head .n-table__line, .n-p2p-setting .n-table__line:nth-child(2n)
        background-color: #FCFEFF
    .n-p2p-setting .n-table__head .n-table__cell
        border-bottom: 9px solid #EFF5F8
    .n-p2p-setting .n-table__body .n-table__cell
        border-bottom: 1px solid #EFF5F8
    .n-profile-table__p2p-info .n-table__title-line
        color: #40A7EA
        font-weight: 700
    .n-profile-table__p2p-select-label
        color: #142546
    .n-p2p-setting .n-profile-table__p2p-name
        color: #142546
        font-size: 14px
        font-weight: 500
    .n-p2p-setting .n-profile-table__p2p-select .select-toggle
        border: 2px solid #A9C8FF
