.n-wallet-credit 
  .n-toolbar__list.active
    background-image: url(../img/wallet/list.svg)
.n-wallet-table__credits
    display: flex
    @media (max-width: 1104px)
      display: block
    &-items
      flex: 1 0 531px
    &-item
      display: flex
      align-items: center
      margin-top: 6px
      background: #213151
      border-radius: 10px
      padding: 28px 39px 27px 16px
      @media (max-width: 1104px)
        flex-wrap: wrap
        padding: 12px 16px
      &:first-child
        margin-top: 0
    &-img
      background: #3D5481
      border-radius: 8px
      width: 48px
      height: 48px
      display: flex
      align-items: center
      justify-content: center
    &-text
      margin-left: 12px
      flex: 1 0 auto
      @media (max-width: 1104px)
        width: calc( 100% - 60px )
    &-name
      font-weight: 400
      font-size: 14px
      line-height: 17px
    &-info
      margin-top: 4px 
      color: #AFB6BA
      font-size: 12px
      line-height: 15px
      max-width: 194px
      @media (max-width: 1104px)
        max-width: 100%
    &-count
      padding-right: 32px
      padding-top: 5px
      padding-bottom: 5px
      @media (max-width: 1104px)
        margin-top: 16px
      &:last-child
        padding-left: 32px
        border-left: 1px solid #131d13
        padding-right: 0
        min-width: 115px
        @media (max-width: 1104px)
          border-left: 1px solid #DEEAFF
    &-currency
      font-weight: 700
      font-size: 14px
      line-height: 17px
      color: #40A7EA
    &-number
      font-weight: 400
      font-size: 14px
      line-height: 17px
      margin-top: 4px
      min-width: 76px
    &-description
      margin-left: 20px
      @media (max-width: 1104px)
        margin-left: 0px
        margin-top: 32px
    &-title
      font-weight: 600
      font-size: 16px
      line-height: 20px
    &-list
      margin-top: 16px
    &-condition
      padding-left: 18px
      font-weight: 400
      font-size: 12px
      line-height: 150%
      position: relative
      & b
        font-weight: 600
      &::before
        position: absolute
        content: ""
        width: 3px
        height: 3px
        border-radius: 50%
        left: 7px
        top: 7px
        background: #fff
    &-btns
      margin-top: 16px
      display: flex
      column-gap: 8px
    &-control
      cursor: pointer
      padding: 8px
      font-weight: 600
      font-size: 14px
      line-height: 17px
      background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
      border-radius: 7px
      border: 1px solid #44AAEC
      transition: all .3s linear
      &:hover
        background: #fff
        color: #44AAEC
.light .n-wallet
  .n-wallet-table__credits-condition::before
    background-color: #142546
  .n-wallet-table__credits-item
    background: #FCFEFF
  .n-wallet-table__credits-img
    background: #DEEAFF
  .n-wallet-table__credits-control
    color: #fff
  .n-wallet-table__credits-count:last-child
    border-left: 1px solid #deeaff