@media screen and (max-width: 1200px)
   .n-market
      &-wrapper
         & .container 
            max-width: 100%
            padding: 0 30px
      &__head
         &-currency
            margin-right: auto
      &__inner 
         grid-template-columns: 1fr 1fr
         grid-template-rows: repeat(6,auto)
      &__bidding
         border-right: none
      &__statistics-item
         flex: 1 0 calc(50% + 2px)
         &:last-child
            flex: 1 0 calc(50% - 2px)
            border-right: none
      &__pair
         grid-row: 6
         grid-column: 1/2
         border-bottom: none
         border-right: 4px solid #14223b
         width: calc(100% + 2px)
      &__history
         border-bottom: 4px solid #14223b
         border-right: none
      &__feed
         grid-row: 6
         grid-column: 2/3
         width: calc(100% + -2px)
         margin-left: 2px
   .light
      & .n-market__bidding
         border-right: none
      & .n-market__pair
         border-right: 4px solid #e5f4f8
         border-bottom: none
      & .n-market__statistics-item
         flex: 1 0 calc(50% + 2px)
         &:last-child
            flex: 1 0 calc(50% - 2px)
            border-right: none
      & .n-market__history
         border-bottom: 4px solid #e5f4f8
         border-right: none
@media screen and (max-width: 900px)
   .n-market .n-market__history .simplebar-track.simplebar-vertical
      right: -10px
   .n-market
      overflow: hidden
      & .container 
         max-width: 100%
         padding: 0
      &__inner
         margin-top: 12px
         grid-template-columns: 1fr
         grid-template-rows: repeat(5,auto)
      &__head
         grid-column: 1
         &-property
            &:nth-child(5)
               display: none
            &:nth-child(6)
               display: none
         &-logo
            display: none
         &-currency
            padding-left: 27px
            position: relative
            cursor: pointer
            &::after
               position: absolute
               content: ""
               left: 0
               width: 20px
               height: 100%
               background: url(../img/market/arrows.svg)
               background-size: contain
               background-repeat: no-repeat
               background-position: center
      &__bidding
         padding: 0 0 20px
         grid-column: 1
         display: block
         &::after
            display: none
         &-content.active
            display: block
         &-buy 
            display: none
         &-buy.active
            display: block
         &-actions
            display: block
         &-buy:not(:first-of-type)
            padding: 12px 26px 0 12px
      &__statistics
            grid-column: 1
            display: block
            &-item
               border-right: none
               &:first-child
                  border-bottom: 4px solid #14223b
            &-volume
               text-align: right
            &-row
               background-position: left
            &-row.active::before
               background-color: #FFD600
               background-image: none
               width: 4px
               height: 4px
               top: calc( 50% - 1px )
               left: 1px
               border-radius: 50%
      &__feed
         &-inner
            overflow-y: hidden
      &__history
         border-bottom: 0
         &-content:not([data-tab="history-tab-1"])
            .n-market__history-head
               margin-right: 12px
               padding-right: 7px
            .n-market__history-body
               margin-right: 12px
            .n-market__history-row
               padding-right: 7px
            .n-market__history-inner
               overflow-y: hidden
               width: 842px 
               min-width: 100%
               margin-top: 16px
               &--less
                  max-width: 100%
                  & .n-market__history-col
                     &:nth-child(1)
                        width: 12.5%
                     &:nth-child(2)
                        width: 15%
                     &:nth-child(3)
                        width: 15%
                     &:nth-child(4)
                        width: 15%
                     &:nth-child(5)
                        width: 22%
         &-tab--mobile + label
            display: inline-block
            margin-right: 0
         &-content:not([data-tab="history-tab-1"]) .simplebar-content:after
            display: none
      &__pair
         border-right: 0
         width: 85%
         height: calc(100% - 83px)
         position: fixed
         background: #14223B
         box-shadow: 4px 20px 20px 0px rgba(0, 0, 0, .3)
         border-radius: 0 16px 16px 0
         bottom: -1px
         z-index: 20
         padding-top: 16px
         left: -150%
         transition: all 0.3s linear
         border: 0
         &.active
            left: 0
         &-blur 
            position: fixed
            z-index: 10
            width: 105vw
            height: 100vh
            top: 0
            left: 0
            backdrop-filter: blur(10px)         
            &.active
               display: block
         &-close
            display: block
            background: url(../img/market/pairClose-w.svg) center no-repeat
            position: absolute
            top: 12px
            right: 8px
            width: 32px
            height: 32px
         &-title
            padding-left: 20px
            display: block
            font-weight: 600
            font-size: 20px
            line-height: 24px
            margin-bottom: 16px
         &-input
            padding: 5px 43px 6px
         &-btn 
            top: 7px          
         &-head 
            display: none
         &-body .n-market__pair-row.active
            display: grid
            grid-template-columns: repeat(3, auto)
            grid-template-rows: repeat(2, auto)    
         &-body .n-market__pair-name
            min-width: 29%
            grid-row: 1/3
            align-self: center
            text-align: left
         &-body .n-market__pair-price
            min-width: initial
            grid-row: 1/2
            grid-column: 3/4
         &-body .n-market__pair-favorite + label
            top: -2px
         &-body .n-market__pair-changes
            grid-column: 3/4
            grid-row: 2/3
            min-width: initial
            padding-right: 0
      &__feed
         display: none
         grid-column: 1
         &-head
            padding-left: 12px
            margin-right: 12px
            margin-bottom: 14px
         &-body
            padding-left: 12px
            height: 427px
            margin-right: 12px
         &-time
            text-align: left
   .light 
      .n-market__statistics-item:first-child
         border-bottom: 4px solid #E5F4F8
      .n-market__pair
         background: #FCFEFF
         border: 0
      .n-market__pair-title
         color: #142546
      .n-market__pair-close
            background: url(../img/market/pairClose.svg) center no-repeat
@media screen and (max-width: 576px)
   .n-market .simplebar-track.simplebar-horizontal
      left: 9px
      bottom: -20px
   .n-market .simplebar-track.simplebar-vertical
      right: -9px
   .n-market
      margin-top: 16px
      &-wrapper
         & .container 
            padding: 16px 18px
         .header
            padding: 0
      & .container 
         padding: 0
      &__head 
         grid-column: 1
         flex-wrap: wrap
         padding: 12px 21px 10px
         &-logo
            display: none
         &-currency
            width: 100%
         &-property
            margin-top: 12px
      &__inner
         margin-top: 0
      &__bidding
         grid-column: 1/3
         &-tabs 
            padding: 0 20px
         &-tab + label
            margin-right: 21px
         &-form
            margin-top: 14px
         &-buy
            padding: 14px 20px 0 20px
            &:not(:first-of-type)
               padding: 14px 20px 0 20px
         &-input
            flex: auto
            width: 137px
            max-width: 100%
            &--short
               width: 61px
            &--percent
               text-align: center
         &-plus
            right: 4px
         &-minus
            right: 4px
            bottom: 5px
         &-parameter
            width: 94px
      &__statistics
         &-title
            font-weight: 600
         &-head
            padding-right: 3px
         &-body
            padding-right: 3px
         &-item 
            padding: 12px 12px 9px 12px
         &-row
            padding: 2px 4px 2px 7px
            width: calc(100% + 10px)
         &-price
            width: 17%
         &-volume
            width: 25%
         &-type
            width: 24%
         &-total
            width: 27%
      &__history
         // height: 332px
         // padding: 9px 0 31px
         &-content:not([data-tab="history-tab-1"])
            .n-market__history-tab+label
               font-weight: 600
            .n-market__history-head 
               margin-bottom: 12px
            .n-market__history-inner
               margin-top: 13px
            .n-market__history-inner--less
               max-width: 100%
               & .simplebar-track.simplebar-vertical 
                  right: -10px
               & .n-market__history-row
                  align-items: start
               & .n-market__history-col
                  &:nth-child(1)
                     width: 15.5%
                     white-space: normal
                  &:nth-child(2)
                     width: 17.8%
                     padding: 0 4px
                  &:nth-child(3)
                     width: 30.2%
                     text-align: right
                     padding: 0 4px
                  &:nth-child(4)
                     width: 32.5%
                     text-align: right
      &__feed
         &-head 
            margin-top: 13px
            margin-bottom: 14px
         &-body
            height: 242px
@media screen and (max-width: 341px)
   .n-market__feed-body 
      height: 222px