.n-popup-p2p
    & .n-popup__inner
        background-color: transparent
    & .n-popup__content
        background: linear-gradient(105.58deg, #FD5454 0%, #ED2661 100%)
        box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.1)
        border-radius: 20px
        padding: 32px 68px 16px
        @media (max-width: 768px)
            padding: 32px 12px
        @media (max-width: 414px)
            max-width: calc( 100% - 24px )
            margin: 0 auto
    & .n-popup__p2p-head
        display: flex
        align-items: center
        padding-bottom: 12px
        border-bottom: 1px solid #FF8989
    & .n-popup__p2p-img
        flex: 0 0 91px
        width: 91px
        height: 91px
        @media (max-width: 768px)
            display: none
        & img
            width: 100%
    & .n-popup__p2p-text
        margin-left: 24px
        @media (max-width: 768px)
            margin-left: 0
    & .n-popup__p2p-title
        font-weight: 700
        font-size: 30px
        line-height: 1.2
        text-align: left
        @media (max-width: 768px)
            text-align: center
    & .n-popup__p2p-subtitle
        margin-top: 12px
        font-size: 12px
        line-height: 1.2
        text-align: left
        @media (max-width: 768px)
            text-align: center
    & .n-popup__p2p-description
        font-weight: 500
        max-width: 290px
        margin: 16px auto 0
    & .n-popup__p2p-card
        display: inline-block
        font-weight: 700
        margin: 26px auto 0
        color: #F3FFA9
        font-size: 30px
        line-height: 37px
        padding-left: 55px
        position: relative
        @media (max-width: 768px)
            font-size: 24px
        &::before
            position: absolute
            left: 0
            top: 50%
            transform: translateY(-50%)
            content: ""
            width: 43px
            height: 43px
            background: url(../img/p2p/card.svg) center no-repeat
            background-size: cover
    & .n-popup__p2p-warning
        position: relative
        width: 100%
        max-width: 100%
        left: -20px
        margin-top: 12px
        background: url(../img/p2p/popup-notice.png) center no-repeat
        background-size: 58px
        background-position: left center
        text-align: left
        padding-left: 74px
        font-weight: 500
        font-size: 14px
        line-height: 1.4
        @media (max-width: 768px)
            padding-left: 0
            background: none
            left: 0
            text-align: center
.n-popup-p2p--success
    & .n-popup__close
        background-color: #ffffff
    & .n-popup__content
        background: linear-gradient(104.92deg, #14B467 0%, #1DE083 100%)
    & .n-popup__p2p-description
        max-width: 100%
    & .n-popup__p2p-head 
        border-bottom: 1px solid #36E691
.n-popup-p2p--paid
    & .n-popup__content
        background: linear-gradient(104.92deg, #14B467 0%, #1DE083 100%)
    & .n-popup__p2p-description
        max-width: 100%
    & .n-popup__p2p-head 
        padding-bottom: 24px
        border-bottom: 1px solid #36E691
    & .n-popup__p2p-title
        text-align: center
    & .n-popup__control
        border: 2px solid #FFFFFF
        color: #fff
        background: none
        -webkit-text-fill-color: initial
        &:hover
            background: #fff
            color: #17C16F

    