.light .n-market
   & .n-market__inner
      background-color: #ffffff
   & .select-toggle
      color: #142546
   & .n-market__head
      color: #142546
      border-bottom: 4px solid #E5F4F8

   & .n-market__bidding
      border-right: 4px solid #E5F4F8
      border-bottom: 4px solid #E5F4F8
      &::after
         background: linear-gradient(180.01deg, rgba(214, 237, 248, 0) 3.19%, rgba(214, 237, 248, 0.970833) 100%)
   & .n-market__bidding-balance 
      color: #000000
   & .n-market__bidding-title
      color: #142546

   & .n-market__bidding-form
      color: #142546

   & .n-market__bidding-input
      border: 2px solid #DEEAFF
      color: #142546
      &.disabled
         outline: none
         background: #F8F8F8
         border: 2px solid #E9E9E9
         &::placeholder
            color: #7993ad

   & .n-market__pair
      border-bottom: 0

   & .n-market__pair-body .n-market__pair-name
      color: #142546
      &:hover
         color: #40A7EA
   & .n-market__pair-price
      color: #142546
   & .n-market__pair-changes
      color: #142546
   & .n-market__pair-row--red
      & .n-market__pair-changes
         color: #FD5454
   & .n-market__pair-row--green
      & .n-market__pair-changes
         color: #1BD079
      
   & .n-market__pair-row
         &:hover, &:active
               background-image: linear-gradient(90deg, #EFFAFF 0%, rgba(220, 240, 248, 0) 100%)
               background-position: 0 0

   & .n-market__pair-favorite
      &:checked + label
         background-image: url(../img/market/favorite-check.svg)

   & .n-market__pair-inner
      color: #142546

   & .n-market__feed-inner
      color: #142546

   & .n-market__pair-input
      background: #F4F9F8
      border: 1px solid #E2EEEC
      color: #7993ad

   & .n-market__statistics
      border-bottom: 4px solid #E5F4F8

   & .n-market__statistics-item
      border-right: 4px solid #E5F4F8

   & .n-market__statistics-row
      &:hover, &:active
         background: #DEEAFF
         background-size: 100% 100%

   & .n-market__statistics-inner
      color: #142546

   & .n-market__history
      border-right: 4px solid #E5F4F8

   & .n-market__history-inner
      color: #142546

   & .n-market__history-row
      &:nth-child(odd)
         background-color: #eef9ff
      &:hover, &:active
         background: #dbf1fd 
         background-size: 100% 100%

   & .n-market__feed
      color: #142546