.n-counters
  margin-top: 100px
  background: linear-gradient(97.39deg, rgba(20, 37, 70, 0.7) 0.62%, rgba(20, 35, 65, 0.7) 100%)
  padding: 67px 0
  font-family: 'Montserrat', sans-serif
  @media screen and (max-width: 1200px)
    padding: 18px 0
    margin-top: 80px
  @media screen and (max-width: 900px)
    margin-top: 40px
  .container
    display: flex
    justify-content: space-between
  &_item
    &_title
      font-weight: 700
      font-size: 70px
      line-height: 85px
      color: #40A7EA
      text-align: center
      @media screen and (max-width: 1200px)
        font-size: 40px
        line-height: 48px
        margin-bottom: 8px
    &_prompt
      color: #FFFFFF
      font-size: 16px
      line-height: 24px
      text-align: center
      @media screen and (max-width: 1200px)
        font-size: 12px
        line-height: 18px
@media screen and (max-width: 767px)
  .n-counters
    background: #142546
    & .container
      justify-content: flex-start
      flex-direction: column
      gap: 16px