.n-profile-table__extended-title
    margin-top: 12px
    font-weight: 600
    font-size: 16px
    line-height: 20px
    margin-bottom: 16px
    &:not(:first-child)
        margin-top: 32px
.n-profile-table__notification
    position: relative
    padding: 12px 12px 32px
    color: #fff
    &-inner
        display: flex
        justify-content: space-between
        @media (max-width: 900px)
            display: block
    &-title
        font-weight: 600
        font-size: 16px
        line-height: 20px
    &-subtitle
        max-width: 294px
        margin-top: 8px
        font-weight: 400
        font-size: 14px
        line-height: 17px
        color: #8D94A1
    &-column
        max-width: 382px
        &:not(:first-child) 
            @media (max-width: 900px)
                margin-top: 32px
    &-list
        margin-top: 16px
    &-item
        font-weight: 400
        font-size: 14px
        line-height: 17px
        padding-left: 20px
        position: relative
        & span 
            font-weight: 600
        & a 
            color: #40A7EA
        & .blue 
            font-weight: 400
            color: #40A7EA
        &::before
            position: absolute
            content: ""
            width: 16px
            height: 16px
            left: 0
            top: 0
            background-image: url(../img/profile/check.svg) 
            background-repeat: no-repeat
            background-position: center
            background-size: cover
        &:not(:first-child)
            margin-top: 8px
        &.uncheck::before
            background-image: url(../img/profile/uncheck-blue.svg) 
    &-settings
        display: inline-block
        margin-top: 16px
        cursor: pointer
        padding: 8px 30px
        background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
        border-radius: 7px
    &-extended
        margin-top: 32px
    &-next
        position: relative
        display: inline-block
        margin-top: 8px
        font-weight: 600
        font-size: 16px
        line-height: 1.5
        color: #40A7EA
        cursor: pointer
        &::after
            position: absolute
            content: ""
            width: 16px
            height: 16px
            right: -20px
            top: 4px
            background-image: url(../img/profile/next.svg) 
            background-repeat: no-repeat
            background-position: center
            background-size: cover
    &-mailing
        margin-top: 32px
        & .n-profile-table__notification-subtitle
            max-width: 322px
    &-info
        margin-top: 16px
        max-width: 366px
    &-text
        position: relative
        font-weight: 400
        font-size: 12px
        line-height: 1.5
        padding-left: 18px
        &::before
            position: absolute
            content: ""
            width: 2px
            height: 2px
            left: 8px
            top: 8px
            background: #fff
        &:not(:first-child)
            margin-top: 8px
    &-subtext
        width: calc(100% + 20px)
        position: relative
        left: -20px
        font-weight: 400
        font-size: 12px
        line-height: 15px
        color: #8D94A1
    &-hint
        display: none
        position: absolute
        bottom: 32px
        left: calc(50% - 117.5px)
        background: #213151
        border: 1px solid #40A7EA
        border-radius: 8px
        color: #fff
        text-align: center
        font-size: 12px
        padding: 12px 35px
        max-width: 235px
        @media (max-width: 1104px)
            position: fixed
    &-toogle
        margin-top: 16px
        width: 44px
        height: 22px
        background: #E5E5E5
        z-index: 0
        padding: 0
        appearance: none
        border: none
        cursor: pointer
        position: relative
        border-radius: 16px
        -moz-border-radius: 16px
        -webkit-border-radius: 16px
        transition: all 0.3s linear
        &:before
            content: ' '
            position: absolute
            left: 0px
            top: 0px
            width: 44px
            height: 22px
            background: #C3C7C8
            z-index: 1
            border-radius: 16px
            -moz-border-radius: 16px
            -webkit-border-radius: 16px
        &:after
            content: ' '
            height: 18px
            width: 18px
            border-radius: 50%
            background: #FFFFFF
            position: absolute
            z-index: 2
            top: 2px
            left: 2px
            transition: all 0.2s linear
        &.toogleOn
            &:before
                background: #1BD079 !important
            &:after
                transform: translateX(21px)
                
.light .n-profile-table__notification
    color: #142546
    .n-profile-table__notification-settings
        color: #fff
    .n-profile-table__notification-hint
        color: #142546
        background: #FFFFFF
        border: 1px solid #40A7EA
        filter: drop-shadow(0px 15px 20px rgba(0, 0, 0, 0.16))
    .n-profile-table__label-popup
        color: #142546
        background: #FFFFFF
    