.n-p2p-trade
    width: 100%
    max-width: calc( 100% - 305px )
    background: rgba(33, 59, 86, 0.39)
    border: 1px solid #89B0FD
    border-radius: 20px
    color: #fff
    padding-bottom: 12px
    @media (max-width: 1104px)
        border-radius: 10px
        max-width: initial
    &__head
        padding: 12px 24px
        display: flex
        align-items: center
        @media (max-width: 900px)
            display: block
            padding: 12px 20px
    &__title 
        font-weight: 600
        font-size: 20px
        line-height: 24px
        flex: 0 0 auto
        @media (max-width: 900px)
            margin-bottom: 12px
    &__sort-label
        font-size: 14px
        align-self: flex-end
        padding-bottom: 6px
        @media (max-width: 768px)
            padding-bottom: 0
            margin-bottom: 8px
            align-self: initial
    &__sort
        display: flex
        margin-left: auto
        flex: 0 0 auto
        @media (max-width: 768px)
            flex-direction: column
            align-items: flex-start
    &__filters
        display: flex
        align-items: center
        @media (max-width: 500px)
            flex-direction: column
            width: 100%
    &__filter
        position: relative
        margin-left: 16px
        @media (max-width: 768px)
            margin-left: 0
        @media (max-width: 500px)
            width: 100%
        &:not(:first-child)
            margin-left: 8px
            @media (max-width: 500px)
                margin-left: 0
                margin-top: 8px
                width: 100%
    &__filter-title 
        font-weight: 600
        font-size: 12px
        line-height: 1.3
        margin-bottom: 4px
    &__search
        width: 148px
        background: #213151
        border: 1px solid #3D5481
        border-radius: 7px
        outline: 0
        color: #fff
        font-weight: 400
        font-size: 12px
        line-height: 15px
        padding: 9px 30px 9px 9px
        &::placeholder
            color: #AFB6BA
        @media (max-width: 500px)
            width: 100%
    &__search-button
        position: absolute
        right: 9px
        bottom: 9px
        outline: 0
        border: 0
        background-color: transparent
        width: 16px
        height: 16px
        cursor: pointer
        background-image: url(../img/p2p/search.svg)
        background-size: contain
        background-repeat: no-repeat
    &__row
        display: flex
        align-items: center
    &__select
        text-align: left
        position: relative
        border-radius: 7px
        width: 148px
        @media (max-width: 500px)
            width: 100%
        & .n-p2p-trade__filter
            margin-left: 0
        & .n-p2p-trade__search
            width: 100%
            padding: 7.5px 9px 7.5px 30px
            border: 1px solid #5578BC
        & .n-p2p-trade__search-button
            background-image: url(../img/p2p/search-gray.svg)
            left: 9px
        &.active .n-p2p-trade__select-inner
            display: block
        &.active .n-p2p-trade__select-btn::after
            transform: rotate(180deg)
    &__select-btn
        position: relative
        top: 0
        left: 0
        color: #fff
        background: #213151
        border: 1px solid #3D5481
        border-radius: 7px
        outline: none
        padding: 9px 30px 9px 9px
        width: 100%
        text-align: left
        font-weight: 400
        font-size: 12px
        line-height: 15px
        cursor: pointer
        &::after
            position: absolute
            content: ""
            top: 12px
            right: 12px
            width: 12px
            height: 12px
            background-image: url(../img/wallet/arrow-select.svg)
            background-position: center
            background-repeat: no-repeat
            transition: all .3s linear
    &__select-inner
        padding: 9px 4px
        text-align: left
        flex-direction: column
        z-index: 10
        display: none
        position: absolute
        top: calc( 100% + 4px )
        right: 0
        width: 100%
        max-width: 100%
        height: auto
        box-sizing: border-box
        background: #213151
        border: 1px solid #3D5481
        border-radius: 7px
    &__select-checkboxs
        margin-top: 9px
        max-height: 160px
        padding-bottom: 0
        overflow-x: hidden
        &::-webkit-scrollbar
            width: 0
            -ms-overflow-style: none
            overflow: -moz-scrollbars-none
        & .simplebar-scrollbar:before
            background: #40A7EA
        & .simplebar-scrollbar.simplebar-visible:before
            opacity: 1
            border-radius: 100px
        .simplebar-track.simplebar-vertical
            width: 8px
    .n-p2p-trade__label
        position: relative
        font-weight: 400
        font-size: 12px
        line-height: 1.4
        padding: 3.5px 10px 3.5px 32px
        white-space: nowrap
        overflow: hidden
        text-overflow: ellipsis
        cursor: pointer
        text-align: left
        & a 
            color: #40A7EA
        &::before
            position: absolute
            content: ""
            left: 0
            top: calc(50% - 12px)
            width: 24px
            height: 24px
            border: 2px solid #3D5481
            border-radius: 3px
            transition: border-color 0.3s linear
        &--blue
            color: #40A7EA
        &--disabled
            pointer-events: none
            color: #8D94A1
            &::before
                border: 2px solid #8D94A1 !important
                background-image: url(../img/profile/check-gray.svg) !important
        & span
            color: #FD5454
    .n-p2p-trade__label.check
        &::before
            border: 2px solid #40A7EA
            background-image: url(../img/profile/check-blue.svg) 
            background-repeat: no-repeat
            background-position: center
    .n-p2p-trade__checkbox
        max-width: 384px
        margin-top: 8px
        &:first-child
            margin-top: 1px
        & + .n-p2p-trade__checkbox
            margin-top: 8px

.body-wrapper.light .n-p2p-trade
    background: rgba(237, 250, 255, 0.39)
    border: 1px solid #89B0FD
    color: #142546
    .n-p2p-trade__sort-label
        color: #8D94A1
    .n-p2p-trade__search
        background: #D9EDFF
        border: 1px solid #ECF3FF
    .n-p2p-trade__select-btn
        background: #D9EDFF
        border: 1px solid #ECF3FF
        color: #142546
    .n-p2p-trade__select-inner
        background: #D9EDFF
        border: 1px solid #ECF3FF
        color: #142546
    .n-p2p-trade__label::before
        border: 2px solid #89B0FD
    .simplebar-scrollbar:before
        background: #89B0FD
    .bank-green
        color:#009E32
    .bank-yellow
        color:#F3BE00
    .bank-red
        color:#FF1339