.n-profile-table__referrals
    padding: 12px
    @media (max-width: 1104px)
        padding: 12px 0
        & .n-table__cell
            height: 46px
    .n-pages
        margin-top: 16px
.n-profile-table__rules
    padding: 12px
    color: #fff
    h2
        font-weight: 600
        font-size: 16px
        line-height: 1.25
        margin-bottom: 16px
    p
        font-size: 12px
        line-height: 1.5
        & b 
            font-weight: 500
        & a 
          color: #40A7EA
        &:not(:first-child)
            margin-top: 6px
.n-profile .n-profile-table--referral .main
    grid-auto-rows: auto 847px
.n-profile-table__referral
    background: url(../img/profile/referral/referral-bg.png) center no-repeat
    background-size: 100% 100%
    @media (max-width: 1104px)
        background: url(../img/profile/referral/referral-bg-mob.png) center no-repeat
        background-size: 100% 100%
    & .simplebar-content
        overflow-x: hidden
    & .simplebar-horizontal 
        display: none
    &-main 
        display: flex
        padding: 12px 101px 0 52px
        @media (max-width: 1200px)
            padding: 12px 12px 0
        @media (max-width: 768px)
            display: block
    &-description 
        margin-right: 23px
        margin-top: 40px
        position: relative
        @media (max-width: 768px)
            margin-top: 0
            margin-right: 0
        &::after
            pointer-events: none
            position: absolute
            width: 240px
            height: 240px
            content: ""
            left: calc( 50% - 130px )
            bottom: -15px
            background: url(../img/profile/referral/saly.png) center no-repeat
            @media (max-width: 768px)
                width: 128px
                background-size: contain
                right: 0
                left: auto
    &-title 
        max-width: 315px
        font-weight: 700
        font-size: 24px
        line-height: 1.2
        color: #fff
        & span
            color: #40A7EA
    &-text
        margin-top: 16px
        font-weight: 400
        font-size: 16px
        line-height: 1.5
        color: #fff
        @media (max-width: 768px)
            max-width: 217px
    &-rules
        display: inline-block
        cursor: pointer
        margin-top: 16px
        font-weight: 600
        font-size: 16px
        line-height: 1.5
        color: #fff
        position: relative
        padding-right: 20px
        @media (max-width: 380px)
            font-size: 14px
        &::before 
            position: absolute
            content: ""
            width: 12px
            height: 16px
            background: url(../img/profile/back.svg) center no-repeat
            background-size: contain
            transform: rotate(180deg)
            right: 0
            top: 4px
            @media (max-width: 380px)
                top: 3px
    &-slider
        width: 389px
        background: rgba(33, 59, 86, 0.39)
        border: 1px solid #89B0FD
        backdrop-filter: blur(20px)
        border-radius: 20px
        @media (max-width: 768px)
            margin-top: 24px
            margin-left: auto
            margin-right: auto
        @media (max-width: 480px)
            margin-top: 24px
            margin-left: auto
            margin-right: auto
            width: 320px
            max-width: 100%
        .slick-track
            display: flex
        .slick-slide
            opacity: 0
            &.slick-active
                opacity: 1
        .slick-arrow
            z-index: 1
            position: absolute
            top: 95px
            left: 32px
            font-size: 0
            border: 0
            outline: 0
            width: 16px
            height: 16px
            background: url(../img/profile/referral/arrow.svg) center no-repeat
        .slick-arrow.slick-next 
            left: auto
            right: 32px
            transform: rotate(180deg)
    &-slider-item 
        width: 100%
        padding: 24px 19px
    &-slider-title
        text-align: center
        font-weight: 600
        font-size: 16px
        line-height: 1.25
        color:#fff
        margin-bottom: 6px
    &-slider-img
        text-align: center
        position: relative
        z-index: 5
        & img
            position: relative
            z-index: 5
        &::after 
            z-index: 1
            content: ""
            position: absolute
            width: 273px
            height: 52px
            left: calc( 50% - 136.5px )
            bottom: 0
            background: url(../img/profile/referral/circle.svg) bottom no-repeat
    &-slider-sale 
        font-family: 'Lora', serif
        text-align: center
        margin-top: 4px
        margin-bottom: 12px
        background: linear-gradient(224.77deg, #40A7EA 17.06%, #AF6EF0 87.23%)
        -webkit-background-clip: text
        -webkit-text-fill-color: transparent
        background-clip: text
        text-fill-color: transparent
        font-size: 24px
        line-height: 1.5
        font-weight: 700
        font-size: 24px
    &-slider-frame  
        max-width: 322px 
        margin: 0 auto
        padding: 6px 16px
        background: #213151
        border: 2px dashed #3D5481
        border-radius: 10px
        margin-bottom: 6px
        display: grid
        grid-template-columns: 1fr auto
        grid-template-rows: auto auto
    &-slider-label
        grid-column: 1 
        grid-row: 1 
        font-weight: 400
        font-size: 10px
        line-height: 1.5
        color: #fff
    &-slider-code
        grid-column: 1 
        grid-row: 2 
        font-weight: 500
        font-size: 14px
        line-height: 1.25
        letter-spacing: 0.06em
        color: #40A7EA
        white-space: nowrap
        overflow: hidden
        text-overflow: ellipsis
    &-slider-copy
        display: flex
        flex-direction: column
        justify-content: center
        height: 100%
        grid-column: 2
        grid-row: 1/3
        align-self: center
        padding-left: 12px
        border-left: 2px solid #3D5481
        border-radius: 0
        cursor: pointer
    &-slider-controls
        display: flex
        max-width: 322px 
        margin: 0 auto
    &-slider-invite
        flex-grow: 1
        background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
        border-radius: 10px
        padding: 12px 52.5px 12px 31.5px
        font-weight: 600
        font-size: 16px
        line-height: 1.5
        display: block
        color: #fff
        position: relative
        &::after
            position: absolute
            content: ""
            width: 12px
            height: 16px
            top: calc(50% - 8px)
            background: url(../img/profile/back.svg) center no-repeat
            transform: rotate(180deg)
            right: 42px
        &:hover, &:focus-visible
            color: #fff
            background: linear-gradient(180deg, rgba(45, 152, 222, 0.7) 0%, rgba(68, 170, 236, 0.7) 100%)
            bottom: 0
            outline: 0
    &-qr
        display: none
        @media (max-width: 768px)
            margin-top: 32px
            display: block
            text-align: center
    &-slider-qr
        cursor: help
        margin-left: 6px
        width: 48px
        height: 48px
        border: 1px solid #40A7EA
        border-radius: 10px
        background-color: transparent
        background-image: url(../img/profile/qr-small.svg)
        background-position: center
        background-repeat: no-repeat
        position: relative
        @media (max-width: 768px)
            display: none
        &:hover
            & .n-profile-table__referral-qr-hint 
                opacity: 1
                z-index: 10000
    &-qr-hint
        position: absolute
        z-index: -1
        opacity: 0
        transition: all .3s linear
        bottom: calc( 100% + 13px )
        width: 235px
        left: calc(50% - 117.5px)
        padding: 15px 6px
        background: #213151
        -webkit-border-radius: 8px
        border-radius: 8px
        text-align: center
        &::after
            content: ""
            position: absolute
            bottom: -9px
            left: -webkit-calc(50% - 32.5px)
            left: calc(50% - 32.5px)
            width: 65px
            height: 9px
            background-repeat: no-repeat
            background-position: center
            background-image: url(../img/profile/referral/beak.svg)
            @media (max-width: 1104px)
                display: none
    &-qr-title
        font-weight: 400
        font-size: 12px
        line-height: 15px
        margin-bottom: 15px
        color: #fff
    &-qr-label
        margin-top: 12px
        font-size: 14px
        line-height: 21px
        color: #fff
    &-qr-text
        margin-top: 4px
        position: relative
        text-align: center
        font-weight: 500
        font-size: 16px
        line-height: 19px
        letter-spacing: 0.06em
        color: #40A7EA
    &-how
        margin-top: 32px
        margin-left: 52px
        font-weight: 600
        font-size: 16px
        line-height: 1.5
        color: #fff
        @media (max-width: 1104px)
            margin-left: 0
            padding-left: 20px
            padding-right: 20px
    &-more
        margin-left: 52px
        font-weight: 600
        font-size: 16px
        line-height: 1.5
        padding-right: 20px
        display: inline-block
        position: relative
        padding-right: 20px
        margin-top: 8px
        color: #40a7ea
        cursor: pointer
        @media (max-width: 1104px)
            margin-left: 0
            padding-left: 20px
            padding-right: 20px
        &::before 
            position: absolute
            content: ""
            width: 16px
            height: 16px
            right: 0
            top: 4px
            background: url(../img/profile/arrow-blue.svg) center no-repeat
    &-history
        margin-top: 32px
        padding-left: 52px
        padding-right: 101px
        @media (max-width: 1104px)
            padding: 0 12px 
    &-history-head
        display: flex
        align-items: center
        justify-content: space-between
        margin-bottom: 8px
    &-history-title
        font-weight: 600
        font-size: 16px
        line-height: 1.5
        color: #fff
    &-history-more
        font-weight: 400
        font-size: 14px
        line-height: 1.21
        color: #40A7EA
        cursor: pointer
.light
    .n-profile-table__referral
        background: url(../img/profile/referral/referral-bg-white.png) center no-repeat
        background-size: 100% 100%
        @media (max-width: 1104px)
            background: url(../img/profile/referral/referral-bg-mob-white.png) center no-repeat
            background-size: 100% 100%
    .n-profile-table__referral-title
        color: #142546
    .n-profile-table__referral-text
        color: #142546
    .n-profile-table__referral-rules
        color: #142546
        &::before
            background: url(../img/profile/back-blue.svg) center no-repeat
            background-size: cover
            transform: rotate(0)
    .n-profile-table__referral-slider
        background: rgba(237, 250, 255, 0.39)
        border: 1px solid #89B0FD
        backdrop-filter: blur(20px)
    .n-profile-table__referral-slider-title
        color: #142546
    .n-profile-table__referral-slider .slick-arrow
        background: url(../img/profile/referral/arrow-blue.svg) center no-repeat
    .n-profile-table__referral-slider-img::after
        background: url(../img/profile/referral/circle-blue.svg) bottom no-repeat
    .n-profile-table__referral-slider-frame
        background: #FCFEFF
        border: 2px dashed #DEEAFF
    .n-profile-table__referral-slider-label
        color: #142546
    .n-profile-table__referral-slider-copy
        border-left: 2px solid #DEEAFF
    .n-profile-table__referral-qr-hint
        background: #FCFEFF
        border: 1px solid #40A7EA
    .n-profile-table__referral-qr-hint::after
        background-image: url(../img/profile/beak.svg)
    .n-profile-table__referral-qr-title
        color: #142546
    .n-profile-table__referral-qr-label
        color: #142546
    .n-profile-table__referral-how
        color: #142546
    .n-profile-table__referral-history-title
        color: #142546
    .n-profile-table__rules
        color: #142546