.btn 
    padding: 7px 16px
    font-size: 12px
    font-weight: 600
    line-height: 1.5
    color: $white
    border-radius: 17px
    background-color: $blue-l
    border: 1px solid transparent
    @include hover
        background-color: $hover-blue-l
    &:active
        background-color: $active-blue-l
    &--border
        background-color: transparent
        color: $blue-l
        border: 1px solid $blue-l
        @include hover
            background-color: $hover-blue-l
            border: 1px solid $hover-blue-l
            color: $white
        &:active
            border: 1px solid $active-blue-l
            background-color: $active-blue-l
            color: $white
    &--orange
        background-color: $orange
        @include hover
            background-color: $hover-orange
        &:active
            background-color: $active-orange

.link
    font-size: 14px
    font-weight: 600
    line-height: 1.4
    color: $white
    @include hover
        color: $blue-l
    &:active
        color: $grey-l
    .light &
        color: $dark
        @include hover
            color: $blue-l
        &:active
            color: $dark-grey
        