.n-market__head
	grid-column: 1/4
	grid-row: 1
	display: flex
	align-items: center
	padding: 15px 12px 13px
	align-content: center
	border-bottom: 4px solid #14223B
	color: #fff
	&-logo
		margin-right: 12px
	&-currency
		margin-right: 65px
		padding-bottom: 1px
		font-weight: 600
		font-size: 20px
		line-height: 1.22
	&-property
		font-weight: 500
		font-size: 14px
		line-height: 1.22
		min-width: 100px
		light &
			color: #142546
		dark &
			color: #fff
		&:not(:first-of-type)
			margin-left: 32px
	&-value
		display: block
		margin-top: 8px
		font-size: 12px
		line-height: 1.25
		color: #7993ad
		&--green
			display: inline-block
			color: #1BD079
			&:not(:first-child)
				margin-left: 16px
		&--red
			display: inline-block
			color:#FD5454
			&:not(:first-child)
				margin-left: 16px