@mixin hover
	@media (hover: hover)
		transition: all .1s ease-in-out
		&:hover
			@content

//  
@mixin animatebtn($smax: 1, $smin: 0.95)
	transform-origin: center center
	transition: all ease-out 255ms

	// &:hover
	// 	transform: scale($smax)

	&:focus
		outline: none
		transform: scale($smax) translateY(-1%)

	&:active
		transform: scale($smin)
