.n-profile
  font-family: 'Montserrat', sans-serif
  & img 
    max-width: 100%
    padding: 0
    margin: 0
  & ul, & ol 
    list-style-type: none
  & .n-toolbar
    min-height: 63px
  & .main 
    grid-auto-rows: auto 706px
  & .n-table 
    margin-bottom: 0
    @media (max-width: 1104px)
      padding: 0
      height: auto
  &-header
    margin-top: 32px
    text-align: center
    color: #ffffff
  &__title
    font-weight: 700
    font-size: 36px 
  &__subtitle
    font-weight: 500
    margin-top: 8px
    font-size: 14px
    line-height: 1.2
  &__sum
    font-weight: 500
    margin-top: 8px
    font-size: 12px
    line-height: 1.2
  &-table 
    margin-top: 24px
.light .n-profile
  .n-profile-header
    color: #142546