.n-first-screen
  // pointer-events: none
  background: url(../img/main/firstScreen2.png) no-repeat bottom center
  background-size: 100% 100%
  margin-top: -72px
  padding: 120px 0 0
  font-family: 'Montserrat', sans-serif
  overflow: hidden

  @media screen and (max-width: 1200px)
    margin-top: -83px
    background-size: 100% 100%

  @media screen and (max-width: 767px)
    background-color: #1B2039
    background-image: url(../img/main/firstScreenMob.png)
    background-repeat: no-repeat
    background-position: top center
    background-size: 100% 38%
    padding: 90px 0 0

  @media screen and (max-width: 576px)
    padding: 83px 0 0

  .container
    display: flex

    @media screen and (max-width:  1023px)
      max-width: 100% !important

  &__img-wrapper
    max-width: 100%
    margin-left: auto

    @media screen and (max-width: 900px)
      margin: 40px auto 0

    @media screen and (max-width: 767px)
      display: none

    & img 
      max-width: 100%

  &__text
    max-width: 437px
    margin-right: 125px
    display: flex
    flex-direction: column

    @media screen and (max-width: 1200px)
      margin-top: 0
      flex: 0 0 235px
      width: 235px
      min-width: initial
      margin-right: 15px

    @media screen and (max-width: 767px)
      flex: 0 0 100%
      max-width: 100%
      width: auto

    @media screen and (max-width: 576px)
      margin-top: 15px
      width: 300px
      min-width: 300px
      margin-right: 0

    h1
      font-weight: 700
      font-size: 43px
      line-height: 52px
      color: #FFFFFF
      margin-bottom: 42px

      @media screen and (max-width: 1200px)
        font-size: 30px
        line-height: 37px
        margin-bottom: 9px

      @media screen and (max-width: 767px)
        font-size: 30px
        line-height: 33px
        margin-bottom: 19px

      span
        display: block
        color: #40A7EA

    p
      font-weight: 600
      font-size: 16px
      line-height: 24px
      width: 100%
      color: #FFFFFF
      margin-bottom: 42px

      span
        margin-left: -4px

    a
      display: flex
      border-radius: 10px
      background: linear-gradient(90deg, #175D8A 0%, #213151 100%)
      padding: 12px 32px
      font-weight: 500
      color: #FFFFFF
      font-size: 15px
      line-height: 24px
      margin-right: auto
      position: relative
      z-index: 6

      @media screen and (max-width: 1200px)
        margin-top: auto
        background: #40A7EA

      @media screen and (max-width: 767px)
        font-size: 16px
        display: none

      svg
        margin: auto 0 auto 5px

      &:hover
        background: linear-gradient(180deg, rgba(45, 152, 222, 0.7) 0%, rgba(68, 170, 236, 0.7) 100%)

    .n-first-screen__link
      display: none

      @media screen and (max-width: 767px)
        font-weight: 600
        display: block
        width: 360px
        max-width: 100%
        margin: 0 auto
        background: #15233C
        border: 2px solid #1BD079
        border-radius: 53px
        padding: 18px 22px 18px 46px
        font-size: 18px
        line-height: 1.5
        position: relative

        &::after
          content: ""
          position: absolute
          height: 100%
          width: 12px
          right: 20px
          top: 0
          mask-image: url('../img/main/arrow-link.svg')
          background-color: #fff
          mask-position: center
          mask-repeat: no-repeat

        &::before
          content: ""
          position: absolute
          height: 100%
          width: 20px
          left: 15px
          top: 0
          mask-image: url('../img/main/dollar.svg')
          background-color: #fff
          mask-position: center
          mask-repeat: no-repeat

        & span
          display: block
          font-weight: 500
          font-size: 10px
          line-height: 1.5

        &--p2p
          margin: 15px auto
          border: 2px solid #40A7EA

          &::before
            mask-image: url('../img/main/rub.svg')

  &__market
    color: #fff
    display: flex
    width: 100%
    max-width: 626px
    margin-left: auto
    border-radius: 16px
    background: rgba(42, 63, 104, 0.64)
    max-height: 351px
    padding: 9px 9px 10px 16px

    @media screen and (max-width: 1200px)
      max-height: 337px
      max-width: 540px
      padding: 25px 16px 16px 26px

    & .n-market
      &__bidding
        padding: 0 0
        border: 0
        display: block
        flex: 1 0 307px
        max-width: 307px
        padding-left: 14px
        margin-left: 19px
        position: relative

        &::before
          content: ""
          position: absolute
          left: 0
          bottom: 0
          height: calc( 100% - 40px )
          width: 2px
          background-color: #253962

        &::after
          display: none

        &-content.active
          display: block

        &-buy 
          display: none

        &-buy.active
          display: block

        &-actions
          display: block

        &-buy:not(:first-of-type)
          padding: 12px 26px 0 12px

      &__statistics-item
        border: 0
        padding: 0

        @media screen and (max-width: 1200px)
          width: auto
          flex: 1 1 200px

      &__statistics-head
        font-weight: 600
        font-size: 14px
        line-height: 1.2
        color: #71ABCB
        padding-right: 0

      &__statistics-body
        height: auto
        max-height: 100%
        overflow: hidden
        margin-left: 0
        padding: 0
        margin-top: 15px

        @media screen and (max-width: 1200px)
          margin-top: 7px

          & .n-market__statistics-price
            font-weight: 700

      &__statistics-row
        background: none
        margin-left: 0
        padding-left: 0
        font-weight: 600
        margin-right: 0
        padding-right: 0

        @media screen and (max-width: 1200px)
          font-weight: 500
          margin-top: 5px

      &__statistics-body
        overflow: hidden

      &__statistics-price
        width: 20%

        @media screen and (max-width: 1200px)
          width: 74px

        &.red
          color: #F37F7F

        &.green
          color: #58FFAF

      &__statistics-volume
        width: 30%
        text-align: left

        @media screen and (max-width: 1200px)
          width: 107px
          text-align: right

      &__statistics-total
        width: 35%

        @media screen and (max-width: 1200px)
          display: none

      &__statistics-inner
        padding-top: 0
        margin-top: 0

      &__bidding-action + label
        border-radius: 7px
        cursor: pointer

      &__bidding-action:checked + label::after
        border-radius: 10px

      &__bidding-toggle
        width: 100%
        text-align: left
        font-weight: 600
        font-size: 12px
        line-height: 1.25
        color: #fff
        background: #213151
        border: 1px solid #3D5481
        border-radius: 10px
        padding: 10px 30px 10px 16px
        position: relative
        outline: 0

        @media screen and (max-width: 1200px)
          padding: 6px 30px 7px 16px

        &::after
          content: ""
          position: absolute
          right: 12px
          bottom: 12px
          width: 15px
          height: 8px
          background: url(../img/main/toggle.svg) center no-repeat
          transition: all .3s linear

        &.active + .n-market__bidding-tabs
          display: block

        &.active::after
          transform: rotate(180deg)

      &__bidding-tabs
        display: none
        margin-top: 2px
        position: absolute
        background: #213151
        border: 1px solid #3D5481
        box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.16)
        border-radius: 10px
        padding: 11px 16px
        z-index: 100
        width: 293px

      &__bidding-tab + label
        width: 100%
        display: block
        color: #fff
        font-weight: 600
        font-size: 12px
        line-height: 1.25

        &:not(:first-of-type)
          margin-top: 8px

      &__bidding-form
        @media screen and (max-width: 1200px)
          margin-top: 8px

      &__bidding-tab.active + label
        color: #40A7EA

      &__bidding-input
        background: #213151
        border: 2px solid #3D5481

        &:focus-visible
          border: 2px solid #40A7EA

      &__bidding-buy
        padding: 16px 0 0

        @media screen and (max-width: 1200px)
          padding: 8px 0 0

        &:not(:first-of-type)
          padding: 16px 0 0

          @media screen and (max-width: 1200px)
            padding: 8px 0 0

      &__bidding-balance
        display: flex
        justify-content: space-between
        font-size: 14px

        @media screen and (max-width: 1200px)
          font-size: 12px

      &__bidding-parameter
        width: calc(100% - 211px)

      &__bidding-btn
        margin-top: 13px

        @media screen and (max-width: 1200px)
          margin-top: 8px

