.n-exchange
  padding: 64px 0 79px
  background-color: #152443
  font-family: 'Montserrat', sans-serif
  position: relative
  overflow: hidden
  .container
    position: relative
    display: flex
    flex-wrap: wrap
    justify-content: space-between
    overflow-x: hidden
  &_title
    font-weight: 700
    font-size: 36px
    line-height: 44px
    text-align: center
    width: 100%
    margin-bottom: 32px
    color: #FFFFFF
    span
      color: #40A7EA
  &__list
    padding-top: 15px
    width: 100%
    display: flex
    flex-direction: column
    position: relative
    z-index: 3
    &-item
      max-width: 470px
      display: flex
      margin-bottom: 42px
      img
        width: 65px
        height: 65px
        &.light
          display: none
      p
        font-weight: 600
        color: #FFFFFF
        font-size: 24px
        line-height: 29px
        margin: auto 0 auto 20px
        width: calc(100% - 85px)
    a
      background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
      display: flex
      color: #FFFFFF
      font-weight: 600
      font-size: 16px
      line-height: 24px
      padding: 12px 34px
      border-radius: 10px
      margin-right: auto
      @media screen and (max-width:  1023px)
        padding: 7px 34px
      @include mobile
         margin: auto
      svg
        margin: auto 0 auto 5px
      &:hover
        background: linear-gradient(180deg, rgba(45, 152, 222, 0.7) 0%, rgba(68, 170, 236, 0.7) 100%)
  &_img
    position: relative
    z-index: 3
    &.light
      display: none
  &__payments 
    position: absolute
    display: flex
    justify-content: flex-end
    list-style-type: none
    margin-top: 30px
    right: 0
    bottom: 75px
  &__payment
    &:not(:first-of-type)
      margin-left: 18px
    img
      &.light
        display: none
  &__bidding
    overflow: hidden
    flex: 1 0 auto
    margin-left: 8px
    padding-top: 45px
    display: flex
    flex-direction: column
    margin-top: auto
    padding-bottom: 70px
    &-bg
      width: 970px
      height: 303px
      background: url(../img/main/exchange2.png) no-repeat center
      background-size: 970px 303px
      background-position-x: right
      position: absolute
      top: 50%
      transform: translateY(-50%)
      right: -13px
      @media screen and (max-width: 1399px)
        left: calc( 100% - 707px )
        top: 54%
      @media screen and (max-width: 900px)
        background-size: 700px auto
        right: -130px
        left: auto
  .n-market__bidding
    overflow: hidden
    background: rgba(42, 63, 104, 0.64)
    border-radius: 16px
    border: 0
    padding: 16px 12px 20px
    color: #fff
  .n-market__bidding::after
    height: calc( 100% - 21px )
    top: auto
    bottom: 0
    background: linear-gradient(180.01deg, #213151 -3.19%, rgba(21, 32, 52, 0.970833) 100%)
.n-exchange_title-br
  display: none
@media screen and (max-width: 1400px)
  .n-exchange__payments
    right: -64px
@media screen and (max-width: 1200px)
  .n-exchange
    padding: 32px 0 46px
    background: linear-gradient(97.39deg, #142546 0.62%, #142341 100%)
    margin-top: -2px
    .container
      flex-direction: column
      justify-content: flex-start
    &_title
      font-size: 30px
      line-height: 37px
      margin-bottom: 24px
    &__list
      padding-top: 0
      width: 100%
      &-item
        margin-bottom: 24px
        &:last-of-type
          margin-bottom: 32px
        img
          width: 41px
          height: 41px
          margin: auto 0
        p
          font-size: 20px
          line-height: 24px
          margin: auto 0 auto 12px
          width: 265px
    &__payments
      position: absolute
      bottom: 0 
      right: 0
    &_img
      width: 100%
      margin: 12px 0 0

@media screen and (max-width: 1023px)
  .n-exchange
      & .container
        max-width: 100%
        padding: 0 30px
  .n-exchange__payments
    right: -30px
@media screen and (max-width: 767px)
  .n-exchange
    background: #142546
    .container
      max-width: 100%
    &_title
      font-size: 27px
      line-height: 33px
    &__payments
      display: none
    &__bidding-bg
      display: none
    &__list-item p
      max-width: 100%
      width: 100%

@media screen and (max-width: 515px)
  .n-exchange_title-br
    display: block