.n-profile-table__hint
    position: relative
    &:hover
        & .n-profile-table__label-popup
            display: block
            color: #fff
            width: auto
            min-width: 190px
            padding: 12px
            left: calc( 50% -  95px)
            bottom: calc(100% + 20px)
            z-index: 5
            @media (max-width: 1104px)
                display: none
        & .n-profile-table__label-popup--short
            min-width: 130px
            left: calc( 50% - 65px)
.n-profile .n-profile-table--p2p .main
    grid-auto-rows: auto auto
    grid-template-areas: 'toolbar toolbar' 'menu table'
.n-profile .n-profile-table--p2p .n-toolbar__user
    @media (max-width: 1104px)
        margin-bottom: 0
.n-profile-table__p2p
    position: relative
    padding: 12px
    color: #fff
    &-title
        font-weight: 600
        font-size: 16px
        line-height: 20px
    &-head
        display: flex
        align-items: center
        background: #213151
        border-radius: 10px
        padding: 12px
        @media (max-width: 768px)
            display: grid
            grid-template-rows: repeat(3, auto)
            grid-template-columns: 33px auto auto
            justify-content: start
    &-avatar
        background: linear-gradient(135deg, #FFAE49 0%, #FF74DB 100%)
        font-weight: 600
        font-size: 12px
        line-height: 15px
        margin-right: 8px
        border-radius: 50%
        padding: 9px 6px
        color: #fff
        @media (max-width: 768px)
            grid-row: 1
            grid-column: 1
            width: 33px
            height: 33px
    &-name
        font-weight: 500
        font-size: 14px
        line-height: 17px
        display: flex   
        align-items: center
        &-edit 
            cursor: pointer
            pointer-events: all
            margin-left: auto
            border: none
            outline: none
            background-color: transparent
            font-size: 0
            margin-left: 8px
            margin-bottom: 2px
            height: 24px
            width: 24px
            background: url(../img/profile/api-edit.svg) center no-repeat
            background-size: cover
        @media (max-width: 768px)
            grid-row: 1
            grid-column: 2
            margin-left: 8px
    &-status
        margin-left: 16px
        font-weight: 500
        font-size: 10px
        line-height: 12px
        color: #FFFFFF
        background: linear-gradient(90.8deg, #FFAF47 0.62%, #FF6FE8 100%)
        border-radius: 100px
        padding: 4px 8px
        @media (max-width: 768px)
            grid-row: 2
            grid-column: 1/4
            width: max-content
            margin-top: 7px
            margin-left: 0
        @media (max-width: 400px) 
            grid-row: 3
        &--red 
            color: #fff
            background: #fd5454
    & .n-toolbar__status 
        margin-left: 0
        @media (max-width: 1104px)
            margin-left: 13px
        @media (max-width: 768px)
            grid-row: 1
            grid-column: 3
            margin-left: 13px
        @media (max-width: 400px) 
            grid-row: 2
            grid-column: 1/3
            margin-left: 0
            margin-top: 8px
    &-link
        margin-left: auto
        padding: 8px 16.5px
        border: 1px solid #44AAEC
        border-radius: 7px
        background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
        -webkit-background-clip: text
        -webkit-text-fill-color: transparent
        background-clip: text
        text-fill-color: transparent
        transition: all 0.3s linear
        cursor: pointer
        font-weight: 600
        font-size: 14px
        line-height: 17px
        @media (max-width: 768px)
            grid-row: 3
            grid-column: 1/4
            max-width: 300px
            margin: 13px 0 0
            text-align: center
        @media (max-width: 400px) 
            grid-row: 4
        &:hover, &:active 
            text-fill-color: initial
            -webkit-text-fill-color: initial
            color: #fff
            background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
    &-info
        margin-top: 32px
    &-info-head 
        display: flex
        @media (max-width: 768px)
            display: block
    &-info-text 
        margin-left: auto
        font-weight: 500
        font-size: 14px
        line-height: 17px
        @media (max-width: 768px)
            margin-top: 8px
        & span 
            margin-left: 11px
            font-size: 12px
    &-table
        margin-top: 8px
        box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05)
        @media (max-width: 768px)
            margin-top: 16px
            width: calc( 100% + 24px )
            left: -12px
            & .n-table__cell
                font-weight: 700
                font-size: 14px
            & .n-table__value
                width: 210px
                font-weight: 400
        & .n-table__value--blue 
            color: #40A7EA
        & .n-table__cell:not(:first-of-type)
            text-align: center
    &-setting
        margin-top: 32px
    &-selects
        margin-top: 16px
        display: flex
        @media (max-width: 900px)
            flex-wrap: wrap
            column-gap: 9px
            row-gap: 12px
    &-selects-item
        width: calc( 33.3333% -  4px)
        margin-right: 6px
        //@media (max-width: 1200px)
          //  width: calc( 26.5% -  4.5px)
        @media (max-width: 900px)
            width: calc( 50% -  4.5px)
            margin-right: 0
        @media (max-width: 550px)
            width: 100%
    &-select-label
        font-weight: 600
        font-size: 14px
        line-height: 17px
        color: #b1cbda
        margin-bottom: 4px
    &-select
        text-align: left
        position: relative
        &.select-show .select-dropdown 
            display: block
        &.select-show .select-toggle::after
            transform: rotate(180deg)
        & .select-toggle
            position: relative
            top: 0
            left: 0
            background: #213151
            border: 2px solid #3D5480
            border-radius: 10px
            outline: none
            padding: 10px 39px 10px 16px
            white-space: nowrap
            overflow: hidden
            text-overflow: ellipsis
            min-height: 48px
            width: 100%
            text-align: left
            font-weight: 400
            font-size: 14px
            line-height: 17px
            &::after
                transition: all .3s linear
                position: absolute
                content: ""
                top: 10px
                right: 16px
                width: 24px
                height: 24px
                background-image: url(../img/wallet/arrow-select.svg)
                background-position: center
                background-repeat: no-repeat
        & .select-dropdown
            padding: 8px 16px
            text-align: left
            flex-direction: column
            z-index: 10
            display: none
            position: absolute
            top: calc( 100% + 4px )
            right: 0
            width: 100%
            max-width: 100%
            height: auto
            box-sizing: border-box
            background: #213151
            border: 2px solid #3D5480
            box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.16)
            border-radius: 10px
            & .select-option
                cursor: pointer
                padding: 8px 0
                transition: all .3s linear
                &:not(:last-child)
                    border-bottom: 1px solid #3D5480
                &:hover
                    color: #40A7EA            
    &-btn
        width: calc( 33.3333% -  4px)
        height: 48px
        align-self: flex-end
        padding: 8px 16.5px
        border: 1px solid #44AAEC
        border-radius: 7px
        background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
        -webkit-background-clip: text
        -webkit-text-fill-color: transparent
        background-clip: text
        text-fill-color: transparent
        transition: all 0.3s linear
        cursor: pointer
        font-weight: 600
        font-size: 16px
        line-height: 24px
        // @media (max-width: 1200px)
            // width: calc( 19% -  4.5px)
        @media (max-width: 900px)
            width: calc( 50% -  4.5px)
        @media (max-width: 550px)
            width: 100%
        &:hover, &:active 
            text-fill-color: initial
            -webkit-text-fill-color: initial
            color: #fff
            background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
    &-payments
        margin-top: 32px
    &-payments-head
        display: flex
        justify-content: space-between
        @media (max-width: 768px)
            display: block
        & .n-profile-table__p2p-title
            align-self: flex-end
    &-payments-add
        background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
        border-radius: 10px
        padding: 8px 33.5px 8px 53.5px
        border: none
        outline: 0
        font-weight: 600
        font-size: 14px
        line-height: 17px
        color: #fff
        position: relative
        cursor: pointer
        @media (max-width: 768px)
            max-width: 201px
            margin-top: 8px
        &:hover
            background: linear-gradient(180deg, rgba(45, 152, 222, 0.7) 0%, rgba(68, 170, 236, 0.7) 100%)
        &::after
            position: absolute
            content: ""
            width: 16px
            height: 16px
            top: calc( 50% - 8px )
            left: 34.5px
            background-image: url(../img/wallet/plus-w.svg)
            background-size: cover
            background-position: center
            background-repeat: no-repeat
    &-payments-items
        margin-top: 16px
        overflow: hidden
        & .slick-track
            display: flex
        & .slick-dots
            display: flex
            justify-content: center
            margin-top: 0px
            & button 
                font-size: 0
                line-height: 0
                padding: 0
                background: #2D98DE
                border-radius: 50%
                width: 7px
                height: 7px
                border: 0
                outline: 0
                transition: all .3s linear
                margin: 0 8px
            & .slick-active button
                background: #213151
    &-payments-item
        pointer-events: none
        background: #213151
        border-radius: 10px
        padding: 12px
        display: flex
        align-items: center
        @media (max-width: 768px)
            flex-wrap: wrap
            row-gap: 16px
    &-payments-icon
        width: 32px
        height: 32px
        margin-right: 12px
    &-payments-block
        margin-right: auto
        @media (max-width: 768px)
            width: calc( 100% - 44px )
    &-payments-title
        font-weight: 400
        font-size: 14px
        line-height: 17px
    &-payments-text
        font-weight: 400
        font-size: 12px
        line-height: 15px
        color: #AFB6BA
    &-payments-controls
        margin-left: auto
        @media (max-width: 768px)
            width: 100%
            margin-left: 0
    &-payments-edit
        cursor: pointer
        pointer-events: all
        margin-left: auto
        border: none
        outline: none
        background-color: transparent
        font-size: 0
        height: 24px
        width: 24px
        background: url(../img/profile/api-edit.svg) center no-repeat
        background-size: cover
    &-payments-del
        cursor: pointer
        pointer-events: all
        margin-left: 12px
        border: none
        outline: none
        background-color: transparent
        font-size: 0
        height: 24px
        width: 24px
        background: url(../img/profile/api-del.svg) center no-repeat
        background-size: cover
    &-feedback 
        margin-top: 32px
    &-feedback-head
        display: flex
        align-items: flex-end
        justify-content: space-between
        @media (max-width: 768px)
            flex-direction: column-reverse
            align-items: flex-start
    &-numbers
        display: flex
        align-items: center
        margin-bottom: 4px
        @media (max-width: 768px)
            margin-top: 16px
            margin-bottom: 0
    &-number
        position: absolute
        z-index: -1
        opacity: 0
        & + label
            cursor: pointer
            color: #b1cbda 
            font-weight: 700
            font-size: 14px
            line-height: 17px
            margin-right: 16px
            @media (max-width: 768px)
                margin-right: 24px                
                & span 
                    display: none
            @media (max-width: 360px)
                margin-right: 8px
        &:checked + label
            color: #40A7EA
    &-stats
        display: grid
        grid-template-columns: repeat(4, auto)
        row-gap: 4px
        @media (max-width: 768px)
            margin-top: 16px
        & *
          align-self: center  
    &-stats-percent
        margin-right: 16px
        font-weight: 500
        font-size: 14px
        line-height: 17px
    &-stats-total
        margin-right: 16px
        font-size: 12px
        line-height: 15px
        color: #b1cbda
    &-stats-icon
        height: 21px
    &-stats-line
        position: relative
        width: 65px
        margin-left: 6px
        margin-right: 8px
        height: 4px
        border-radius: 10px
        background-color: #3d5481
        margin-top: 2px
        & span 
            position: absolute
            top: 0
            left: 0
            content: ""
            width: 65%
            height: 100%
            border-radius: 10px
            background-color: #1BD079
        & span.red 
            background-color: #FD5454
    &-reviews
        display: none
        margin-top: 12px
        &.active
            display: block
        & .n-pages
            margin-top: 16px
    &-review 
        display: grid
        background: #213151
        border-radius: 10px
        padding: 16px
        grid-template-columns: 64px 123px 85px auto
        @media (max-width: 768px)
            width: 100%
            grid-template-columns: 64px 123px 1fr
            grid-template-rows: auto auto
            row-gap: 16px
        & *
            align-self: center 
        &:not(:first-child)
            margin-top: 6px
    &-review-avatar
        margin-right: 16px
        width: 48px
        height: 48px
        text-align: center
        text-transform: uppercase
        background: #182C52
        font-weight: 600
        font-size: 14px
        line-height: 18px
        -webkit-border-radius: 50%
        border-radius: 50%
        padding: 15px 5px
        color: #fff
        overflow: hidden
        &.verified
          background: linear-gradient(135deg, #FFAE49 0%, #FF74DB 100%)
    &-review-info
         margin-right: 53px
    &-review-name 
        font-weight: 500
        font-size: 14px
        line-height: 17px
        margin-bottom: 11px
    &-review-date
        font-size: 14px
        line-height: 17px
        color: #afb6ba
    &-review-icon
        margin-right: 53px
    &-review-text
        @media (max-width: 768px)
            grid-row: 2
            grid-column: 1/4
            display: -webkit-box
            -webkit-line-clamp: 2
            -webkit-box-orient: vertical 
            text-overflow: ellipsis
            overflow: hidden
            max-height: 42px
            &.open 
                overflow: visible
                -webkit-line-clamp: initial
                max-height: initial
    &-review-less
        cursor: pointer
        font-size: 14px
        line-height: 1.5
        grid-column: 1/4
        color: #40A7EA
        display: none
        &.show 
            display: block
    &-review-img
        width: 32px
        height: 32px
        text-align: center
        display: flex
        flex-direction: column
        justify-content: center
        border-radius: 50%
        background-color: #3B3551
    &-review-img--like
        background-color: #213C54
    &-review-img--neutral
        background-color: #41576b8f
    &-notification
        display: none
        position: absolute
        bottom: 34px
        left: calc(50% - 117.5px)
        background: #213151
        border: 1px solid #40A7EA
        border-radius: 8px
        color: #fff
        text-align: center
        font-size: 12px
        padding: 12px 20px
        max-width: 235px
        @media (max-width: 1104px)
            position: fixed
.light 
    .n-profile-table__p2p
        color: #142546
    .n-profile-table__p2p-head
        background: #FCFEFF
    .n-profile-table__p2p-name
        color: #AFB6BA
    .n-profile .n-toolbar__status-item
        color: #142546
    .n-profile-table__p2p-title
        color: #142546
    .n-profile-table__p2p-info-text
        color: #142546
    .n-profile-table__p2p-select .select-toggle
        background: #FCFEFF
        border: 2px solid #DEEAFF
        color: #142546
    .n-profile-table__p2p-select.select-show .select-dropdown
        background: #FCFEFF
        border: 2px solid #DEEAFF
    .n-profile-table__p2p-select .select-dropdown .select-option
        color: #142546
    .n-profile-table__p2p-select .select-dropdown .select-option:not(:last-child)
        border-bottom: 1px solid #DEEAFF
    .n-profile-table__p2p-notification
        color: #142546
        background: #FFFFFF
        border: 1px solid #40A7EA
    .n-profile-table__p2p-payments-item 
        background: #FCFEFF
    .n-profile-table__p2p-stats-line
        background: #C8E7D8
    .n-profile-table__p2p-stats-line--red
        background: #EBDDDD
    .n-profile-table__p2p-review
        background: #FCFEFF
    .n-profile-table__p2p-review-img
        background: #FBEFF4
    .n-profile-table__p2p-review---like
        background: #ECFBF6
    .n-profile-table__p2p-review-avatar
        background: #89B0FD
        .verified
            background: linear-gradient(135deg, #FFAE49 0%, #FF74DB 100%)