.surface
	position: fixed
	@include flexing(space-between, center)
	left: 50%
	transform: translateX(-50%)
	width: 769px
	padding: 8px 32px
	border-radius: 16px
	transition: opacity 1.2s ease 0s, top 1.2s ease 0s
	overflow: hidden
	z-index: 1000

	// ?  -    
	color: #1C1C1E
	background-color: #DFEEFF
	border: 1px solid rgba(205,214,218,0.25)

	@include tablet
		left: 50%
		width: auto
		min-width: 375px
		width: fit-content
		max-width: 62%
		padding: 8px 16px

	@include smalltablet
		max-width: 75%

	@include smallmobile
		min-width: initial
		left: 0
		transform: none
		margin: 0 8px
		width: calc(100% - 16px)
		max-width: 100%

	// .dark &
	// 	color: #1C1C1E
	// 	background-color: #DFEEFF
	// 	border: 1px solid rgba(205,214,218,0.25)

	.light &
		color: rgba(255, 255, 255, 0.9)
		background: #1C1C1E
		border: 1px solid #000000

	&__text
		font-family: Inter
		font-size: 14px
		font-weight: 500
		line-height: 1.4
		max-width: 75% // 532/769

		& b
			font-weight: 700

		@include oversmalltablet
			padding-right: 16px

		@include smalltablet
			padding-right: 8px

	&__button
		flex: 0 1 fit-content
		font-family: Inter
		font-size: 16px
		font-weight: 700
		line-height: 1.2
		color: #ffffff
		background-color: #092743
		border-radius: 8px
		border: none
		padding: 7px 16px
		outline: none
		user-select: none
		-webkit-tap-highlight-color: transparent

		@include animatebtn

		@include smalltablet
			min-width: 108px

		.light &
			background-color: #7A27FF
