.n-profile .n-menu
    @media (max-width: 1104px)
        position: relative
    .n-menu__btn
        @media (max-width: 1104px)
            height: 46px
            @media (max-width: 1104px)
                &::before
                    top: 18px
                    transition: all 0.3s linear
                &::after
                    top: 26px
    .n-menu__list
        @media (max-width: 1104px)
            position: absolute
            top: calc(100% + 4px)
            z-index: 10
            background: #131D32
            box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05)
            border-radius: 10px
            width: 100%
    .n-menu__link 
        font-size: 12px
        padding-right: 19px
    .n-menu__verification::after
        -webkit-mask-image: url(../img/profile/menu1.svg)

    .n-menu__safety::after
        -webkit-mask-image: url(../img/profile/menu2.svg)

    .n-menu__notifications::after
        -webkit-mask-image: url(../img/profile/menu3.svg)

    .n-menu__setp2p::after
        -webkit-mask-image: url(../img/profile/menu4.svg)

    .n-menu__api::after
        -webkit-mask-image: url(../img/profile/menu5.svg)

    .n-menu__refs::after
        -webkit-mask-image: url(../img/profile/menu6.svg)
.n-profile .n-menu_active .n-menu__btn
    @media (max-width: 1104px)
        &::before
            top: 18px
        &::after
            top: 26px
