.img__zoom
  width: 100%
  height: 100%
  position: fixed
  padding: 30px 15px
  z-index: 10000
  bottom: 0
  left: 0
  display: flex
  flex-direction: column
  justify-content: center
  align-items: center
  background: rgba(19, 32, 51, 0.7)
  overflow: hidden
  &-wrapper
    position: relative
    box-sizing: border-box
    padding: 50px 30px 
    max-height: 100%
    max-width: 100%
    text-align: center
    @media screen and (max-width: 768px)
      margin: 45px 0
      padding: 0 25px 
  &-block
    border-radius: 20px
    object-fit: contain
    overflow: hidden
    position: relative
    max-width: 100%
    max-height: 100%
  &-close 
    position: absolute
    top: 0
    right: 0
    content: ""
    width: 32px
    height: 32px
    cursor: pointer
    background: url(../img/profile/popup-close.svg) center no-repeat
    @media screen and (max-width: 1023px)
      width: 20px
      height: 20px