.n-wallet .n-menu
    @media (max-width: 1104px)
        position: relative
    .n-menu__btn
        @media (max-width: 1104px)
            height: 46px
            @media (max-width: 1104px)
                &::before
                    top: 18px
                    transition: all 0.3s linear
                &::after
                    top: 26px
    .n-menu__list
        @media (max-width: 1104px)
            position: absolute
            top: calc(100% + 4px)
            z-index: 10
            background: #131D32
            box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05)
            border-radius: 10px
            width: 100%
    .n-menu__link 
        font-size: 12px
        padding-right: 19px
        font-weight: 500
        @media (max-width: 1104px)
            font-weight: 400
        &::after
            margin-top: -12px
    &__addresses::after
        -webkit-mask-image: url(../img/wallet/addresses.svg)

    .n-menu__wallet::after
        -webkit-mask-image: url(../img/wallet/wallet.svg)

    .n-menu__fia::after
        -webkit-mask-image: url(../img/operation-history/menu/history-account-icon.svg)

    .n-menu__crypto::after
        -webkit-mask-image: url(../img/operation-history/menu/cryptocurrency-history-icon.svg)

    .n-menu__credit::after
        -webkit-mask-image: url(../img/wallet/credit.svg)

    .n-menu__codes::after
        -webkit-mask-image: url(../img/operation-history/menu/ceibit-codes-icon.svg)

.n-wallet .n-menu_active .n-menu__btn
    @media (max-width: 1104px)
        &::before
            top: 18px
        &::after
            top: 26px
