.n-market
   flex: 1 1 auto
   font-family: 'Montserrat', sans-serif
   & .simplebar-track.simplebar-vertical 
      width: 7px
      top: -5px
      right: -6px
      pointer-events: auto
   & .n-market__history .simplebar-track.simplebar-vertical 
      top: -15px
      right: -11px
   & .n-market__feed .simplebar-track.simplebar-vertical 
      top: -11px
   & .simplebar-track.simplebar-horizontal 
      pointer-events: auto
      bottom: -19px
      height: 5px
   & .simplebar-scrollbar:before
      background: #152034
   & .simplebar-scrollbar.simplebar-visible:before
      opacity: 1
      border-radius: 100px
   &__inner
      margin-top: 20px
      margin-bottom: 32px
      background: #213151
      color: #fff
      border-radius: 24px
      width: 100%
      display: grid
      grid-template-columns: 1fr 1fr 348px
      grid-template-rows: repeat(5, auto)

@import 'market/market-head'
@import 'market/market-bidding'
@import 'market/market-statistics'
@import 'market/market-pair'
@import 'market/market-history'
@import 'market/market-feed'
@import 'market/market-light-theme'
@import 'market/market-breakpoints'
