body 
  font-family: 'Montserrat', sans-serif
.pages 
  text-align: center
  color: #ffffff
  padding: 50px 0
  & h1
    margin-bottom: 24px
  & h2
    margin-bottom: 16px
  & a 
    font-size: 16px
    margin-bottom: 8px
    color: inherit
    display: block
    transition: all .1s linear
    &:hover
      transform: scale(1.05)
    @include mobile
      font-size: 14px
.n-main 
  flex: 1 1 auto
  font-family: 'Montserrat', sans-serif
.body-wrapper
    display: flex
    flex-direction: column
    min-height: 100vh
    background: #061429
    & .container 
      max-width: 1194px
    & .header__theme-toggler 
      padding-right: 0
    & .select-toggle
      color: #fff
    &.light  
      background: linear-gradient(0deg, #BDE1F7 0%, #EAF7F9 100%)
      & .simplebar-scrollbar:before
        background: #DEF2FA
.copy-text
  position: relative
  cursor: pointer
  display: inline-block
  &::after
     content: ""
     position: absolute
     right: -31px
     top: -4px
     width: 22px
     height: 22px
     mask-image: url(../img/profile/f-copy.svg)
     mask-size: contain
     mask-position: center
     mask-repeat: no-repeat
     background-color: #ffffff
  &.copied::after
     mask-image: url(../img/profile/f-check.svg)
     background-color: #1BD079

.n-pass-eye
    content: ""
    position: absolute
    z-index: 6
    top: 46px
    right: 12px
    width: 24px
    height: 24px
    background-repeat: no-repeat
    background-position: center
    background-image: url(../img/eye.svg) 
    cursor: pointer
    &.close 
        background-image: url(../img/eye-close.svg) 
body
  overflow: hidden
  &.loaded
    overflow: auto
    .loader
      display: none
  .loader
    position: fixed
    top: 0
    right: 0
    bottom: 0
    left: 0
    background: #ffffff
    display: flex
    z-index: 100000
    height: 100vh
    width: 100vw
    img
      margin: auto

.inputWithCalc
  position: relative
  img
    padding: 2px
    background: #DCDCDC
    right: 15px
    position: absolute
    cursor: pointer
    &.disabled
      cursor: default
  .plusBtn
    top: 4px
  .minusBtn
    bottom: 4px
    transform: rotate(180deg)

.lg-backdrop
  background: rgba(19, 32, 51, 0.7)

.fancybox__caption
  text-align: center



.light 
  .copy-text
    &::after
      background-color: #061429
  .pages 
    color: #142546

