/*  */
.n-filter {
  z-index: 2;
  position: relative;
  margin-right: auto;
}

.n-filter__btn {
  position: relative;
  padding-left: 28px;
  width: 109px;
  height: 40px;
  border: none;
  border-radius: 7px;
  background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%);
}

.n-filter__open {
  -webkit-tap-highlight-color: transparent;
  display: none;
}

.n-filter__btn::before {
  content: url(../img/operation-history/filter-icon.svg);
  position: absolute;
  top: 9px;
  left: 8px;
  width: 24px;
  height: 24px;
}

.n-filter__text {
  font-size: 14px;
  font-weight: 600;
  color: #FFFFFF;
}

.n-filter__list {
  position: absolute;
  top: 41px;
  left: 0;
  display: none;
  margin: 0;
  padding-top: 12px;
  padding-right: 12px;
  padding-left: 12px;
  width: 229px;
  border-radius: 7px;
  background-color: #FCFEFF;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  list-style-type: none;
}

.n-main_load .n-filter__list {
  display: none !important;
}

.n-filter__item {
  position: relative;
  padding-bottom: 12px;
  padding-left: 18px;
}
.n-filter__item:not(:last-child) {
  margin-bottom: 12px;
  border-bottom: 1px solid #D9EAF0;
}

.n-filter__switch::before, .n-filter__switch::after {
  content: "";
  position: absolute;
  top: 0px;
  width: 18px;
  height: 18px;
  background-size: contain;
  background-position: center;
}
.n-filter__switch::before {
  right: 20px;
  background-image: url(../img/operation-history/direction-down.svg);
}
.n-filter__switch::after {
  right: 0;
  background-image: url(../img/operation-history/direction-up.svg);
}

.n-filter__more::after {
  content: "";
  position: absolute;
  top: 2px;
  right: 0;
  width: 16px;
  height: 16px;
  background-image: url(../img/operation-history/arrow.svg);
  background-size: contain;
  background-position: center;
}

.n-filter__triger {
  position: relative;
  display: block;
  cursor: pointer;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.n-filter__triger::before {
  content: ".";
  position: absolute;
  left: -13px;
  bottom: -6px;
  font-size: 40px;
}

.n-filter__options {
  display: none;
  padding-top: 12px;
  padding-left: 0;
  list-style-type: none;
}

.n-filter__option:not(:last-child) {
  margin-bottom: 8px;
}

.n-filter__item_active .n-filter__options {
  display: block;
}
.n-filter__item_active::before, .n-filter__item_active::after {
  transform: rotate(180deg);
