body {
  font-family: "Montserrat", sans-serif;
}

.pages {
  text-align: center;
  color: #ffffff;
  padding: 50px 0;
}
.pages h1 {
  margin-bottom: 24px;
}
.pages h2 {
  margin-bottom: 16px;
}
.pages a {
  font-size: 16px;
  margin-bottom: 8px;
  color: inherit;
  display: block;
  transition: all 0.1s linear;
}
.pages a:hover {
  transform: scale(1.05);
}
@media (max-width: 576px) {
  .pages a {
    font-size: 14px;
  }
}

.n-main {
  flex: 1 1 auto;
  font-family: "Montserrat", sans-serif;
}

.body-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: #061429;
}
.body-wrapper .container {
  max-width: 1194px;
}
.body-wrapper .header__theme-toggler {
  padding-right: 0;
}
.body-wrapper .select-toggle {
  color: #fff;
}
.body-wrapper.light {
  background: linear-gradient(0deg, #BDE1F7 0%, #EAF7F9 100%);
}
.body-wrapper.light .simplebar-scrollbar:before {
  background: #DEF2FA;
}

.copy-text {
  position: relative;
  cursor: pointer;
  display: inline-block;
}
.copy-text::after {
  content: "";
  position: absolute;
  right: -31px;
  top: -4px;
  width: 22px;
  height: 22px;
  mask-image: url(../img/profile/f-copy.svg);
  mask-size: contain;
  mask-position: center;
  mask-repeat: no-repeat;
  background-color: #ffffff;
}
.copy-text.copied::after {
  mask-image: url(../img/profile/f-check.svg);
  background-color: #1BD079;
}

.n-pass-eye {
  content: "";
  position: absolute;
  z-index: 6;
  top: 46px;
  right: 12px;
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../img/eye.svg);
  cursor: pointer;
}
.n-pass-eye.close {
  background-image: url(../img/eye-close.svg);
}

body {
  overflow: hidden;
}
body.loaded {
  overflow: auto;
}
body.loaded .loader {
  display: none;
}
body .loader {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #ffffff;
  display: flex;
  z-index: 100000;
  height: 100vh;
  width: 100vw;
}
body .loader img {
  margin: auto;
}

.inputWithCalc {
  position: relative;
}
.inputWithCalc img {
  padding: 2px;
  background: #DCDCDC;
  right: 15px;
  position: absolute;
  cursor: pointer;
}
.inputWithCalc img.disabled {
  cursor: default;
}
.inputWithCalc .plusBtn {
  top: 4px;
}
.inputWithCalc .minusBtn {
  bottom: 4px;
  transform: rotate(180deg);
}

.lg-backdrop {
  background: rgba(19, 32, 51, 0.7);
}

.fancybox__caption {
  text-align: center;
}

.light .copy-text::after {
  background-color: #061429;
}
.light .pages {
  color: #142546;
}