.n-profile .n-table
    overflow: hidden
.n-profile .n-table--settings
    display: none
    position: relative
    & .n-profile-table__head 
        padding: 7px 20px
    & .n-profile-table__api-controls
        @media (max-width: 1104px)
            margin-top: 0
.n-profile-table__main
    height: calc(100% - 47px)
.n-profile .n-profile-table
    & .container.main
        @media (max-width: 1104px)
            padding: 0
    &__head
        display: flex
        align-items: center
        padding: 7px 24px
        height: 47px
        overflow: hidden
        background: #213151
        box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05)
        border-radius: 14px 14px 0px 0px
        @media (max-width: 1104px)
            flex-wrap: wrap
            border-radius: 0px
            height: auto
            padding: 14px 20px
        &::before, &::after
          content: ''
          position: absolute
          top: 0
          width: 18px
          height: 47px
          background-color: #213151
        &::before
            left: -0
            border-top-left-radius: 14px
            @media (max-width: 1104px)
                display: none
        &::after
            right: -0
            border-top-right-radius: 14px
            @media (max-width: 1104px)
                display: none
    &__title
        font-weight: 600
        font-size: 16px
        color: #ffffff
        @media (max-width: 1104px)
            width: 100%
            line-height: 20px
        &--extended
            margin-left: 16px
            @media (max-width: 1104px)
                width: auto
                margin-left: 0
    &__checkbox
        &:not(:first-of-type)
            margin-top: 8px
    &__label
        position: relative
        font-size: 12px
        line-height: 1.4
        padding: 3.5px 0 3.5px 32px
        cursor: pointer
        &::before
            position: absolute
            content: ""
            left: 0
            top: 0
            width: 24px
            height: 24px
            border: 2px solid #3D5481
            border-radius: 3px
            transition: border-color 0.3s linear
        &--blue
            color: #40A7EA
        &--disabled
            pointer-events: none
            color: #8D94A1
            &::before
                border: 2px solid #8D94A1 !important
                background-image: url(../img/profile/check-gray.svg) !important
        & span
            color: #FD5454
    &__label.check
        &::before
            border: 2px solid #40A7EA
            background-image: url(../img/profile/check-blue.svg) 
            background-repeat: no-repeat
            background-position: center
    &__label--flex 
        display: flex
    &__label-hint
        cursor: help
        display: inline-block
        position: relative
        margin-left: 8px
        top: 0px
        content: ""
        width: 16px
        height: 16px
        background-repeat: no-repeat
        background-position: center
        background-image: url(../img/profile/hint.svg) 
        &:hover, &:active 
            & .n-profile-table__label-popup
                display: block
    &__label-popup
        display: none
        position: absolute
        width: 348px
        padding: 15px 9px
        background: #213151
        border: 1px solid #40A7EA
        border-radius: 8px
        bottom: calc(100% + 23px)
        left: -166px
        text-align: center
        &::after
            content: ""
            position: absolute
            bottom: -9px
            left: calc(50% - 32.5px)
            width: 65px
            height: 9px
            background-repeat: no-repeat
            background-position: center
            background-image: url(../img/profile/beak.svg) 
    &__api
        padding: 12px 0
        color: #fff
        &-controls
            display: flex
            align-items: center
            margin-left: auto
            @media (max-width: 1104px)
                margin-top: 8px
                margin-left: 0
        &-control
            font-weight: 600
            font-size: 14px
            line-height: 1.22
            padding: 8px 10px
            border: 1px solid #44AAEC
            border-radius: 7px
            background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
            -webkit-background-clip: text
            -webkit-text-fill-color: transparent
            background-clip: text
            text-fill-color: transparent
            transition: all 0.3s linear
            cursor: pointer
            &:not(:first-child)
                margin-left: 6px
            &:hover, &:active 
                text-fill-color: initial
                -webkit-text-fill-color: initial
                color: #fff
                background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
        &-title
            padding: 0 12px
            font-weight: 600
            font-size: 16px
            line-height: 1.25
        &-list
            margin-top: 16px
            margin-bottom: 32px
            padding: 0 12px
        &-text
            padding-left: 16px
            position: relative
            &::after
                position: absolute
                left: 6px
                top: calc(50% - 1px)
                content: ""
                width: 2px
                height: 2px
                background-color: #fff
            &:not(:first-child)
                margin-top: 12px
        &-items
            margin-top: 8px
            padding: 0 6px
        &-item
            padding: 15px 25px
            background: #213151
            border-radius: 10px
            display: flex
            align-items: center
            &:not(:first-child)
                margin-top: 6px
        &-name
            font-size: 14px
            line-height: 1.2
        &-edit
            margin-left: auto
            border: none
            outline: none
            background-color: transparent
            font-size: 0
            height: 24px
            width: 24px
            background: url(../img/profile/api-edit.svg) center no-repeat
            background-size: cover
        &-del
            margin-left: 12px
            border: none
            outline: none
            background-color: transparent
            font-size: 0
            height: 24px
            width: 24px
            background: url(../img/profile/api-del.svg) center no-repeat
            background-size: cover
        &-back
            content: ""
            width: 24px
            height: 24px
            background-color: #40A7EA
            border-radius: 5px
            background-image: url(../img/profile/back.svg) 
            background-position: center
            background-repeat: no-repeat
            background-size: auto
            cursor: pointer
            @media (max-width: 1104px)
                margin-right: auto
            &--extended 
                @media (max-width: 1104px)
                    margin-right: 16px
        &-container
            padding: 0 12px
        &-subtitle
            font-weight: 600
            margin-top: 32px
            font-size: 16px
            line-height: 1.25
        &-block
            margin-top: 9px
            display: flex
        &-key
            font-weight: 600
            font-size: 18px
            line-height: 1.25
            color: #2F9ADF
            word-break: break-all
            max-width: 90%
        &-copy 
            cursor: pointer
            margin-left: 10px
            content: ""
            width: 24px
            height: 24px
            background-image: url(../img/profile/copy.svg) 
            background-position: center
            background-repeat: no-repeat
            background-size: auto
            cursor: pointer
        &-success 
            display: none
            position: absolute
            bottom: 34px
            left: calc(50% - 117.5px)
            background: #213151
            border: 1px solid #40A7EA
            border-radius: 8px
            color: #fff
            text-align: center
            font-size: 12px
            padding: 12px 35px
            max-width: 235px
            @media (max-width: 1104px)
                position: fixed
        &-form
            & .n-profile-table__api-subtitle 
                margin-bottom: 16px
.light .n-profile .n-profile-table
    .n-profile-table__title
        color: #142546
    .n-profile-table__head
        background: #FCFEFF
        &::before, &::after
          background-color: #FCFEFF
    .n-profile-table__api
        color: #142546
    .n-profile-table__api-item
        background: #FCFEFF
    .n-profile-table__label::before
        border: 2px solid #DEEAFF
    .n-profile-table__label.check::before
        border: 2px solid #40A7EA
    .n-profile-table__api-success
        background: #FFFFFF
        border: 1px solid #40A7EA
        filter: drop-shadow(0px 15px 20px rgba(0, 0, 0, 0.16))
        color: #142546

