.n-p2p
    &-sidebar 
        width: 284px
        min-width: 284px
        margin-right: 21px
        max-height: 695px
        @media (max-width: 1104px)
            width: 100%
            min-width: 100%
            margin-bottom: 4px
            margin-right: 0
            position: relative
            z-index: 20
            border: 0
    &__nav
        color: #fff
        @media (max-width: 1104px)
            position: relative
            z-index: 2
    &__nav-button
        display: none
        & .n-p2p__link-count
            display: none
        @media (max-width: 1104px)
            display: flex
            align-items: center
            padding: 10px 16px 
            border: 0
            outline: 0
            background: 0 0
            color: #fff
            width: 100%
            text-align: left
            font-weight: 600
            font-size: 12px
            line-height: 15px
            border-radius: 10px
            border: 1px solid #89B0FD
            backdrop-filter: blur(10px)
            border-radius: 10px
            &::before
                display: block
                content: ''
                position: absolute
                width: 16px
                height: 16px
                top: 16px
                right: 20px
                background-image: url(../img/operation-history/arrow.svg)
                transition: all .3s linear
        &.active 
            border-radius: 10px 10px 0 0 
            border-bottom: 1px solid transparent
            background: rgba(33, 59, 86, 0.99)
            &::before
                transform: rotate(180deg)
            & + .n-p2p__links
                border-top: 0
                display: block
    &__links 
        list-style-type: none
        max-width: 284px
        height: 687px
        padding: 32px
        @media (max-width: 1200px)
            padding: 36px 20px
        @media (max-width: 1104px)
            height: auto
            padding: 0
            top: calc( 100% - 1px )
            left: 0
            max-width: initial
            width: 100%
            display: none
            position: absolute
            z-index: 2
            background: rgba(33, 59, 86, 0.99)
            border: 1px solid #89B0FD
            backdrop-filter: blur(10px)
            border-radius: 0 0 10px 10px
            padding-bottom: 12px
            &.active
                display: block
    &__link
        font-weight: 600
        font-size: 12px
        line-height: 1.3
        display: flex
        align-items: center
        padding: 20px 16px
        position: relative
        background: transparent
        outline: none
        border: none
        color: #fff
        width: 100%
        &:not(:first-child)
            margin-top: 8px
        &.active, &:hover
            background: rgba(64, 167, 234, 0.12)
            border-radius: 10px
            color: #40A7EA
            & .n-p2p__link-img
                background-color: #40A7EA
        @media (max-width: 1104px)
            padding: 11px 16px
            &.active
                background: 0
    &__link-img 
        width: 24px
        height: 24px
        display: inline-block
        mask-size: contain
        mask-position: center
        background-color: #fff
        margin-right: 8px
    &__link-count
        position: absolute
        font-weight: 500
        font-size: 12px
        line-height: 15px
        color: #fff
        background: #FD5454
        border-radius: 10px
        min-width: 16px
        height: 16px
        text-align: center
        right: 16px
        top: calc( 50% - 8px )
        padding: 0 3px

.light
    .n-p2p-sidebar.n-p2p-block
        border: 1px solid #89B0FD
    .n-p2p__sidebar 
        background: none
    .n-p2p__nav-button
        color: #142546
        background: rgba(237, 250, 255, 0.39)
    .n-p2p__nav-button.active
        background: rgb(237, 250, 255)
    .n-p2p__links
        @media (max-width: 1104px)
            background: rgb(237, 250, 255)
            backdrop-filter: blur(10px)
    .n-p2p__link
        color: #142546
    .n-p2p__link:hover, .n-p2p__link:active, .n-p2p__link.active
        background: rgba(64, 167, 234, 0.12)
        border-radius: 10px
        color: #40A7EA
    .n-p2p__link-img
        background-color: #142546