.n-market__statistics
	grid-column: 1/3
	grid-row: 3/5
	display: flex
	border-bottom: 4px solid #14223B 
	&-item
		flex: 0 1 50%
		padding: 12px 12px 8px 12px
		border-right: 4px solid #14223B 
		overflow: hidden
		&--buy 
			& .n-market__statistics-row
				background-image: linear-gradient(rgba(27, 208, 121, 0.23) 0, rgba(27, 208, 121, 0.23) 100%)
				background-position: left
			& .n-market__statistics-body .n-market__statistics-price
				/* color: #1BD079 */
	&-title
		font-weight: 700
		font-size: 14px
		line-height: 17px
		color: #40A7EA
	&-inner
		margin-top: 16px
		padding-bottom: 3px
		& li
			white-space: nowrap
	&-head
		display: flex
		list-style-type: none
		justify-content: space-between
		font-weight: 400
		font-size: 12px
		line-height: 15px
		padding-right: 9px
	&-body
		padding: 5px 14px 0 7px
		height: 258px
		margin-left: -7px   
	&-price
		width: 24%
	&-volume
		width: 27%
		text-align: right
	&-type
		width: 26%
		text-align: right
		padding-right: 10px
	&-total
		width: 23%
		text-align: right
		white-space: nowrap
		& span
			font-weight: 500
	&-row
		cursor: pointer
		display: flex
		list-style-type: none
		justify-content: space-between
		font-weight: 500
		font-size: 12px
		line-height: 15px
		margin-top: 4px
		margin-left: -7px
		margin-right: -10px
		padding: 2px 4px 2px 7px
		background-image: linear-gradient(rgba(253, 84, 84, 0.23) 0, rgba(253, 84, 84, 0.23) 100%)
		background-size: 75% 100%
		background-repeat: no-repeat
		background-position: right
		& .n-market__statistics-price
			/* color: #FD5454 */
		& .n-market__statistics-volume
			text-align: right
		&:hover,&:active
			background: #324873
			background-size: 100% 100%
		&.active
			position: relative
			font-weight: 600
			&::before
				position: absolute
				left: -2px
				top: calc( 50% - 4.5px )
				content: ""
				width: 8px
				height: 9px
				background: url(../img/market/triangle.svg) center no-repeat
				background-size: cover