.contest
	position: fixed
	display: block
	left: 50%
	transform: translateX(-50%) translateY(-50%)
	width: 706px
	padding: 8px 10px 27px 10px
	border-radius: 22px
	transition: opacity 1.2s ease 0s, top 1.2s ease 0s
	overflow: hidden
	z-index: 1000
	color: #FFFFFF
	background-color: #122035
	@include smalltablet2
		width: calc( 100% - 32px )
		left: 0
		transform: translateY(-50%)
		margin: 0 16px 
		padding: 10px 10px 16px
	&__inner 
		position: relative
		height: 482px
		background-image: url(../img/new-year-contest/banner.png)
		background-position: center
		background-size: cover
		width: 100%
		display: flex
		flex-direction: column
		text-align: center
		border-radius: 16px
		@include smalltablet2
			height: auto
	&__title
		max-width: 340px
		margin: 183px auto 0
		text-transform: uppercase
		font-size: 42px
		font-weight: 700 
		line-height: 1.3
		@include smalltablet2
			margin: 60px auto 0
			font-size: 32px
	&__text
		margin-top: 40px
		font-size: 16px
		line-height: 1.75
		font-weight: 600
		@include smalltablet2
			max-width: 90%
			margin: 20px auto 0
			font-size: 14px
	&__label
		margin-top: 62px
		text-align: left
		color: #7B7F8F
		margin-left: 15px
		margin-bottom: 18px
		max-width: 182px
		font-size: 10px
		line-height: 1.4
		font-weight: 600
		@include smalltablet2
			margin-top: 30px
	&__button 
		position: absolute
		content: ''
		width: 20px
		height: 20px
		background-color: transparent
		top: 10px
		right: 10px
		background-image: url(../img/new-year-contest/close.svg)
		border: 0
		outline: 0
		background-position: center
		background-size: 20px
		cursor: pointer
	&__footer 
		margin-top: 18px
		text-align: center
		@include smalltablet2
			margin-top: 16px
	&__link 
		display: inline-block
		color: #ffffff
		padding: 12px 49px
		font-size: 20px
		line-height: 1.5
		font-weight: 600
		border-radius: 10px
		background: #0F4FA1
		@include smalltablet2
			font-size: 16px
			padding: 8px 16px
