.footer
   font-family: 'Montserrat', sans-serif
   background: $dark
   padding: 64px 0 48px
   color: #ffffff
   .light &
      background: $background
   @include smalltablet
      padding: 40px 0 24px
   &-row
      display: grid
      grid-template-columns: repeat(4, 1fr)
      gap: 24px
      @include tablet
         grid-template-columns: repeat(3, 1fr)
         grid-template-rows: auto auto
      @include smalltablet
         display: flex
         flex-direction: column
         justify-content: center
         gap: 0
      &--border
         align-items: center
         margin-top: 32px
         padding: 16px 0
         border-top: 2px solid #2B3B59
         border-bottom: 2px solid #2B3B59
         grid-template-columns: 1fr auto 1fr
         .light &
            border-top: 2px solid #C8DDFF
            border-bottom: 2px solid #C8DDFF
            @include smalltablet
               border: 0
         @include tablet
            grid-template-columns: auto auto auto
            grid-template-rows: auto
         @include smalltablet
            padding: 0
            margin-top: 24px
            border: 0
            display: block
   &-title
      font-weight: 600
      font-size: 20px
      line-height: 1.4
      .light &
         color: $dark
      @include tablet
         max-width: 392px
   &-btns
      margin-top: 24px
      @include tablet
         display: flex
         margin-top: 0
   &-btn.btn
      border-radius: 28px
      text-align: center
      display: inline-block
      padding: 14px
      min-width: 190px
      font-weight: 700
      @include smalltablet
         min-width: initial
         padding: 14px 24px
      &:not(:first-of-type)
         margin-top: 8px
         @include tablet
            margin-top: 0
            margin-left: 16px
   &__col
      @include smalltablet 
         width: 100%
         display: flex
         flex-direction: column
         align-items: flex-start
         padding-right: 0
      &:first-child
         @include tablet
            grid-row: 1/2
            grid-column: 1/4
            display: flex
            align-items: center
            justify-content: space-between
         @include smalltablet
            align-items: flex-start
            gap: 16px
      &_title
         font-weight: 600
         margin-bottom: 16px
         font-size: 20px
         line-height: 1.4
         color: $blue-l
         @include smalltablet
            margin-bottom: 0
            cursor: pointer
            position: relative
            padding: 24px 99px 24px 0
            border-bottom: 2px solid #2B3B59
            .light &
               border-bottom: 2px solid #C8DDFF
            @include smalltablet
               width: 100%
               width: -moz-available
               width: -webkit-fill-available
               width: fill-available
            &::after
               content: ''
               position: absolute
               width: 24px
               height: 24px
               right: 0
               top: 26px
               background: url('../img/main/footer-chevron.svg') center no-repeat
               transform: rotate(180deg)
               transition: .2s
               color: inherit
            &.open
               padding: 24px 99px 0 0
               border-bottom: 0
               &::after
                  transform: rotate(0)
               & + ul
                  margin-top: 16px
                  width: 100%
                  display: block
                  padding-bottom: 24px
                  border-bottom: 2px solid #2B3B59
                  .light &
                     border-bottom: 2px solid #C8DDFF
            &:not(.accordion).open
               br
                  display: block
         &:not(:first-child)
            margin-top: 32px
            @include smalltablet
               margin-top: 0
      ul
         @include smalltablet
            display: none
         li
            display: block
            &:not(:first-child)
               margin-top: 16px
            a
               font-weight: 500
               line-height: 1.4px
               font-size: 14px
               color: #FFFFFF
               transition: all .1s linear
               &:hover
                  color: $grey-l
               .light &
                  color: $dark-grey
                  &:hover
                     color: $grey-l
   &-logo
      & img 
         width: 87px
         height: 16px
         @include smalltablet
            width: 148px
            height: 26px
      & .dark
         display: none
         .light &
            display: block
      .light &
         img
            display: none
         
   &-center
      display: flex
      align-items: center
      justify-content: center
      gap: 24px
      padding-left: 24px
      @include tablet
         padding-left: 0
      @include smalltablet
         margin-top: 24px
         display: block
   &-social
      display: flex
      justify-content: flex-end
      align-self: center
      @include smalltablet
         justify-content: flex-start
      &__link
         transition: all .1s linear
         display: block
         &:not(:last-child)
            margin-right: 10px
         &:hover
            opacity: .7
         & .dark
            display: none
            .light &
               display: block
         .light &
            img
               display: none
   &-docs
      display: flex
      flex-direction: column
      @include smalltablet
         margin-top: 24px
         gap: 4px
      &__link
         font-size: 10px
         line-height: 1.4
         color: #ffffff
         @include smalltablet
            font-size: 14px
         &:hover
            color: $grey-l
         .light &
            color: $dark-grey
            &:hover
               color: $grey-l
   &-copyright
      text-align: right
      padding-left: 5px
      font-weight: 500
      font-size: 10px
      line-height: 1.4
      @include smalltablet
      padding-left: 0
      text-align: left
      margin-top: 24px
      .light &
         color: $dark-grey