.n-p2p.n-updates-bg
  background: url(../img/p2p/updates-bg.png) center no-repeat
  background-size: cover
  flex: 1 0 auto
  margin-top: -79px
  padding-bottom: 0
  @media (max-width: 768px)
    background: url(../img/p2p/updates-bg-mob.png) center no-repeat
    background-size: cover
.n-p2p-updates
  color: #fff
  text-align: center
  padding-top: 279px
  @media (max-width: 768px)
    padding-top: 138px
  &__title 
    max-width: 824px
    font-weight: 700
    font-size: 64px
    line-height: 1.21
    margin: 0 auto 
    @media (max-width: 768px)
      font-size: 28px
      max-width: 319px
  &__text
    max-width: 548px
    font-size: 16px
    line-height: 1.5
    color: #FCFEFF
    margin: 25px auto 0
    @media (max-width: 768px)
      margin: 50px auto 0
      max-width: 320px
  &__links
    margin: 50px auto 0
    display: flex
    justify-content: center
    @media (max-width: 768px)
      margin: 32px auto 0
  &__link
    text-decoration: none
    font-weight: 600
    font-size: 16px
    line-height: 1.5
    color: #fff
    border: 2px solid #fff
    border-radius: 10px
    padding: 12px
    min-width: 183px
    transition: all .3s linear
    @media (max-width: 768px)
      min-width: 151px
    &:last-child
      margin-left: 20px
      @media (max-width: 768px)
        margin-left: 16px
    &:hover,&:active
      background: #fff
      color: #44AAEC
  &__footer
    background: url(../img/p2p/updates-footer.png) center no-repeat
    background-size: 100% 100%
    text-align: center
    min-height: 200px
    display: flex
    flex-direction: column
    justify-content: center
    margin-top: 200px
    @media (max-width: 768px)
      background: url(../img/p2p/updates-footer-mob.png) center no-repeat
      background-size: 100% 100%
      margin-top: 100px
      min-height: 170px
  &__progress
    margin: 0 auto 16px
    display: block
    width: 384px
    height: 20px
    @media (max-width: 768px)
      width: 320px
    @media (max-width: 360px)
      width: 280px
  &__progress-label
    font-weight: 500
    font-size: 16px
    letter-spacing: 0.11em
    text-transform: uppercase
    color: #FCFEFF