.f-profile-notifications
  position: fixed
  bottom: 40px
  right: 40px
  z-index: 1000 
  @media (max-width: 900px)
      display: none
.f-profile-notification
  z-index: 1000
  width: 300px
  min-height: 66px
  color: rgba(255, 255, 255, 0.851)
  background: linear-gradient(267.06deg, #13182E, #1F2439 100%)
  font-weight: 600
  font-size: 14px
  line-height: 15px
  border-radius: 12px
  list-style-type: none
  font-family: 'Montserrat', sans-serif
  position: relative
  padding: 14px 14px 14px 59px
  transition: all 0.4s ease-in
  & h5
    color: #00CC99
  &:not(:first-child)
    margin-top: 12px
  &::after
    content: ''
    position: absolute
    width: 4px
    height: 100%
    top: 0
    right: -4px
    background: #00CC99
    border-radius: 4px
  &:hover .f-profile-notification__icon
    background-color:#1F304B
    background-image: url('../img/profile/notification-close.svg')
    background-position: center
    background-repeat: no-repeat
    background-size: auto
  &--error
    &::after
      background: #EB5757
    & h5
      color:#EB5757
    & .f-profile-notification__icon
      background-color:#EB5757
      background-image: url('../img/profile/notification-error.svg')
  &--warning
    &::after
      background: #F2C94C
    & h5
      color:#F2C94C
    & .f-profile-notification__icon
      background-color:#F2C94C
      background-image: url('../img/profile/notification-warning.svg')
  &--info
    &::after
      background: #5458F7
    & h5
      color:#5458F7
    & .f-profile-notification__icon
      background-color:#5458F7
      background-image: url('../img/profile/notification-info.svg')
  &.hide
    transform: translateX(150%)
  &__icon 
    width: 34px
    height: 34px
    background-color:#00CC99
    background-image: url('../img/profile/notification-check.svg')
    background-position: center
    background-repeat: no-repeat
    background-size: auto
    position: absolute
    left: 14px
    top: calc( 50% - 17px )
    border-radius: 50%
    cursor: pointer
    transition: all .1s linear
  & h5
    font-size: 14px
    line-height: 17px
    padding-right: 30px
  & p
    margin-top: 0
    &:first-of-type
      margin-top: 4px
.light .f-profile-notification
  background: #ffffff
  color: #061429