.n-p2p-deal
    width: 100%
    background: rgba(33, 59, 86, 0.39)
    border: 1px solid #89B0FD
    border-radius: 20px
    color: #fff
    padding: 12px 0 16px
    min-height: 200px
    @media (max-width: 1280px)
        margin-right: 20px
        width: calc(100% - 344px)

    @media (max-width: 1104px)
        width: 100%
        border-radius: 10px
        margin-right: 0

    &-title
        text-align: center
        font-weight: 600
        font-size: 20px
        line-height: 1.25

    &-back
        position: absolute
        top: 0
        left: 24px
        content: ""
        width: 24px
        height: 24px
        background-color: #40A7EA
        -webkit-border-radius: 5px
        border-radius: 5px
        background-image: url(../img/profile/back.svg)
        background-position: center
        background-repeat: no-repeat
        background-size: auto
        cursor: pointer

    &__toggles
        margin: 16px auto 0
        display: flex
        padding: 5px
        background: #131D32
        border: 1px solid #40A7EA
        border-radius: 100px
        width: 360px

        @media (max-width: 1104px)
            margin: 12px auto 0

        @media (max-width: 380px)
            max-width: 100%

    &__toggle 
        align-items: center
        text-align: center
        width: 50%
        color: #AFB6BA
        font-size: 12px
        line-height: 15px
        font-weight: 600
        padding: 4px
        cursor: pointer

        &:hover,&:active
            color: #fff

        &.active
            color: #fff
            background: #40A7EA
            border-radius: 100px

        &:hover,&:active
            color: #fff

    &__head
        margin-top: 24px
        margin-bottom: 12px
        padding: 0 24px
        display: flex
        align-items: center
        justify-content: space-between

        @media (max-width: 900px)
            display: block
            padding: 0 20px

        &--chat
            margin-top: 0
            justify-content: center
            position: relative

    &__head-title
        font-weight: 600
        font-size: 16px
        line-height: 1.25

        @media (max-width: 900px)
            margin-bottom: 12px

    &__tabs
        display: flex
        align-items: center

        @media (max-width: 900px)
            overflow: scroll
            position: relative
            width: calc(100% + 20px)

            &::-webkit-scrollbar
                width: 0
                -ms-overflow-style: none
                overflow: -moz-scrollbars-none
                height: 0

            &::-webkit-scrollbar
                height: 0
                width: 0
                -ms-overflow-style: none
                overflow: -moz-scrollbars-none

    &__tab
        font-weight: 700
        font-size: 14px
        line-height: 1.2
        color: #93afc0
        border: 0
        outline: 0
        background: none
        white-space: nowrap

        &.active
            color: #40A7EA

        &:not(:first-child)
            margin-left: 24px

    &__info 
        background: #273755
        border-radius: 10px
        display: flex
        padding: 16px 42px
        margin: 0 6px

        @media (max-width: 768px)
            display: block
            padding: 16px 20px

    &__main 
        background: #273755
        border-radius: 10px
        padding: 14px
        margin: 6px 6px 12px

    &__main-title
        font-weight: 500
        font-size: 10px
        line-height: 12px
        text-transform: uppercase
        color: #61759D

        &.red
            color: #FD5454

    &__inner 
        display: flex
        margin-top: 14px
        row-gap: 20px

        @media (max-width: 900px)
            display: block

    &__column
        @media (max-width: 1280px)
            width: calc(100% - 385px)

        @media (max-width: 900px)
            width: 100%

    &__conditions
        box-sizing: border-box
        background: #2A3B5C
        border: 2px solid #5578BC
        border-radius: 10px
        padding: 12px 16px
        font-size: 12px
        max-width: 100%
        width: 467px
        height: 199px
        overflow: hidden
        @media (max-width: 1280px)
            width: 100%
            margin-right: 20px

        & span
            display: block
            font-weight: 600
            font-size: 14px
            margin-bottom: 8px

        & textarea
            background: none
            border: 0
            font-size: 12px
            color: #ffffff
            width: 100%
            height: calc( 100% - 24px)
            resize: none
            padding-right: 5px
            margin-right: 5px

            &:focus-visible
                outline: none
                border: none

            &::-webkit-scrollbar 
                width: 4px
                height: 4px

            &::-webkit-scrollbar-button:start:decrement,
            &::-webkit-scrollbar-button:end:increment  
                height: 4px
                background-color: transparent

            &::-webkit-scrollbar-track-piece  
                background-color: transparent
                -webkit-border-radius: 16px

            &::-webkit-scrollbar-thumb:vertical 
                height: 4px
                background-color: #3570a3
                border: 0 solid #eee

    &__seller
        margin-top: 12px
        padding: 12px 33px 12px 36px 
        background: #2A3B5C
        border: 2px solid #5578BC
        border-radius: 10px
        color: #ffffff
        font-weight: 600
        font-size: 14px
        line-height: 17px
        text-transform: uppercase

        & span
            text-transform: none
            color: rgba(255, 255, 255, 0.5)

    &__controls
        display: flex
        margin-top: 14px

    &__control
        display: flex
        align-items: center
        justify-content: center
        width: 159px
        border-radius: 7px
        background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
        -webkit-background-clip: text
        -webkit-text-fill-color: transparent
        background-clip: text
        text-fill-color: transparent
        border: 2px solid #5578BC
        padding: 8px
        text-align: center
        font-size: 14px
        font-weight: 600
        cursor: pointer
        transition: all .3s linear

        @media (max-width: 500px)
            width: calc(50% - 4px)

        &:first-child
            margin-right: 8px

        &:hover
            background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
            color: #fff
            background-clip: initial
            text-fill-color: initial
            -webkit-background-clip: initial
            -webkit-text-fill-color: initial
            border: 2px solid #44AAEC

    &__info-text
        margin-top: 8px
        font-weight: 400
        font-size: 12px
        line-height: 1.5
        color: #AFB6BA

    &__info-item
        width: 33.333%
        text-align: center
        font-size: 14px

        & span
            font-weight: 600

        @media (max-width: 768px)
            width: 100%
            text-align: left
            display: flex

            & span
                margin-left: auto

        &:first-child
            text-align: left

        &:last-child
            text-align: right

            @media (max-width: 768px)
                 text-align: left

    &__chat
        width: 365px
        border-radius: 10px
        border: 2px solid #5578BC
        overflow: hidden

        @media (min-width: 901px)
            margin-left: 20px

        @media (max-width: 900px)
            width: 100%
            margin-top: 20px
            margin-left: 0

        &.load 
            position: relative
            pointer-events: none

            &::before
                content: ''
                position: absolute
                top: 0
                left: 0
                width: 100%
                height: 100%
                background: rgba(20, 34, 59, 0.91)
                z-index: 2

            &::after
                content: ''
                position: absolute
                top: 0
                left: 0
                width: 100%
                height: 100%
                background-image: url(../img/operation-history/dark/load-dark.svg)
                background-position: center
                background-repeat: no-repeat
                animation: load 4s linear infinite
                z-index: 3

    &__chat-head
        padding: 13px
        background: rgba(169, 200, 255, 0.22)
        font-weight: 600
        font-size: 16px
        line-height: 20px

        & span
            color: #97BFEB

    &__chat-body
        background: #2A3B5C
        padding: 12px 10px
        position: relative
        height: 406px
        overflow: scroll

        &::-webkit-scrollbar
            width: 0
            -ms-overflow-style: none
            overflow: -moz-scrollbars-none
            height: 0

        &::-webkit-scrollbar
            height: 0
            width: 0
            -ms-overflow-style: none
            overflow: -moz-scrollbars-none

        & .simplebar-track.simplebar-vertical
            width: 8px

        & .simplebar-scrollbar:before 
            background-color: #40A7EA

        & .default
            left: 50%
            transform: translateX(-50%)
            position: absolute
            display: flex
            flex-direction: column
            justify-content: center
            height: 100%
            width: 295px
            font-weight: 400
            font-size: 12px
            line-height: 1.5
            text-align: center
            color: #AFB6BA

            @media screen and (max-width: 360px)
                width: 250px

    &__chat-footer
        filter: drop-shadow(0px -4px 6px rgba(112, 118, 129, 0.11))
        display: flex
        align-items: center
        padding: 12px

    &__chat-input 
        font-weight: 600
        width: 261px
        border: 2px solid #5578BC
        border-radius: 10px
        padding: 12px 16px
        color: #fff
        font-size: 12px
        line-height: 1.5
        background: transparent

        @media screen and (max-width: 900px)
            width: calc(100% - 80px)

        @media screen and (max-width: 480px)
            margin-right: auto

        @media screen and (max-width: 360px)
            padding: 12px 6px

        &::placeholder
            color: #AFB6BA

        &:focus-visible
            outline: none

    &__chat-file
        opacity: 0
        z-index: -1
        position: absolute

        &.load + label
            background-image: url(../img/p2p/load.svg)
            animation: move .75s infinite linear

    &__chat-file-label
        flex: 0 0 30px
        width: 30px
        height: 30px
        font-size: 0
        line-height: 0
        border: none
        outline: none
        background-color: transparent
        background-image: url(../img/p2p/file-add.svg)
        background-repeat: no-repeat
        background-size: contain
        margin-left: 8px
        display: block

    &__chat-btn
        width: 32px
        height: 32px
        background-color: #2b5681
        opacity: 0.7
        border-radius: 7px
        font-size: 0
        line-height: 0
        border: none
        outline: none
        background-image: url(../img/p2p/sent.svg)
        background-repeat: no-repeat
        background-position: center
        margin-left: 8px
        display: block

    &__chat-block 
        margin-top: 16px
        scroll-margin: 12px
        &:first-child
            margin-top: 0
        &.right .n-p2p-deal__chat-message
            margin-left: auto

            border-radius: 16px 16px 0px 16px
        &.right .n-p2p-deal__chat-time
            text-align: right

        &.right .n-p2p-deal__chat-name
            text-align: right

        &.right .n-p2p-deal__chat-message.paycard
            border-radius: 7px
            &.last 
                border-radius: 7px 7px 16px 16px

        &.img 
            & img
                cursor: zoom-in

            .n-p2p-deal__chat-message
                background: none
                border-radius: 10px
                overflow: hidden
                max-height: 215px
                padding: 0

                & img
                    height: 100%
                    width: 100%
                    object-fit: contain

        &.paycard
            margin-top: 2px

    &__chat-message 
        margin-top: 4px
        background: #9EA5E6
        border-radius: 16px 16px 16px 0px
        padding: 2px 12px 8px
        color: #fff
        font-weight: 600
        font-size: 12px
        max-width: 218px
        line-height: 1.5

        &.buyer
            background: #36486b

        &.seller
            background: #1e2c47

        &.admin
            background: #FFF5A3!important
            color: #142546

        &.paycard
            font-weight: 600
            font-size: 14px
            line-height: 1.5
            padding: 8px
            text-align: center
            border-radius: 7px
            position: relative
            cursor: pointer
            color: #9CD8FF
            &.last 
                border-radius: 7px 7px 16px 16px
            &::after
               content: ""
               position: absolute
               right: 4px
               top: 4px
               width: 11px
               height: 11px
               mask-image: url(../img/p2p/copy.svg)
               mask-position: center
               mask-repeat: no-repeat
               mask-size: contain
               background-color: #ffffff

            &.copied::after
               mask-image: url(../img/p2p/check.svg)
               mask-position: center
               mask-repeat: no-repeat
               mask-size: contain
               background-color: #38FFA0

    &__chat-text
        word-wrap: break-word

    &__chat-name
        font-weight: 600
        font-size: 10px
        line-height: 1.5
        color: #7D98C6

    &__chat-time 
        font-size: 12px
        line-height: 1.5
        color: #AFB6BA

    & .n-p2p-table__block
        background: #2A3B5C
        border: 0
        border-top: 1px solid rgba(137, 176, 253, 0.34)

        &:first-child
            border-top: 0

        &:hover 
            background: #324A78

    & .n-p2p-table__row
        padding: 6.5px 16px
        height: 77px

        @media (max-width: 900px)
            padding: 0 16px
            height: auto

    & .n-p2p-table__row--head
        height: auto

    & .n-p2p-table__column
        &:nth-child(1)
            width: 16%

        &:nth-child(2)
            width: 15%

        &:nth-child(3)
            width: 22%

        &:nth-child(4)
            width: 21%

        &:nth-child(5)
            text-align: left
            width: 132px

        @media (max-width: 900px)
            &:first-child
                padding-top: 14px

            &:last-child
                padding-bottom: 14px

    & .n-p2p-table__row--head
        padding: 16px 22px

        & .n-p2p-table__column:nth-child(5)
            text-align: center

    & .n-p2p-table__column-percent
        font-size: 14px

    & .n-p2p-table__column-status
        display: flex
        align-items: center
        padding-right: 36px
        justify-content: flex-end

        &.timer
            padding-right: 32px

            @media (max-width: 900px)
                padding-right: 0
                align-items: center

.body-wrapper.light .n-p2p-deal
    background: rgba(237, 250, 255, 0.39)
    border: 1px solid #89B0FD
    color: #142546

    .n-p2p-table__hint
        background: #fff
        border: 1px solid #89B0FD
        color: #142546

    .n-p2p-table__block
        background: #EEFAFF

    .n-p2p-table__row--head
        background: #E6F1FF
        box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05)

    .n-p2p-deal__toggles
        background: #EEFAFF
        border: 1px solid #40A7EA

    .n-p2p-table__column::after
        background: #CCDEFE

    .n-p2p-deal__info
        background: #EFF6FF

    .n-p2p-deal__main
        background: #EFF6FF

    .n-p2p-deal__control
        border: 2px solid #44AAEC

    .n-p2p-deal__conditions
        background: #F5F9FF
        border: 2px solid #DEEAFF

    .n-p2p-deal__chat.load::before
        background-color: rgba(212, 237, 250, 0.91)

    .n-p2p-deal__chat.load::after
        background-image: url(../img/operation-history/loader.svg)

    .n-p2p-deal__chat
        border: 2px solid #DEEAFF

    .n-p2p-deal__chat-body
        background: #F5F9FF

    .n-p2p-deal__chat-input
        color: #142546

    .n-p2p-deal__chat-message
        background: #e5e7fe
        color: #142546

        .admin
            background: #FFF5A3

    .n-p2p-deal__chat-block.img .n-p2p-deal__chat-message
        background: none

    .n-p2p-deal__chat-message.buyer
        background: #e4eeff

    .n-p2p-deal__toggle:hover, .n-p2p-deal__toggle:active
        color: #93afc0

    .n-p2p-deal__conditions textarea
        color: #142546

    .n-p2p-deal__seller
        background: #F5F9FF
        border: 2px solid #DEEAFF
        color: #142546
		  
        & span 
            color: rgba(20, 37, 70, 0.5)

    .n-p2p-deal__chat-message.paycard::after
        background-color: #142546
    .n-p2p-deal__chat-message.paycard.copied::after
        background-color: #142546