//p2p-arbiter appeal
.n-p2p-arbiter.n-p2p-arbiter-appeal
  .n-wallet-header__links
    border: 1px solid #40A7EA
    .n-wallet-header__toggle.active
      background: #40A7EA
  padding: 0
  .n-p2p-table__row--head
    padding: 15px 14px
  .n-p2p-add__header
    padding: 12px 24px
  .n-p2p-add-tab-content
    padding-top: 24px
    h3
      font-weight: 600
      font-size: 16px
      line-height: 20px
      color: #142546
      padding: 0 0 12px 24px
    .n-p2p-table__row .n-p2p-table__column .n-p2p-ad-list__table-title::after
      display: none
    .n-p2p-table__row .n-p2p-table__column:nth-child(5) .n-p2p-ad-list__table-title
      justify-content: flex-end
      display: flex
      align-items: center
      overflow-x: clip
      overflow-y: visible
      width: 100%
      &::after
        display: block
    .n-p2p-table__column
      &:nth-child(1)
          width: 8.81%
          text-align: left
      &:nth-child(2)
          width: 21.34%
          text-align: center
      &:nth-child(3)
          width: 21.34%
          text-align: center
      &:nth-child(4)
          width: 12.76%
          text-align: center
      &:nth-child(5)
          width: 28.13%
          text-align: right
      .n-p2p-ad-list__table-title
        font-weight: 400
        font-size: 14px
        line-height: 15px
        text-align: center
        color: #142546
      .n-p2p-table__column-extra
        font-size: 12px
        span
          font-weight: 400
          line-height: 15px
          text-align: right
          color: #142546
    .n-p2p-ad-list__table-title
        overflow: hidden
        white-space: nowrap
        text-overflow: ellipsis
        .n-p2p-table__column-status.timer
          margin: -1px 16px 0 6px
          padding: 0
          @media screen and (max-width: 900px)
            margin: -2px 24px 0 6px
          & > div
            margin: 0
            padding: 0
            max-width: 12px
        &::after
          position: absolute
          content: " "
          width: 16px
          height: 16px
          background: url(../img/p2p/arrow-next.svg) center no-repeat
          right: 5px
          top: -webkit-calc( 50% - 8px)
          top: calc( 50% - 8px)
          @media screen and (max-width: 900px)
            top: auto
            right: 0
        .n-p2p-table__hint
          max-width: 121px
          left: -127px
          top: calc( 50% - 26.5px)
          white-space: break-spaces
    .n-p2p-table__block.ad:before
      content: none
  

//p2p-arbiter panel
.n-p2p-arbiter
  .n-p2p-deal__main
    background: #EFF6FF
  .n-p2p-deal__inner
    margin-top: 0
  .n-p2p-deal__chat
    margin-top: 26px
  .n-p2p-deal__control
    width: 100%
    max-width: 151px
    min-height: 33px
    padding: 4px 2px
    &:not(:last-child)
      margin-right: 8px
  .n-p2p-deal__column1
    display: flex
    justify-content: space-between
    flex-direction: column
  &-panel__deals
    &-item
      max-width: 467px
      background: #F5F9FF
      border: 1px solid #DEEAFF
      border-radius: 10px
      padding: 6px 5px 12px 22px
      position: relative
      overflow: hidden
      @media screen and (max-width: 1280px)
        max-width: 100%
      &:before
        content: ''
        position: absolute
        left: -2px
        top: 0
        width: 10px
        height: 101%
        background: #DEEAFF
      &.active:before
        background: #9EA5E6
      &:not(last-child)
        margin-bottom: 8px
      &-header
        display: flex
        justify-content: space-between
        .n-p2p-arbiter-panel__deals-item-who
          > p
            font-weight: 600
            font-size: 12px
            line-height: 15px
            color: #142546
            padding-top: 6px
        .n-p2p-arbiter-panel__deals-item-who2
          p
            padding: 4px 7px
            max-width: 74px
            height: 20px
            background: #EDF2FA
            border-radius: 100px
            font-weight: 500
            font-size: 10px
            line-height: 12px
            color: #5F6571
            &:not(:last-child)
              margin-bottom: 7px
      .n-p2p-arbiter-panel__user 
        display: flex
        align-items: center
        padding-top: 4px
        &-img
          width: 33px
          height: 33px
          text-align: center
          text-transform: uppercase
          background: #182C52
          font-weight: 600
          font-size: 12px
          line-height: 15px
          margin-right: 8px
          -webkit-border-radius: 50%
          border-radius: 50%
          padding: 9px 2px
          color: #fff
          overflow: hidden
          &.verified
            background: linear-gradient(135deg, #FFAE49 0%, #FF74DB 100%)
        &-name
          display: flex
          align-items: center
          max-width: 160px
          @media screen and (max-width: 1280px)
            overflow: hidden
            white-space: nowrap
            text-overflow: ellipsis
          @media screen and (max-width: 900px)
            max-width: initial
            overflow: visible
          a
            font-weight: 500
            font-size: 14px
            line-height: 17px
            color: #142546 !important
            margin-left: 4px
            display: block
            cursor: pointer
            @media screen and (max-width: 1280px)
              overflow: hidden
              white-space: nowrap
              text-overflow: ellipsis
            @media screen and (max-width: 900px)
              max-width: initial
              overflow: visible
      .n-p2p-arbiter-panel__deals-item-footer
        display: flex
        justify-content: space-between
        padding-top: 12px
        p
          font-weight: 400
          font-size: 10px
          line-height: 150%
          color: #142546
        h3
          font-weight: 600
          font-size: 14px
          line-height: 17px
          color: #142546

.n-p2p-arbiter-panel 
  .n-p2p-deal__main-title
    margin-bottom: 14px
  .n-p2p-deal__chat
    margin-top: 0

//p2p-arbiter close-deal

.n-p2p-arbiter
  .n-p2p-deal__column1
    display: flex
    justify-content: space-between
    flex-direction: column
    width: 100%
    max-width: calc(100% - 385px)
  .n-p2p-deal__completed-deal
    h4
      font-weight: 500
      font-size: 10px
      line-height: 12px
      text-transform: uppercase
      padding-bottom: 14px
      color: #32405A
    .n-p2p-deal__completed-deal-content
      background: #F5F9FF
      border: 2px solid #DEEAFF
      border-radius: 10px
      height: 296px
      &-text
        display: flex
        flex-direction: column
        justify-content: center
        height: 296px
        > p
          font-weight: 400
          font-size: 12px
          line-height: 15px
          color: #142546
          padding-top: 7px
          text-align: center
      img
        padding-bottom: 16px
        margin: 0 auto
        display: flex
      .n-p2p-deal__completed-deal-text
        display: flex
        justify-content: center
        p
          font-weight: 600
          font-size: 14px
          line-height: 17px
          color: #142546
        span
          font-weight: 600
          font-size: 14px
          line-height: 17px
          color: #97BFEB
        
  .n-p2p-deal__review
    padding-top: 32px
    h4
      font-weight: 600
      font-size: 14px
      line-height: 17px
      color: #142546
    &-form
      margin-top: 8px
      border: 1px solid #A9C8FF
      border-radius: 10px
      padding: 8px
    form
      margin-top: 8px
      border: 1px solid #A9C8FF
      border-radius: 10px
      padding: 8px
    &-head
      display: flex
      justify-content: space-between
      padding-bottom: 8px
      button
        border: 2px solid #6FA2FC
        border-radius: 7px
        max-width: 130px
        height: 33px
        width: 100%
        background: transparent
        font-weight: 600
        font-size: 14px
        line-height: 17px
        display: flex
        align-items: center
        justify-content: center
        color: #6FA2FC
        transition: all .3s ease
      .n-p2p-deal__review-likes
        button
          transition: none
          &:first-child
            background-image: url('../img/p2p/p2p-arbiter/like.svg')
            background-repeat: no-repeat
            background-size: 12px
            background-position: center
          &:last-child
            background-image: url('../img/p2p/p2p-arbiter/dis_like.svg')
            background-repeat: no-repeat
            background-size: 12px 
            background-position: center 7px
          &:first-child.active
            border: 1px solid #1AC774
            background-image: url('../img/p2p/p2p-arbiter/like-active.svg')
          &:last-child.active
            border: 1px solid #F02D60
            background-image: url('../img/p2p/p2p-arbiter/dis_like-active.png')
    &-content
      textarea
        border: 1px solid #A9C8FF
        border-radius: 10px
        font-weight: 400
        font-size: 10px
        line-height: 150%
        padding: 12px 12px 8px
        color: #142546
        width: 100%
        resize: none
        height: 108px
        outline: none
        background: transparent
        :placeholder
          font-weight: 400
          font-size: 10px
          line-height: 150%
          color: #142546
          opacity: .5
    &-likes
      display: flex
      button
        border: 1px solid #A9C8FF
        width: 25px
        height: 25px
        overflow: hidden
        border-radius: 50%
        background: transparent
        display: flex
        align-items: center
        img
          max-width: 12.3px
          max-height: 11.58px
      button:first-child
        margin-right: 8px


//black theme

body.dark
  .n-p2p-arbiter
    .n-p2p-deal__main
      background: #273755
    .n-p2p-deal__completed-deal-content
      background: #2A3B5C !important
      border: 2px solid #5578BC !important
    .n-p2p-deal__completed-deal, .n-p2p-deal__review
      h4, p, textarea, h3
        color: #fff !important
    .n-p2p-table__column 
      span
        color: #fff !important
      .n-p2p-ad-list__table-title
        color: #fff !important
    &-panel__deals-item
      background: #2A3B5C
      h3, h4, p, span, a
        color: #fff !important
      .n-p2p-arbiter-panel__deals-item-who2
        p
          color: #5F6571 !important
  .n-p2p-arbiter.n-p2p-arbiter-appeal
    .n-p2p-add-tab-content
      h3
        color: #fff !important


.n-p2p-arbiter
  @media screen and (max-width: 1280px)
    .n-p2p-deal__column
      width: 100% !important
  @media screen and (max-width: 890px)
    & .n-p2p-deal__column1
      max-width: 100%
    .n-p2p-deal__column, .n-p2p-arbiter-panel__deals
      margin-bottom: 30px
  @media screen and (max-width: 780px)
    .n-p2p-deal__column1
      max-width: 100% !important
  @media screen and (max-width: 520px)
    .n-p2p-arbiter-panel__deals-item 
      .n-p2p-arbiter-panel__user-name a
        font-size: 10px
  @media screen and (max-width: 450px)
    .n-p2p-deal__column1
      max-width: 100% !important
  @media screen and (max-width: 420px)
    .n-p2p-deal__completed-deal-text p, .n-p2p-deal__completed-deal-text span
      font-size: 10px !important
    .n-p2p-deal__completed-deal-content-text > p
      font-size: 9px !important
    .n-p2p-arbiter-panel__deals-item 
      .n-p2p-arbiter-panel__deals-item-who2 p
        padding: 2px 5px
        height: auto
    .n-p2p-arbiter-panel__deals-item 
      .n-p2p-arbiter-panel__user
        display: grid
        grid-template-columns: 32px
        grid-row-gap: 5px


body.dark
  .n-popup--arbiter-cancel, .n-popup--arbiter-close, .n-popup--arbiter-change
    .n-popup__content
      background: rgba(33, 59, 86, 0.39)
      h3, h5, p
        color: #fff !important
      input
        color: #FFFFFF !important
        background: #2A3B5C !important
        border: 2px solid #5578BC !important

//popup

.n-popup--arbiter-cancel, .n-popup--arbiter-close, .n-popup--arbiter-change
  .n-popup__inner
    .n-popup__content
      background: #FFFFFF
      padding: 32px 36px
      .n-popup__p2p-head
        border: none
        .n-popup__p2p-text
          margin: 0
          .n-popup__p2p-title
            font-weight: 700
            font-size: 30px
            line-height: 37px
            color: #142546
            text-align: center
            max-width: 515px
            margin: 0 auto
          .n-popup__p2p-subtitle
            font-weight: 400
            font-size: 12px
            line-height: 140%
            text-align: center
            color: #142546
            margin: 12px auto 0
            max-width: 340px
      .n-popup__p2p-form
        padding-top: 12px
        & > *
          max-width: 384px
          margin: 0 auto
          h3
            font-weight: 600
            font-size: 16px
            line-height: 20px
            color: #142546
            text-align: left
            padding-bottom: 4px
            &:not(:first-of-type)
              padding-top: 28px
          input
            border: 2px solid #DEEAFF
            border-radius: 10px
            font-weight: 400
            font-size: 16px
            line-height: 150%
            color: #142546
            height: 48px
            max-width: 384px
            width: 100%
            outline: none
            padding: 12px 16px
          span
            display: none
            font-weight: 400
            padding-top: 4px
            font-size: 12px
            line-height: 150%
            color: #FD5454
            text-align: left
          .n-popup__p2p-input
            padding-top: 0
          .n-popup__p2p-input.error
            position: relative
            span
              position: absolute
              bottom: -20px
              display: block
            input
              border: 2px solid #FD5454 !important
              color: #FD5454 !important
          .n-popup__controls
            margin-top: 32px
            .n-popup__control
              max-width: 188px !important
              width: 100%
              @media screen and (max-width: 768px)
                max-width: 100%!important
.light
  .n-p2p-arbiter-panel__user-img
    background: #89B0FD
    .verified
      background: linear-gradient(135deg, #FFAE49 0%, #FF74DB 100%)
.n-popup--arbiter-cancel, .n-popup--arbiter-close, .n-popup--arbiter-change
  @media screen and (max-width: 375px)
    &
      padding: 0 8px !important
    .n-popup__content
      padding: 12px 20px !important
    h5
      font-size: 25px !important
    .n-popup__controls
      margin-top: 12px
  @media screen and (max-width: 768px)
    .n-popup__controls
      .n-popup__control
        max-width: 100% !important
.n-popup--arbiter-error, .n-popup--arbiter-ok
  .n-popup__p2p-head
    border: none