.operation-popups
  position: fixed
  bottom: 40px
  right: 40px
  z-index: 1000 
  @media (max-width: 900px)
      display: none
.operation-popup
  z-index: 1000
  width: 283px
  color: #fff
  list-style-type: none
  font-family: 'Montserrat', sans-serif
  &:not(:first-child)
    margin-top: 17px
  &__head 
    background: rgba(31, 100, 146, 0.6)
    backdrop-filter: blur(6px)
    border-radius: 10px 10px 0 0
    display: flex
    align-items: center
    padding: 12px 12px 14px 20px
    justify-content: space-between
    & p 
      font-weight: 600
      font-size: 14px
      line-height: 17px
  &__close 
    border: none
    outline: none
    background-color: transparent
    background-image: url(../img/market/close_blue.svg)
    background-size: cover
    width: 16px
    height: 16px
    cursor: pointer
  &__content 
    background: linear-gradient(90deg, #175D8A 0%, #213151 100%)
    border-radius: 0 0 10px 10px
    padding: 8px 16px 12px 12px
    & p 
      font-size: 12px
      line-height: 18px
      font-weight: 300
      & b 
        font-weight: 400
      & strong
        font-weight: 500
    & time
      display: block
      width: 100%
      text-align: right
      margin-top: 8px
      font-size: 10px
      line-height: 15px
      color: #8d94a1
  &__line
    position: relative
    display: block
    margin-top: 8px
    background: #456689
    border-radius: 100px
    width: 100%
    height: 3px
    z-index: 1
  &__timer
    z-index: 2
    position: absolute
    width: 100%
    height: 100%
    top: 0
    left: 0
    border-radius: 100px
    background: #40a7ea
    &.empty
      width: 0%!important