.n-wallet .n-toolbar
    color: #ffffff
    min-height: 66px
    @media (max-width: 1104px)
        min-height: initial
        flex-direction: column-reverse
        flex-wrap: wrap
        padding: 19px 20px 12px
    & .n-filter__btn 
        width: 142px
        height: 34px
        @media (max-width: 1104px)
            width: 33px
            height: 33px
        &::before
            content: url(../img/wallet/sort.svg)
            top: 5px
            @media (max-width: 1104px)
                left: 5px
    &__controls
        display: flex
        align-items: center
    &__balance
        margin-left: auto
    &__label
        position: relative
        font-size: 12px
        line-height: 1.5
        padding: 0 0 0 26px
        cursor: pointer
        font-weight: 500
        &::before
            position: absolute
            content: ""
            left: 0
            top: 0
            width: 18px
            height: 18px
            border: 2px solid #8D94A1
            border-radius: 3px
            transition: border-color 0.3s linear
    &__label.check
        &::before
            border: 2px solid #40A7EA
            background-image: url(../img/profile/check-blue.svg) 
            background-repeat: no-repeat
            background-position: center
    &__favorite
        cursor: pointer
        margin-left: 28px
        @media (max-width: 1104px)
            margin-left: 16px
    &__favorite-label
        position: relative
        content: ""
        left: 0
        top: 0
        width: 20px
        height: 20px
        background-image: url(../img/wallet/favorite-small.svg) 
        background-repeat: no-repeat
        background-size: contain
        transition: border-color 0.3s linear
    &__favorite-label.check
        background-image: url(../img/wallet/favorite-check.svg) 
    &__list
        cursor: pointer
        margin-left: 12px
        position: relative
        content: ""
        left: 0
        top: 0
        width: 32px
        height: 32px
        background-image: url(../img/wallet/list.svg) 
        background-repeat: no-repeat
        background-size: contain
        transition: border-color 0.3s linear
        @media (max-width: 1104px)
            display: none
        &.active
            background-image: url(../img/wallet/list-active.svg) 
    &__cards
        cursor: pointer
        margin-left: 12px
        position: relative
        content: ""
        left: 0
        top: 0
        width: 32px
        height: 32px
        background-image: url(../img/wallet/cards.svg) 
        background-repeat: no-repeat
        background-size: contain
        transition: border-color 0.3s linear
        @media (max-width: 1104px)
            display: none
        &.active
            background-image: url(../img/wallet/cards-check.svg) 
.light .n-profile .n-toolbar
    color: #142546



        
