.header
  padding: 18px 0 17px
  min-height: 72px
  position: relative
  border-bottom: 1px solid #374662
  transition: background-color 0.3s ease-in-out
  &--user 
    padding: 15px 0 14px
  &.header--mini
    border-bottom: 0
  .light & 
    border-bottom: 1px solid #C8DDFF
  &.burger-open
    background-color: $dark
    .light & 
      background-color: $background
  div.container
    padding: 0 24px
    max-width: 100%
    display: flex
    justify-content: space-between
    align-items: center
    overflow: visible
    @include mobile
      padding: 0 16px
  &__logo
    display: block
    position: relative
    z-index: 2
    cursor: pointer
    width: 87px
    height: 16px
    mask-image: url(../img/logo.svg)
    mask-position: center
    mask-repeat: no-repeat
    mask-size: contain
    background-color: $white
    margin-right: auto
    .light &
      background-color: $dark
    .header--mini &
      @include tablet
        background-color: $dark
  .burger
    &-body
      display: flex
      align-items: center
      @include smalldesktop
        flex-direction: column
        position: fixed
        z-index: 10000
        top: 72px
        right: 0
        background: $dark
        padding: 24px
        width: 253px
        height: auto
        max-width: 100%
        box-sizing: border-box
        transform: translate(125%, 0)
        transition: .3s ease-out
        .light & 
          background-color: $background
        &.open
          transform: translate(0)
          &::before
            position: absolute
            width: 100%
            height: 16px
            content: ''
            background: linear-gradient(180deg, rgba(0, 0, 0, 0.05) 0%, rgba(0, 0, 0, 0.00) 100%)
            top: 0
            left: 0
      @include mobile
        min-width: 100vw
        height: calc( 100vh - 72px )
        height: calc( 100dvh - 72px )
        overflow: auto
        padding: 24px 0
      nav
        margin: auto 0
        @include smalldesktop
          margin: 0
          width: 100%
        @include mobile
          padding-top: 24px
          order: 2
      ul
        display: flex
        margin-right: 32px
        @include smalldesktop
          flex-direction: column
          margin-right: 0
          width: 100%
        @include mobile
          align-items: center
        li
          display: block
          margin-right: 20px
          @include smalldesktop
            margin-right: 0
            &:not(:first-child)
              margin-top: 16px
          &:last-child
            margin-right: 0
    &__footer
      display: none
      font-size: 12px
      font-weight: 500
      line-height: 1.4
      color: $white
      opacity: .5
      margin-top: 60px
      padding-top: 18px
      @include mobile
        text-align: center
      .light &
        color: $dark
      @include smalldesktop
        display: block
      @include mobile
        order: 3
        margin-top: auto
    &__policy
      font-size: 12px
      font-weight: 500
      line-height: 1.4
      color: $white
      margin-bottom: 4px
      .light &
        color: $dark
  &__btns
    display: flex
    @include mobile
      width: 100%
      justify-content: center
      padding-bottom: 24px
      border-bottom: 1px solid #374662
      .light & 
        border-bottom: 1px solid #C8DDFF
    &--tablet
      display: none
      @include smalldesktop
        display: flex
      @include mobile
        display: none
    .burger-body & 
      @include smalldesktop
        display: none
      @include mobile
        display: flex
  &__reg
    margin-left: 8px
  &__theme-wrapper
    margin-left: 16px
    width: 24px
    height: 36px
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center
  &__theme-toggler
    cursor: pointer
    background: $white
    width: 24px
    height: 24px
    border: 0
    mask-image: url(../img/sun.svg)
    mask-position: center
    mask-repeat: no-repeat
    mask-size: cover
    @include hover
      background-color: $blue-l
    .light &
      mask-image: url(../img/moon.svg)
      background-color: $dark
      @include hover
        background-color: $blue-l
  .burger-btn
    display: none
    cursor: pointer
    mask-image: url('../img/main/burger.svg')
    mask-size: 24px
    mask-position: center
    mask-repeat: no-repeat
    background-color: $white
    width: 32px
    height: 32px
    margin-left: 8px
    .light & 
      background-color: $dark
    &.open 
      mask-image: url('../img/main/burger-close.svg')
    @include smalldesktop
      display: block

  .user 
    display: flex
    align-items: center
    @include smalldesktop
      display: none
    @include mobile
      display: flex   
      width: 100%
      justify-content: center
      padding-bottom: 24px
      border-bottom: 1px solid #374662
      .light & 
        border-bottom: 1px solid #C8DDFF
    &.verif
      & .user__verif
        display: none
      & .user__info
        border: 1px solid $blue-l
    &--tablet
      display: none
      @include smalldesktop
        display: flex
      @include mobile
        display: none
    &__info 
      border-radius: 20px
      border: 1px solid $red
      display: flex
      align-items: center
      padding: 4px 10px 4px 4px
    &__img 
      width: 32px
      height: 32px
      background-color: $blue-l
      border-radius: 50%
      display: flex
      flex-direction: column
      justify-content: center
      align-items: center
      overflow: hidden
      & img
        max-height: 100%
        max-width: 100%
        object-fit: contain
    &__mail 
      display: block
      font-weight: 600
      font-size: 14px
      line-height: 1.4
      color: $white
      margin-left: 8px
      .light & 
        color: $dark
      @include hover
        color: $blue-l
        text-decoration: underline

    &__logout
      mask-image: url(../img/exit.svg)
      mask-position: center
      mask-repeat: no-repeat
      mask-size: contain
      background-color: $blue-l
      width: 20px
      height: 20px
      display: block
      margin-left: 8px
      @include hover
        opacity: .5
      .light & 
        background-color: $dark
    &__verif
        position: relative
        &:hover 
          .user__verif-dropdown 
            display: block
          .user__verif-btn 
            opacity: .5
    &__verif-btn
        width: 24px
        height: 24px
        margin-right: 8px
        cursor: pointer
        transition: all .1s ease-in-out
        &.active
          opacity: .5
    &__verif-dropdown
        position: absolute
        left: -128px
        top: calc( 100% + 14px )
        width: 280px
        border-radius: 8px
        border: 1px solid #4799CF
        background: $dark
        padding: 16px
        color: $grey-l
        display: none
        z-index: 2
        filter: drop-shadow(0px 4px 8px rgba(0, 0, 0, 0.06)) drop-shadow(0px 0px 4px rgba(0, 0, 0, 0.04))
        .light & 
          background-color: $background
        @include mobile
            width: 312px
            left: -16px
            top: calc( 100% + 23px )
            padding: 10px 21px
        &::before 
            position: absolute
            content: ''
            width: 12px
            height: 6px
            top: -6px
            left: calc( 50% - 6px )
            background-image: url(../img/beak.svg)
            background-position: center
            background-repeat: no-repeat
            background-size: cover
        &::after
            content: ''
            position: absolute
            height: 16px
            top: -16px
            width: 100%
            background: transparent
            z-index: 2
            left: 0
        &.active
            display: block
        & ul.user__verif-list
            margin-top: 9px
            margin-right: 0
            display: block
        & li.user__verif-item 
            display: flex
            align-items: center
            margin: 8px 0 0 0   
            @include mobile
              padding: 0
              margin: 12px 0 0 0   
    &__verif-title
        font-size: 12px
        font-weight: 600
        .light & 
          color: $dark
        @include mobile
          font-size: 12px
    &__verif-icon
        width: 16px
        height: 16px
        flex: 0 0 16px
        margin-right: 5px
        mask-position: center
        mask-size: 16px
        mask-repeat: no-repeat
        background-color: $grey-l
        .light & 
          background-color: $dark-grey
        &--bitcoin
          mask-image: url('../img/icons/icons.svg#bitcoin')
        &--coin
          mask-image: url('../img/icons/icons.svg#coin-hand')
        &--rub
          mask-image: url('../img/icons/icons.svg#rub')
    &__verif-text
        flex: 0 0 auto
        font-size: 10px
        font-weight: 500
        color: $grey-l
        padding-right: 4px
        .light & 
          color: $dark-grey
    &__verif-check
        margin-left: auto
        flex: 0 0 14px
    &__verif-link
        margin-top: 12px
        display: block
        padding: 8px 16px
        border-radius: 17px
        background: $blue-l
        font-size: 12px
        line-height: 1.5
        font-weight: 600
        text-align: center
        color: #ffffff
  &__login-link
    align-self: center
    color: #fff
    font-weight: 500
    font-size: 16px
    line-height: 1.5
    text-align: center
    color: #061429
    @include mobile
      font-size: 12px
    & a
      font-weight: 600
      color: #061429