.f-profile
   background: #061429
   flex: 1 0 auto
   padding-top: 95px
   margin-top: -80px
   color: #ffffff
   font-family: 'Montserrat', sans-serif
   & .container
      @include desktop
         padding: 0 30px
   & section + section
      margin-top: 100px
      @include tablet
         margin-top: 80px
      @include smalltablet
         margin-top: 60px
      @include mobile
         margin-top: 40px
   &__top-wrapper
      text-align: center
      & h1
         font-weight: 700
         font-size: 36px
         line-height: 44px
         @include tablet
            font-size: 34px
         @include smalltablet
            font-size: 32px
   &__level-wrapper
      margin-top: 8px
      font-weight: 600
      font-size: 16px
      line-height: 19.5px
      @include tablet
         font-size: 14px
      @include smalltablet
         font-size: 12px
      & p:not(:first-of-type)
         margin-top: 2px
   &__body
      display: flex
      flex-wrap: wrap
      margin-top: 60px
      column-gap: 26px
      row-gap: 50px
      position: relative
      @include desktop
         &::after
            content: ''
            display: inline-block
            width: 2px
            height: 100%
            position: absolute
            top: 0%
            left: 50%
            transform: translateX(-50%)
            background-color: rgba(47, 71, 107, 0.5)
      @include tablet
         flex-direction: column
      @include smallmobile
         row-gap: 30px
   &__item
      width: calc(50% - 13px)
      display: grid
      grid-template-columns: 44px auto 150px
      grid-template-rows: auto auto
      align-items: center
      @include tablet
         width: 100%
         row-gap: 10px
         padding: 0 20px
      @include smalltablet
         padding: 0
      @include mobile
         grid-template-areas: "img title title" "text text text" "button button button"
   &__item-img
      grid-row: 1/2
      grid-column: 1/2
      width: 44px
      height: 44px
      mask-size: contain
      mask-repeat: no-repeat
      background-color: #ffffff
      @include mobile
         grid-area: img
   &__item-title
      grid-row: 1/2
      grid-column: 2/3
      margin: 0 13px
      font-weight: 600
      font-size: 26px
      line-height: 1.25
      @include tablet
         font-size: 24px
      @include smalltablet
         font-size: 22px
      @include mobile
         grid-area: title
      .f-profile__item--completed &
         grid-column: 2/3
         display: flex
         align-content: center
         column-gap: 18px
   &__item-text
      margin-top: 4px
      grid-row: 2/3
      grid-column: 1/3
      font-size: 14px
      line-height: 1.25
      align-self: flex-start
      @include tablet
         font-size: 14px
      @include smalltablet
         font-size: 12px
      @include mobile
         grid-area: text
      & p:not(:first-of-type)
         margin-top: 4px
      .f-profile__item--completed &
         @include smalltablet
            max-width: 77%
   &__item-btn
      grid-row: 1/2
      grid-column: 3/4
      background: #4799CF
      color: #ffffff
      border-radius: 38px
      border: 0
      outline: 0
      padding: 10px
      font-weight: 600
      font-size: 16px
      line-height: 1.5
      cursor: pointer
      transition: all .3s linear
      text-align: center
      @include tablet
         font-size: 14px
         padding: 8px
      @include smalltablet
         font-size: 12px
         padding: 7px
      @include mobile
         grid-area: button
      &:hover
         opacity: 0.8
   &__item-complete
      text-align: right
      position: relative
      z-index: 2
      &:hover::before
         animation: spin infinite 1.5s linear
      & img
         display: block
         position: relative
         z-index: 2
      &::before
         content: ''
         position: absolute
         width: 44px
         height: 44px
         top: -8px
         right: -8px
         background: url('../img/profile/complete-star.svg') center no-repeat
         z-index: 1
   &__disable
      margin-top: 70px
      text-align: right
      font-weight: 600
      font-size: 14px
      line-height: 1.21
      text-decoration-line: underline
      color: #FFFFFF
      cursor: pointer
      @include tablet
         padding: 0 20px
         font-size: 12px
      @include smalltablet
         padding: 0
         font-size: 10px
      &:hover
         opacity: 0.5

.light .f-profile
   background: #E8F6F9
   color: #061429
   &__item-img
      background-color: #061429
   &__disable
      color: #061429

@keyframes spin
   0%
      transform: rotate(0)
   100%
      transform: rotate(360deg)
