.n-wallet.n-wallet-history
  .main 
    grid-auto-rows: auto 680px
  .n-table__blok::before
    height: 49px
    left: -6px
    width: 16px
  .n-table__blok::after
    height: 49px
    right: -6px
    width: 16px
  .account-history-cryptocurrencies .n-table__cell:nth-child(3)
    @media (min-width: 1105px)
      width: 18%
  .account-history-cryptocurrencies .n-table__cell:nth-child(4)
    @media (min-width: 1105px)
      text-align: right
      width: 16%
  .account-history-cryptocurrencies .n-table__cell:nth-child(5)
    @media (min-width: 1105px)
      width: 16%
  .account-history-cryptocurrencies .n-table__cell:nth-child(6)
    @media (min-width: 1105px)
      width: 12%