.n-mainTables
  //background: url(../img/main/mainTablesBack.png) no-repeat bottom center
  background-size: 100% 100%
  padding: 354px 0 64px
  position: relative
  z-index: 5
  font-family: 'Montserrat', sans-serif
  &__list
    display: flex
    justify-content: space-between
  &__item
    width: calc((100% - 40px) / 3)
    margin-top: -66px
    &-head
      display: flex
      padding: 10px 7px 10px 26px
      background: #213151
      border-radius: 10px 10px 0 0
      border-bottom: 1px solid #152034
      &_logo
        width: 48px
        height: 48px
        margin: auto 24px auto 0
      &-text
        p
          &.name
            font-weight: 600
            font-size: 20px
            line-height: 24px
            color: #FFFFFF
          &.volume
            color: #D3D3D3
            font-size: 14px
            line-height: 17px
            margin: 8px 0
          &.price
            font-weight: 500
            font-size: 20px
            line-height: 24px
            color: #89B0FD
      &-right
        margin-left: auto
        display: flex
        flex-direction: column
        justify-content: space-between
        p
          font-weight: 600
          font-size: 20px
          line-height: 24px
          &.green
            color: #1BD079
          &.red
            color: #ED2661
        a
          width: 24px
          height: 24px
          margin-left: auto
          color: #152034
    &-body
      background: #213151
      padding-bottom: 10px
      &-table
        overflow-y: scroll
        max-height: 330px
        min-height: 330px
        margin: 0 4px 0 21px
        &::-webkit-scrollbar-track
          position: absolute
          background: #213151
        &::-webkit-scrollbar
          width: 3px
        &::-webkit-scrollbar-thumb
          background: #152034
      &-head
        padding: 12px 0
        display: flex
        margin: 0 7px 0 21px
        p
          color: #D3D3D3
          font-size: 14px
          line-height: 17px
          span
            font-weight: 500
      &-item
        display: flex
        padding: 4px 0
        p,a
          color: #fff!important
          font-weight: 500
          font-size: 12px
          line-height: 14px
          &.sell
            color: #FD5454!important
          &.buy
            color: #1BD079!important
      &-price
        width: 31%
      &-value
        width: 37%
      &-all
        width: 32%
        text-align: right
        box-sizing: border-box
        padding-right: 8px
  &_title
    font-weight: 700
    font-size: 30px
    line-height: 36px
    margin-bottom: 24px
    text-align: center
    color: #FFFFFF
    display: none
  &__tabs
    background: #213151
    padding: 12px
    border-radius: 10px
    width: 385px
    margin: 0 auto 20px
    display: none
    max-width: 100%
    &-head
      display: flex
      margin-bottom: 12px
      width: 100%
      p
        color: #FFFFFF
        font-size: 12px
        line-height: 15px
    &-name-col
      width: calc(100% - 193px)
    &-price-col
      width: 110px
      margin: 0 4px
    &-change-col
      width: 75px
      text-align: right
    &-item
      display: flex
      padding-bottom: 10px
      margin-bottom: 10px
      border-bottom: 1px solid #152034
      .n-mainTables__tabs-name-col
        display: flex
        img
          width: 32px
          height: 32px
          min-width: 32px
          min-height: 32px
          margin-right: 4px
        p
          font-weight: 500
          font-size: 12px
          line-height: 15px
          margin: auto 0
          color: #FFFFFF
          span
            font-weight: 400
            color: #DEEAFF
      .n-mainTables__tabs-price-col
        display: flex
        p
          text-align: center
          color: #FFFFFF
          font-weight: 500
          font-size: 12px
          line-height: 15px
          margin: auto 0
          width: 100%
      .n-mainTables__tabs-change-col
        display: flex
        p
          margin: auto 0
          font-weight: 500
          font-size: 12px
          line-height: 15px
          width: 100%
          &.green
            color: #1BD079
          &.red
            color: #FD5454
    .n-mainTables__tabs-item:last-child
      margin-bottom: 0
      padding-bottom: 0
      border-bottom: 0
@media screen and (max-width: 1200px)
  .n-mainTables
    background: linear-gradient(178.57deg, #142440 -39.57%, #152239 101.58%)
    padding: 0
    margin-top: -2px
    overflow: hidden
    .container
      max-width: 100%
    &_title
      display: block
    &__tabs
      display: block
    &__list
      justify-content: center
      flex-direction: column
      padding-bottom: 32px
    &__item
      width: 385px
      max-width: 100%
      position: absolute
      left: -10000px
      top: -10000px
      margin: 0 auto
      &.active
        position: static

@media screen and (max-width: 420px)
  .n-mainTables
    &__item
      &-head
        padding: 10px 7px 10px 15px
        &_logo
          width: 40px
          height: 40px
          margin: auto 15px auto 0
        &-text
          p
            &.name
              font-size: 18px
              line-height: 22px
            &.price
              font-size: 18px
              line-height: 22px
        &-right
          p
            font-size: 18px
            line-height: 22px
      &-body
        &-head
          p
            font-size: 11px
            line-height: 15px
        &-item
          margin-bottom: 5px
          p
            font-size: 11px
            line-height: 12px

























































