.n-wallet-header
  color: #FFFFFF
  &__inner
    display: flex
    align-items: center
    justify-content: space-between
    margin-top: 16px
    @media (max-width: 1214px)
      padding: 0 24px
    @media (max-width: 1023px)
      padding: 0
    @media (max-width: 1104px)
      flex-wrap: wrap
      & .n-wallet-header__type
        margin-top: 16px
        order: 3
        width: 100%
  &__links 
    margin-top: 12px
    display: flex
    padding: 5px
    background: #131D32
    border: 1px solid #40A7EA
    border-radius: 100px
    width: 360px
    @media (max-width: 1104px)
      margin: 12px auto 0
    @media (max-width: 380px)
      max-width: 100%
  &__toggle 
    align-items: center
    width: 50%
    color: #AFB6BA
    font-size: 12px
    line-height: 15px
    font-weight: 600
    padding: 4px
    cursor: pointer
    &:hover,&:active
      color: #fff
    &.active
      color: #fff
      background: #40A7EA
      border-radius: 100px
      &:hover,&:active
        color: #fff
  &__link
    position: relative
    width: 25%
    font-weight: 500
    font-size: 14px
    line-height: 17px
    text-align: left
    padding-left: 32px
    color: inherit
    align-self: flex-end
    margin-bottom: 5px
    @media (max-width: 1104px)
      width: 50%
      height: 17px
      top: 0
      & span
        display: none
    &:hover, &:active
      color: inherit
      text-decoration: underline
    &::before
      position: absolute
      content: ""
      width: 24px
      height: 24px
      top: -4px
      left: 0
      background-color: #40A7EA
      border-radius: 5px
      background-image: url(../img/wallet/arrow-btn-l.svg)
      background-position: center
      background-repeat: no-repeat
    &--next
      text-align: right
      padding-right: 32px
      padding-left: 0
      @media (max-width: 1104px)
        text-transform: capitalize
      &::before
        background-image: url(../img/wallet/arrow-btn.svg)
        left: auto
        right: 0
.n-wallet-refill .n-wallet-table .container 
  @media (max-width: 1104px)
    padding: 0
.n-wallet-control
  padding-bottom: 32px
  & .n-wallet-table__codes-btn 
    padding: 12px 57.5px 12px 36.5px
    &::after
      right: 35.5px
.n-wallet .n-wallet-table__control
  height: auto
  padding: 24px 24px 32px
  font-size: 14px
  background: #131D32
  border-radius: 20px
  margin-bottom: 32px
  @media (max-width: 1104px)
    padding: 24px 20px 32px
  & .n-table__blok
    margin-top: 16px
  &-inner
    display: flex
    flex-wrap: wrap
  &-setting
    width: 470px
    @media (max-width: 1104px)
      width: 100%
    & form
      width: 100%
    &--wide
      width: 100%
      max-width: 52.7%
      & .n-wallet-table__control-input, 
      & .n-wallet-table__control-address, 
      & .n-wallet-table__control-currency, 
      & .n-wallet-table__control-net
        width: 444px
        max-width: 444px
        @media (max-width: 1104px)
          max-width: 100%
          width: 100%
      @media (max-width: 1104px)
        max-width: 100%
        width: 100%
  &-currency
    margin-top: 4px
  &-faq
    max-width: 486px
    margin-left: auto
    @media (max-width: 1104px)
      margin-top: 32px
      max-width: 100%
      width: 100%
      order: 3
  &-title
    font-weight: 600
    font-size: 16px
    line-height: 20px
    &:not(:first-of-type)
      margin-top: 32px 
  &-links
    margin-top: 16px
  &-link
    &:not(:first-child)
      margin-top: 6px
    & a
      display: block
      background: #213151
      border-radius: 10px
      padding: 7px 60px 7px 16px
      font-weight: 400
      font-size: 14px
      line-height: 17px
      min-height: 48px
      display: flex
      align-items: center
      position: relative
      transition: all .3s linear
      color: inherit
      &::after
        position: absolute
        width: 24px
        height: 24px
        content: ""
        top: calc( 50% - 12px )
        right: 12px
        background-image: url(../img/wallet/arrow-link.svg)
      &:hover 
        background: #3D5481
        &::after
          background-image: url(../img/wallet/arrow-link-hover.svg)
  &-img
    margin-right: 8px
    width: 24px
    height: 24px
    object-fit: contain
  &-currency
    color: #fff
    position: relative
    margin-top: 4px
    max-width: 384px
    @media (max-width: 1104px)
      max-width: 100%
      width: 100%
  &-net
    color: #fff
    position: relative
    margin-top: 4px
    max-width: 384px
    @media (max-width: 1104px)
      max-width: 100%
      width: 100%
  &-select
    text-align: left
    position: relative
    &.select-show .select-dropdown 
      display: block
    &.select-show .select-toggle::after
      transform: rotate(180deg)
  & .select-toggle
      position: relative
      top: 0
      left: 0
      background: none
      border: 2px solid #3D5481
      border-radius: 10px
      outline: none
      padding: 10px 16px
      min-height: 48px
      width: 100%
      text-align: left
      font-weight: 400
      font-size: 14px
      line-height: 17px
      transition: all .3s linear
      &::after
          position: absolute
          content: ""
          top: 12px
          right: 16px
          width: 24px
          height: 24px
          background-image: url(../img/wallet/arrow-select.svg)
          background-position: center
          background-repeat: no-repeat
  & .select-dropdown
      padding: 8px 16px
      text-align: left
      flex-direction: column
      z-index: 10
      display: none
      position: absolute
      top: calc( 100% + 4px )
      right: 0
      width: 100%
      max-width: 100%
      height: auto
      box-sizing: border-box
      background: #131D32
      border: 2px solid #3D5481
      box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.16)
      border-radius: 10px
  & .select-option
      cursor: pointer
      padding: 8px 0
      transition: all .3s linear
      &:not(:last-child)
        border-bottom: 1px solid #14223B
      &:hover
          color: #40A7EA
  .select-dropdown-head
    position: relative
    text-align: center
    margin-bottom: 50px
  &-history 
    margin-top: 32px
    width: 100%
    font-size: 14px
    @media (max-width: 1104px)
      position: relative
      order: 2
      min-width: calc( 100% + 40px )
      left: -20px
      & .n-wallet-table__control-title
        margin-left: 20px
      & .n-table__line
        padding-left: 20px
        padding-right: 20px
  &-url
    margin-top: 4px
    display: flex
    align-items: center
    max-width: 100%
    @media (max-width: 1104px)
      flex-wrap: wrap
      max-width: 100%
      width: 100%
  &-code
    max-width: 400px
    font-weight: 400
    font-size: 14px
    line-height: 17px
    color: #40A7EA
    word-break: break-all
    @media (max-width: 1104px)
      max-width: calc( 100% -  28px)
      width: auto
  &-copy
    position: relative
    margin-left: 10px
    cursor: pointer
    @media (max-width: 1104px)
      margin-left: 4px
    &:hover
      & .n-wallet-table__control-copy-hint
        opacity: 1
        z-index: 1
  &-coped
    display: none
  &-copy-hint
    position: absolute
    z-index: -1
    opacity: 0
    transition: all .3s linear
    width: 190px
    bottom: calc( 100% + 13px )
    left: calc( 50% - 95px )
    padding: 15px 9px
    background: #213151
    border: 1px solid #40A7EA
    -webkit-border-radius: 8px
    border-radius: 8px
    font-weight: 400
    font-size: 12px
    line-height: 15px
    text-align: center
    @media (max-width: 1104px)
      left: -208px
      bottom: -19px
      height: 60px
      display: flex
      align-items: center
      justify-content: center
    &::after
      content: ""
      position: absolute
      bottom: -9px
      left: -webkit-calc(50% - 32.5px)
      left: calc(50% - 32.5px)
      width: 65px
      height: 9px
      background-repeat: no-repeat
      background-position: center
      background-image: url(../img/profile/beak.svg)
      @media (max-width: 1104px)
        bottom: calc(50% - 4.5px)
        left: auto
        right: -38px
        transform: rotate(-90deg)

  &-qr
    position: relative
    margin-left: 4px
    cursor: help
    @media (max-width: 1104px)
      width: 100%
      text-align: center
      & img
        display: none
    &:hover
      & .n-wallet-table__control-qr-hint
        opacity: 1
        z-index: 1
  &-qr-hint
    position: absolute
    z-index: -1
    opacity: 0
    transition: all .3s linear
    bottom: calc( 100% + 13px )
    width: 255px
    left: calc(50% - 117.5px)
    padding: 15px 6px
    background: #213151
    border: 1px solid #40A7EA
    -webkit-border-radius: 8px
    border-radius: 8px
    text-align: center
    @media (max-width: 1104px)
      display: flex
      flex-direction: column-reverse
      align-items: center
      margin-top: 20px
      position: relative
      text-align: center
      width: 100%
      opacity: 1
      z-index: 1
      left: 0
      top: 0
      height: auto
      padding: 0
      background: transparent
      border: 0
      & img
        width: 195px
        border-radius: 4px
        border: 4px solid #FFF
        display: block
    &::after
      content: ""
      position: absolute
      bottom: -9px
      left: -webkit-calc(50% - 32.5px)
      left: calc(50% - 32.5px)
      width: 65px
      height: 9px
      background-repeat: no-repeat
      background-position: center
      background-image: url(../img/profile/beak.svg)
      @media (max-width: 1104px)
        display: none
  &-qr-title
    font-weight: 400
    font-size: 12px
    line-height: 15px
    margin-bottom: 15px
    @media (max-width: 1104px)
      margin-top: 8px
      margin-bottom: 0
      max-width: 205px
  &-qr-text
    margin-top: 16px
    position: relative
    padding-left: 18px
    text-align: left
    font-weight: 400
    font-size: 12px
    line-height: 15px
    @media (max-width: 1104px)
      display: none
    &:not(:first-child)
      margin-top: 6px
    & span
      color: #ED2661
      filter: blur(0)!important
    & b
      font-weight: 600
    &::before
      position: absolute
      content: ""
      width: 3px
      height: 3px
      border-radius: 50%
      left: 7px
      top: 7px
      background: #fff
  &-footer
    width: 100%
    margin-top: 32px
    font-weight: 400
    font-size: 14px
    line-height: 17px
    text-align: center
    @media (max-width: 1104px)
      order: 4
    & a
      color: #40a7ea
  &-disclaimer
    margin-top: 12px
    max-width: 384px
    font-weight: 500
    font-size: 12px
    line-height: 15px
    color: #ffffff
    padding-left: 28px
    background: url(../img/wallet/disclaimer.svg)
    background-repeat: no-repeat
    background-size: 24px
    background-position: 2px left
    filter: blur(0)!important
    & a
      color: #40a7ea
  &-description
    margin-top: 16px
  &-description--red
    color: #ED2661
    filter: blur(0)!important
    & .n-wallet-table__control-text
      font-size: 12px
    & .n-wallet-table__control-text span, .n-wallet-table__control-text a
      color: #40A7EA
  &-description--grid
    display: grid
    grid-template-columns: auto auto
    grid-template-rows: auto auto 
    flex-wrap: wrap
    column-gap: 48px
    row-gap: 16px
    & .n-wallet-table__control-text
      padding-left: 0
      &::before
        display: none
  &-suptext
    margin-bottom: 8px
    font-weight: 400
    font-size: 14px
    line-height: 17px
    color: #AFB6BA
  &-text
    margin-top: 16px
    position: relative
    padding-left: 18px
    font-weight: 500
    font-size: 14px
    line-height: 17px
    &:not(:first-child)
      margin-top: 6px
    & span
      font-weight: 400
      color: #ED2661
      filter: blur(0)!important
    &::before
      position: absolute
      content: ""
      width: 3px
      height: 3px
      border-radius: 50%
      left: 7px
      top: 7px
      background: #fff
  &-inputs
    display: flex
    width: 100%
  &-inputs-item 
    &:first-child
      flex: 1 0 384px
      margin-right: 20px
    margin-top: 32px
  &-input-label
    padding-left: 18px
    margin-top: 4px
    font-weight: 700
    font-size: 12px
    line-height: 15px
    color: #105480
  &-input
      margin-top: 4px
      padding: 13.5px 16px
      background: #131D32
      border: 2px solid #3D5481
      border-radius: 10px
      max-width: 384px
      width: 100%
      font-weight: 400
      font-size: 14px
      line-height: 17px
      box-sizing: border-box
      color: #fff
      @media (max-width: 1104px)
        max-width: 100%
      &:focus-visible
        outline: 0
      &.error
        border: 2px solid #FD5454
      &.disabled
        background: #253962
        border: 2px solid #3D5481
      &::placeholder
        color: #AFB6BA
  &-address
    position: relative
    width: 384px
    @media (max-width: 1104px)
      width: 100%
    & input 
      padding-right: 80px
      white-space: nowrap
      text-overflow: ellipsis
      overflow: hidden
  &-plus
    position: absolute
    right: 51px
    top: 15px
    cursor: pointer
  &-mark
    position: absolute
    width: 283px
    border: 2px solid #3D5481
    background: #131D32
    border-radius: 10px
    z-index: 10
    left: 0
    top: calc( 100% + 4px )
    display: none
    &.show
      display: block
  &-mark-head
    display: flex
    align-items: center
    padding: 12px 16px
    border-bottom: 1px solid #3D5481
    font-weight: 400
    font-size: 12px
    line-height: 15px
    letter-spacing: 0.085em
    text-transform: uppercase
  &-mark-del
    margin-left: auto
    margin-right: 8px
    cursor: pointer
  &-mark-add
    cursor: pointer
  &-mark-body
    padding: 8px
  &-mark-input
    padding: 12px 16px
    background: #131D32
    border: 2px solid #3D5481
    border-radius: 10px
    width: 100%
    font-weight: 400
    font-size: 14px
    line-height: 17px
    box-sizing: border-box
    color: #fff
    &:focus-visible
      outline: 0
    &::placeholder
      color: #AFB6BA
  &-addresses
    position: absolute
    right: 17px
    background: url(../img/wallet/addresses.svg)
    background-repeat: no-repeat
    background-size: contain
    background-position: center
    width: 24px
    height: 24px
    top: 16px
    & .select-toggle
      width: 24px
      height: 24px
      font-size: 0
      line-height: 0
      border: 0
      min-height: initial
      & p 
        font-size: 0
      &::after
        display: none
    &.select-show .select-dropdown
      display: block
    .select-dropdown
      width: 567px
      max-width: initial
      top: 41px
      right: -199px
      padding: 0
      z-index: 10
      @media (max-width: 1104px)
        right: -18px
        left: auto
      @media (max-width: 768px)
        width: calc( 100vw - 40px)
        left: calc( -100vw - -82px)
        right: auto
    .select-dropdown-head
      display: flex
      align-items: center
      padding: 12px 22px
      border-bottom: 1px solid #3D5481
      font-weight: 400
      font-size: 12px
      line-height: 15px
      letter-spacing: 0.085em
      text-transform: uppercase
      margin-bottom: 0
      @media (max-width: 575px)
        font-size: 10px
    .select-dropdown-label:first-child
      margin-right: 35px
      @media (max-width: 1104px)
        margin-right: 20px
    .select-options
      padding: 8px
    .select-option
      border: 2px solid #3D5481!important
      border-radius: 10px
      display: flex
      padding: 9.5px 12px
      align-items: center
      font-weight: 400
      font-size: 14px
      line-height: 17px
      &:not(:first-child)
        margin-top: 6px
    .select-option-name
      width: 115px
      max-width: 115px
      padding-right: 5px
      white-space: nowrap
      text-overflow: ellipsis
      overflow: hidden
      @media (max-width: 1104px)
        width: 100px
        max-width: 100px
      @media (max-width: 768px)
        font-size: 12px
        flex: 1 0 90px
    .select-option-address
      width: 365px
      font-size: 13px
      white-space: nowrap
      text-overflow: ellipsis
      overflow: hidden
      @media (max-width: 1104px)
        width: 370px
        padding-right: 5px
        word-break: break-all
        text-overflow: initial
        white-space: initial
        overflow: visible
        flex: 0 1 auto
      @media (max-width: 768px)
        font-size: 12px
    .select-option-del
      margin-left: auto
      flex: 1 0 auto

.light .n-wallet
  .n-wallet-header__links
    background: #EFF5F8
  .n-wallet-table__control
    background: #EFF5F8
  .n-wallet-header__toggle
    &:hover,&:active
      color: #142546
    &.active
      &:hover,&:active
        color: #fff
  .n-wallet-table__control .select-toggle
    background: #FCFEFF
    border: 2px solid #DEEAFF
    color: #142546
  .n-wallet-table__control .select-dropdown
    background: #FCFEFF
    border: 2px solid #DEEAFF
    color: #142546
  .n-wallet-table__control .select-option:not(:last-child)
    border-bottom: 1px solid #DEEAFF
  .n-wallet-table__control-input.disabled
    background: #F5F9FF
    border: 2px solid #DEEAFF
    color: #142546
  .n-wallet-table__control-input
    background: #FCFEFF
    border: 2px solid #DEEAFF
    color: #142546
  .n-wallet-table__control-link a
    background: #FCFEFF
    &::after
      background-image: url(../img/wallet/arrow-link-light.svg)
    &:hover
      background: #DEF1FF
  .n-wallet-table__control-copy-hint
    background: #FFFFFF
    border: 1px solid #40A7EA
  .n-wallet-table__control-qr-hint
    background: #FFFFFF
    border: 1px solid #40A7EA
    @media (max-width: 1104px)
      background: transparent
      border: 0
  .n-table__blok
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05)
    border-radius: 14px
  .n-table__head
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05)
  .n-wallet-table__control-text::before
    background: #ed2661
    filter: blur(0)!important
  .n-wallet-table__control-addresses .select-toggle
    background-color: transparent
    border: none
  .n-wallet-table__control-mark-head
    border-bottom: 2px solid #DEEAFF
  .n-wallet-table__control-mark
    background: #FCFEFF
    border: 2px solid #DEEAFF
  .n-wallet-table__control-mark-input
    color: #142546
    background: #FCFEFF
    border: 2px solid #DEEAFF
  .n-wallet-table__control-addresses .select-dropdown-head
    border-bottom: 2px solid #DEEAFF
  .n-wallet-table__control-addresses .select-options .select-option
    background: #fcfeff
    border: 2px solid #DEEAFF!important
  .n-wallet-table__control-disclaimer
    color: #142546