.n-profile .n-toolbar
    color: #ffffff
    @media (max-width: 1104px)
        flex-direction: row
        flex-wrap: wrap
        padding: 24px 20px
    &__user 
        display: flex
        align-items: center
        @media (max-width: 1104px)
            width: 100%
            margin-bottom: 16px
    &__avatar
        width: 33px
        height: 33px
        border-radius: 50px
        margin-right: 12px
    &__mail
        font-weight: 600 
        font-size: 16px
        line-height: 1.25
    &__status
        margin-left: 16px
        list-style-type: none
        display: flex
        @media (max-width: 1104px)
            margin-left: 0
    &__status-item
        position: relative
        font-size: 10px
        line-height: 1.5
        padding-left: 16px
        margin-left: 13.5px
        @media (max-width: 1104px)
            &:first-child
                margin-left: 0
        &::before
            position: absolute
            content: ""
            width: 12px
            height: 12px
            background-image: url(../img/profile/uncheck.svg)
            background-repeat: no-repeat
            background-position: center
            left: 0
            top: 1px
        &--check::before
            background-image: url(../img/profile/check.svg)
    &__country
        margin-left: auto
        display: flex
        align-items: center
    &__country-img  
        line-height: 1
        width: 16px
        height: 16px
        border-radius: 50%
    &__country-text  
        margin-left: 8px
        font-size: 14px
        line-height: 1.33
        @media (max-width: 340px)
            font-size: 12px
    &__btn 
        margin-left: auto
        background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
        border-radius: 7px
        padding: 8px 12.5px
        border: none
        outline: 0
        font-weight: 600
        font-size: 14px
        line-height: 17px
        color: #fff
        position: relative
        cursor: pointer
        @media (max-width: 1104px)
            display: none
        &:hover
            background: linear-gradient(180deg, rgba(45, 152, 222, 0.7) 0%, rgba(68, 170, 236, 0.7) 100%)
.light .n-profile .n-toolbar
    color: #142546



        
