.main
  font-family: 'Montserrat', sans-serif
  position: relative
  display: grid
  grid-template-columns: 243px 79%
  justify-content: space-between
  grid-auto-rows: auto 695px 34px
  grid-template-areas: 'toolbar toolbar' 'menu table' 'menu pages'
  margin-bottom: 32px
  padding-top: 20px
.n-menu
  grid-area: menu

  /* margin-right: 8px
  border-bottom-left-radius: 14px
  border-bottom-right-radius: 14px
  background-color: #FCFEFF

  &__list
    list-style-type: none

.n-menu__link, .n-menu__btn
  position: relative
  display: block
  width: 100%
  height: 61px
  padding-right: 30px
  padding-left: 65px
  font-size: 12px
  text-align: left
  border: none
  background-color: #FFFFFF00

.n-menu__link
  color: #8D94A2

.n-menu__btn
  display: none
  color: #40A7EA

.n-menu__link::after, .n-menu__btn::after
  content: ''
  position: absolute
  left: 25px
  top: 50%
  margin-top: -14px
  width: 24px
  height: 24px
  -webkit-mask-size: contain
  -webkit-mask-repeat: no-repeat
  -webkit-mask-position: center
  background-color: #8D94A2

.n-menu__orders::after
  -webkit-mask-image: url(../img/operation-history/menu/my-orders-icon.svg)

.n-menu__transaction::after
  -webkit-mask-image: url(../img/operation-history/menu/transaction-history-icon.svg)

.n-menu__account::after
  -webkit-mask-image: url(../img/operation-history/menu/history-account-icon.svg)

.n-menu__cryptocurrency::after
  -webkit-mask-image: url(../img/operation-history/menu/cryptocurrency-history-icon.svg)

.n-menu__p2p::after
  -webkit-mask-image: url(../img/operation-history/menu/history-p2p-icon.svg)

.n-menu__codes::after
  -webkit-mask-image: url(../img/operation-history/menu/ceibit-codes-icon.svg)

.n-menu__turnover::after
  -webkit-mask-image: url(../img/operation-history/menu/trade-turnover-icon.svg)

.n-menu__item_active .n-menu__link
  color: #40A7EA
  background: linear-gradient(90deg, rgba(0, 187, 165, 0.06) 0%, rgba(0, 180, 158, 0.02) 100%)

  &::before
    content: ''
    position: absolute
    top: 0
    left: 0
    width: 3px
    height: 100%
    background-color: #40A7EA

.n-menu__link:hover::before
  content: ''
  position: absolute
  top: 0
  left: 0
  width: 3px
  height: 100%
  background-color: #40A7EA

.n-menu__item_active .n-menu__link::after, .n-menu__link:hover::after, .n-menu__btn::after
  background-color: #40A7EA

// *  

.n-toolbar
  grid-area: toolbar
  display: flex
  align-items: center
  padding: 15px 25px
  border-top-left-radius: 14px
  border-top-right-radius: 14px
  background-color: #FCFEFF
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05)
  min-height: 72px

// * 

.n-search
  display: flex
  align-items: center
  position: relative
  margin-right: 16px
  padding-left: 46px
  width: 271px
  border: 1px solid #E2EEEC
  border-radius: 10px
  background-color: #F4F9F8

.n-search__btn
  position: absolute
  left: 13px
  width: 20px
  height: 20px
  cursor: pointer
  border: none
  background-image: url(../img/operation-history/search-icon.svg)
  background-size: contain
  background-repeat: no-repeat
  background-color: #F4F9F800

.n-search__inp
  height: 40px
  border: none
  outline: none
  background-color: #F4F9F800

  &::placeholder
    color: #AFB6BA

.n-toolbar__blok
  display: flex
  align-items: center
  width: 100%

