    border: 1px solid #C1ECFE

// * 

.n-table
  position: relative
  margin-top: 12px
  margin-bottom: 5px
  padding-right: 3px
  padding-left: 3px
  font-size: 14px
  border-radius: 14px
  background-color: #EFF5F8
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05)
  
  & .simplebar-horizontal
    bottom: 7px!important
    margin-left: 4px
    margin-right: 4px
    height: 11px

  & .simplebar-track.simplebar-horizontal .simplebar-scrollbar
    height: 8px

  & .simplebar-scrollbar.simplebar-visible:before
    opacity: 1 !important
    left: 2px
    right: 2px
    height: 100%
    background-color: #40A7EA!important

.n-main_load .n-table, .n-main_table_load .n-table
  pointer-events: none

.n-main_load .n-table::before, .n-main_table_load .n-table::before
  content: ''
  z-index: 1
  position: absolute
  top: 0
  right: 0
  bottom: 0
  left: 0
  width: 100%
  height: 100%
  background: rgba(202, 228, 251, 0.2)
  backdrop-filter: blur(5px)
  border-radius: 14px

.n-main_load .n-table::after, .n-main_table_load .n-table::after
  content: ''
  z-index: 2
  position: absolute
  top: 44%
  left: 47.4%
  width: 48px
  height: 48px
  background-image: url(../img/operation-history/loader.svg)
  transform: rotate(0deg)
  animation: load 4s linear infinite

.n-main_load .n-table__blok, .n-main_table_load .n-table__blok
  border-radius: 14px

.n-table__blok
  width: 100%
  position: relative
  border-spacing: 0

.n-my-orders .n-table__blok
  width: 1800px

.n-table__blok
  &::before, &::after
    content: ''
    position: absolute
    top: 0
    width: 14px
    height: 45px
    background-color: #FCFEFF

  &::before
    left: -3px
    border-top-left-radius: 14px

  &::after
    right: -3px
    border-top-right-radius: 14px

.n-main_load .n-table__blok
  &::after, &::before
    display: none

.n-main_table_load .n-table__blok
  &::after, &::before
    display: none

.n-table__head
  color: #40A7EA

.n-table__line
  background-color: #FCFEFF

  &:nth-child(2n)
    background-color: #EEFAFF

.n-table__open-clouse
  width: 215px

.n-table__title-line
  display: none
  text-align: left

.n-table__cell
  height: 54px
  text-align: center

  &:first-child
    padding-left: 12px
    text-align: left
    border-left: 3px solid #EFF5F8
    border-top-left-radius: 14px

  &:last-child
    padding-right: 12px
    text-align: right
    border-right: 3px solid #EFF5F8
    border-top-right-radius: 14px

.n-table__button
  font-weight: 600
  font-size: 14px
  line-height: 1.2
  color: #40A7EA
  padding: 4.5px 12px
  border: 1px solid #40A7EA
  border-radius: 7px
  background-color: transparent
  transition: all .3s linear

  &:hover 
    background-color: #40A7EA
    color: #fff

  &.disabled
    pointer-events: none
    cursor: default
    border: 0

.n-table__cancel
  font-weight: 600
  font-size: 14px
  line-height: 1.2
  color: #ED2661
  padding: 4.5px 15px
  border: 1px solid #ED2661
  border-radius: 7px
  background-color: transparent
  transition: all .3s linear

  &:hover 
    background-color: #ED2661
    color: #fff

.n-table__head .n-table__cell
  border-bottom: 9px solid #EFF5F8

.n-table__body .n-table__cell
  border-top: 3px solid #EFF5F8
  border-bottom: 3px solid #EFF5F8

  &:first-child
    border-bottom-left-radius: 14px

  &:last-child
    border-bottom-right-radius: 14px

.n-table__no-contect
  display: none
  position: absolute
  bottom: 0
  left: 50%
  font-size: 18px
  color: #AFB6BA
  text-align: center
  background-color: transparent
  transform: translateX(-50%)

.n-table_none
  .n-table__no-contect
    display: block

  .n-table__blok
    height: 280px

.n-table__more-link
  color: #2D98DE
  text-decoration: underline
  cursor: pointer

.n-details
  position: relative

.n-details__link
  position: absolute
  top: 7px
  left: 20px
  display: block
  width: 32px
  height: 32px
  border-radius: 7px
  background-color: #40A7EA
  background-image: url(../img/operation-history/arrow-white.svg)
  background-position: center
  background-repeat: no-repeat
  cursor: pointer

.n-details-toolbar
  position: relative

  .n-details__link
    display: none

