  &:last-child
    border-right: 1px solid #6CACD7
    border-top-right-radius: 8px
    border-bottom-right-radius: 8px
    background-image: url(../img/operation-history/page-next.svg)

.n-pages__item_active
  color: #40A7EA
  background-color: #F4F9F8

