.n-market__bidding
	position: relative
	grid-column: 1/3
	grid-row: 2
	padding: 10px 0 20px
	border-right: 4px solid #14223B 
	border-bottom: 4px solid #14223B 
	&::after
		content: ""
		position: absolute
		width: 4px
		height: 100%
		top: 0
		left: calc(50% - 2px)
		background: linear-gradient(180.01deg, #213151 3.19%, rgba(21, 32, 52, 0.970833) 100%)
	&-tabs
		padding: 0 12px
	&-tab
		position: absolute
		z-index: -1
		opacity: 0
		& + label
			cursor: pointer
			transition: all 0.3s ease-in-out
			margin-right: 20px
			font-weight: 700
			font-size: 14px
			line-height: 17px
			color: #7993ad
			&:hover, &:active
				color: #40A7EA
		&:checked + label
				cursor: default
				color: #40A7EA
	&-actions
		display: none
		padding: 4px 6px
		position: relative
		margin-bottom: 9px
	&-action
		position: absolute
		opacity: 0
		z-index: -1
		& + label
			padding: 4px 0
			width: calc(50% - 3px)
			background: transparent
			border-radius: 3px
			text-align: center
			font-weight: 600
			color: #7993ad
			font-size: 12px
			line-height: 15px
			&:not(:first-of-type)
				margin-left: 1px
		&:checked + label
			color: #ffffff
			background: #FD5454
			&::after
				position: absolute
				content: ""
				width: 100%
				height: 100%
				background: transparent
				border: 1px solid #FD5454
				border-radius: 3px 3px 0 0
				left: 0
				top: 0
				pointer-events: none
		&--green
			&:checked + label
				background: #1BD079
				&::after
					border: 1px solid #1BD079
	&-content
		display: none
		&.active 
			display: flex
	&-buy
		flex: 0 1 50%
		padding: 12px 26px 0 12px
		filter: blur(0)!important
		&:not(:first-of-type)
			padding: 12px 12px 0 28px
	&-title
		display: flex
		font-weight: 500
		font-size: 14px
		line-height: 17px
		color: #ffffff
	&-balance
		font-weight: 600
		padding-right: 1px
		margin-left: auto
		color: #7993ad
	&-form
		margin-top: 16px
	&-row
		display: flex
		align-items: center
		margin-top: 8px
		&:first-child
			margin-top: 0
	&-parameter
		width: calc(100% - 275px)
		font-weight: 400
		font-size: 12px
		line-height: 18px
		margin-right: auto
	&-input-wrapper
		position: relative
		width: 81px
		margin-right: 8px
	&-input
		flex: 1 1 auto
		height: 33px
		padding: 7px 12px 8px
		color: #ffffff
		font-size: 12px
		line-height: 1.5
		background: transparent
		border: 2px solid #3D5481
		border-radius: 7px
		&::placeholder
			color: #7993ad
		&:hover, &:focus-visible
			outline: none
			border: 2px solid #40A7EA
		&.disabled 
			cursor: auto
			background: #253962
			border: 2px solid #324873
			outline: none
			&::placeholder
				color: #7993ad
		&--short 
			width: 60px
			margin-right: 7px
			padding: 7px 6px 8px 12px
			flex: 0 1
		&--percent 
			width: 100%
			padding: 7px 17px 8px 12px
		&--index
			flex: 1 1 144px
			max-width: 144px
		&--moex
			flex: 1 1 122px
			max-width: 122px
	&-minus
		position: absolute
		background: transparent
		border: none
		width: 12px
		height: 12px
		background: url(../img/market/down.svg) center no-repeat
		right: 6px
		bottom: 4px
	&-plus
		position: absolute
		background: transparent
		border: none
		width: 12px
		height: 12px
		background: url(../img/market/up.svg) center no-repeat
		right: 6px
		top: 4px
	&-max 
		margin-left: 8px
		height: 33px
		font-weight: 600
		font-size: 12px
		line-height: 17px
		padding: 8px 13.5px
		color: #44AAEC
		background: transparent
		border-radius: 7px
		border: 1px solid #44AAEC
		transition: all 0.3s ease-in-out
		&:hover 
			color: rgba(68, 170, 236, 0.7)
			border: 1px solid rgba(68, 170, 236, 0.7)
		&:active 
			color: #49B0F2
			border: 1px solid #49B0F2
	&-btn
		margin-top: 16px
		width: 100%
		padding: 8px
		font-weight: 500
		font-size: 14px
		line-height: 17px
		color: #ffffff
		background: #1BD079
		border-radius: 7px
		border: none
		outline: none
		transition: all 0.3s ease-in-out
		&:hover
			background: #11ba69
		&:active 
			background: #24c778
		&--sale
			background: #FD5454
			&:hover
				background: #fc4040
			&:active 
				background: #f95f5f