.n-popup 
    z-index: 10000
    font-family: 'Montserrat', sans-serif
    display: none
    position: fixed
    padding: 0
    top: 0
    left: 0
    flex-direction: column
    justify-content: center
    height: 100vh
    width: 100vw
    background: rgba(21, 32, 54, 0.79)
    @include smalltablet
      padding: 10px 0
    &.js-show 
        display: flex
    &.js-hide 
        display: none
    &--phone
        & .n-popup__controls
            margin-top: 16px
    &--google
        text-align: center
        & .n-popup__inner 
            @media (max-width: 1104px)
                padding: 32px
                overflow: scroll
        & .n-popup__form
            max-width: 384px
            margin: 24px auto 0
        .n-popup__confirm
            margin-top: 24px
    &--telegram
        text-align: center
        & .n-popup__inner 
            padding: 32px 62px
            @media (max-width: 768px)
                max-width: 344px
                padding: 32px 12px
        & .n-popup__content 
            padding: 0
        & .n-popup__form
            margin: 24px auto 0
            @media (max-width: 768px)
                width: 100%
                max-width: 100%
        & .n-popup__controls
            margin-top: 24px
            justify-content: space-between
        & .n-popup__confirm 
            width: calc(50% - 4px)
            @media (max-width: 768px)
                margin-top: 8px
                width: 100%
                max-width: 100%
    &--notifications
        color: #fff
        & .n-popup__controls
            margin-top: 24px
    &--order
        & .n-popup__content
            padding: 32px 102px 15px
            @media (max-width: 768px)
                padding: 32px 12px 20px
        & .n-popup__text
            margin-top: 9px
            font-weight: 500
            font-size: 12px
            line-height: 1.3
            text-align: left
            & b 
                font-weight: 700
            & a 
                color: inherit
                &:hover
                    text-decoration: underline
            &:first-of-type
                margin-top: 33px
                @media (max-width: 768px)
                    margin-top: 18px
        & .n-popup__form-select .select-dropdown .select-option
            padding: 12px 0
    &__inner
        max-height: 80%
        max-width: 587px
        min-width: 587px
        display: inline-block
        margin: 0 auto
        background: #213151
        box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.1)
        border-radius: 20px
        position: relative
        overflow: hidden
        @media (max-width: 620px)
            min-width: initial
            max-width: calc( 100% - 16px )
        &--dark
            background: #061429
            border: 1px solid #89B0FD
        &--blue
            background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
            & .n-popup__controls
                justify-content: space-between
            & .n-popup__control
                color: #fff
                -webkit-text-fill-color: initial
                border: 1px solid #fff
                width: calc( 50% - 17px )
                background: transparent
                @media (max-width: 768px)
                    width: 100%
                &:hover
                    background: #fff
                    color: #39A2E5
        &--red
            background: linear-gradient(106.36deg, #FC5155 0%, #EE2861 100%)
            & .n-popup__controls
                justify-content: space-between
            & .n-popup__control
                color: #fff
                -webkit-text-fill-color: initial
                border: 1px solid #fff
                width: calc( 50% - 17px )
                background: transparent
                @media (max-width: 768px)
                    width: 100%
                &:hover
                    background: #fff
                    color: #39A2E5
        &--green
            background: linear-gradient(104.92deg, #14B467 0%, #1DE083 100%)
            & .n-popup__close
                background-color: #ffffff
            & .n-popup__control
                color: #fff
                -webkit-text-fill-color: initial
                border: 1px solid #fff
                background: transparent
                &:hover
                    background: #fff
                    color: #1DE083
        &--wide
            & .n-popup__content
                padding: 32px 17px
                @media (max-width: 768px)
                    max-width: 344px
                    padding: 32px 12px
    &__content
        max-height: 100%
        color: #fff
        text-align: center
        overflow: scroll
        ms-overflow-style: none
        overflow: -moz-scrollbars-none
        overscroll-behavior: none
        width: 100%
        padding: 32px 82px
        &--small-padding
            padding: 32px 42px
        @media (max-width: 768px)
            padding: 32px 12px
        &::-webkit-scrollbar
            width: 0
            height: 0
    &__close
        position: absolute
        top: 8px
        right: 8px
        content: ""
        width: 32px
        height: 32px
        cursor: pointer
        background-color: #40A6EA
        mask-image: url(../img/profile/popup-close.svg) 
        mask-position: center
        mask-repeat: no-repeat
        z-index: 1
        @media (max-width: 414px)
            right: 12px
    &__head
        display: flex
        align-items: center
        & .n-popup__title
            padding-left: 7px
            font-weight: 700
            font-size: 30px
            line-height: 1.2
            margin-bottom: 0
    &__title
        text-align: center
        font-family: 'Montserrat'
        font-weight: 600
        font-size: 36px
        margin-bottom: 16px
        line-height: 1.2
        color: #fff
        & b 
          font-weight: 700
        & a 
            color: #40A7EA
            word-break: break-word
        &--small
            margin-bottom: 24px
            font-size: 20px
            line-height: 1.22
        &--wide
            position: relative
            width: calc( 100% + 130px )
            left: -65px
            @media (max-width: 768px)
                width: 100%
                left: 0
        &--phone
            @media (max-width: 360px)
                font-size: 32px
    &__warning
        font-weight: 700
        font-size: 14px
        line-height: 1.21
        color: #FD5454
        text-align: left
        &:not(:first-child)
            margin-top: 12px
            
    &__list
        list-style-type: none
        text-align: left
        margin-top: 4px
        & li 
            padding-left: 20px
            position: relative
            font-size: 14px
            line-height: 1.21
            &::before
                content: ''
                position: absolute
                top: 8px
                left: 7px
                width: 3px
                height: 3px
                background-color: #ffffff
                border-radius: 50%

    &__controls
        margin-top: 15px
        display: flex
        align-items: center
        margin-left: auto
        &--market
            justify-content: space-between
        @media (max-width: 768px)
            display: block
    &__control
        text-align: center
        padding: 12px
        width: calc(50% - 4px)
        font-weight: 600
        font-size: 16px
        line-height: 1.22
        border: 1px solid #44AAEC
        border-radius: 7px
        background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
        -webkit-background-clip: text
        -webkit-text-fill-color: transparent
        background-clip: text
        text-fill-color: transparent
        transition: all 0.3s linear
        cursor: pointer
        &--market
            width: calc(50% - 24px)
        &--solo
            width: 100%
            margin-top: 45px
            margin-left: 0!important
        @media (max-width: 768px)
            width: 100%
        &:not(:first-child)
            margin-left: 8px
            @media (max-width: 768px)
                margin-left: 0
                margin-top: 8px
        &:hover, &:active 
            text-fill-color: initial
            -webkit-text-fill-color: initial
            color: #fff
            background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
    &__confirm
        margin-top: 24px
    &__label
        max-width: 300px
        margin: 16px auto 0
        font-size: 12px
        line-height: 1.4
        color: #CFCFCF
        text-align: center
    &__text
        margin-top: 24px
        font-weight: 400
        font-size: 16px
        line-height: 1.4
        text-align: center
        color: #FFFFFF
        &--bold
            font-weight: 700
            font-size: 16px
            margin-top: 12px
        &--border
            margin-top: 0
            border-top: 1px solid #36E691
            padding-top: 6px
            font-weight: 600
            font-size: 26px
            line-height: 1.4
            text-align: center
            @media (max-width: 768px)
                margin-top: 24px
        & a 
            color: #40A7EA
        & b
            font-weight: 600
        & + .n-popup__controls 
            margin-top: 12px
    &__img
        text-align: center
        margin: 14px 0
        @media (max-width: 768px)
            display: none
    &__qr
        margin: 12px auto 0
        filter: drop-shadow(0px 15px 20px rgba(0, 0, 0, 0.16))
        //   background: #213151
        background: #ffffff
        border-radius: 10px
        padding: 16px
        max-width: 222px
    &__form
        margin-top: 16px
    &__form-title
        font-weight: 600
        text-align: left
        font-size: 16px
        line-height: 1.25
        color: #FFFFFF
        position: relative
        &:not(:first-child)
            margin-top: 16px
        &--hint 
            padding-right: 20px
            position: relative
            &::after 
                content: ""
                position: absolute
                top: 2px
                right: 0
                width: 16px
                height: 16px
                background: url(../img/profile/hint.svg) center no-repeat
                cursor: pointer
            &:hover + .n-popup__form-hint 
                display: block
                position: absolute
                right: -55px
                top: -36px
                @media (max-width: 768px)
                    right: -8px
    &__form-recaptcha
        margin-top: 16px
        display: inline-block
        & + div 
            margin-top: 6px
    &__form-hint
        text-align: center
        z-index: 10
        width: 342px
        display: none
        background: #3D5481
        border: 1px solid #40A7EA
        border-radius: 8px
        font-weight: 400
        font-size: 12px
        line-height: 1.4
        color: #fff
        padding: 10px
        @media (max-width: 768px)
            width: 227px
            text-align: left
            font-size: 10px
        &::after
            content: ""
            position: absolute
            bottom: calc(50% - 4.5px)
            left: -16px
            width: 24px
            height: 9px
            background-repeat: no-repeat
            background-position: center
            background-image: url(../img/profile/beak.svg) 
            transform: rotate(90deg)
    &__form-wrapper
        position: relative
    &__form-eye
        content: ""
        position: absolute
        z-index: 6
        top: 16px
        right: 12px
        width: 24px
        height: 24px
        background-repeat: no-repeat
        background-position: center
        background-image: url(../img/eye.svg) 
        cursor: pointer
        &.close 
            background-image: url(../img/eye-close.svg) 
    &__form-input
        padding: 12px 16px
        width: 100%
        margin-top: 4px
        border: 2px solid #3D5481
        border-radius: 10px
        background: transparent
        font-size: 16px
        font-weight: 600
        line-height: 1.25
        color: #FFFFFF
        @media (max-width: 768px)
            width: 100%
        &.error
            border: 2px solid #FD5454
            & + .n-popup__form-error
                position: relative
                margin-top: 4px
                z-index: 1
                opacity: 1
        &:focus 
            outline: none
            border: 2px solid #3D5481
    &__form-block
        position: relative
        display: inline-block
        @media (max-width: 768px)
            display: block
    &__form-send
        position: absolute
        pointer-events: none
        top: 3px
        right: 16px
        width: 100%
        height: 100%
        font-weight: 400
        font-size: 12px
        line-height: 1.4
        text-align: right
        color: #CFCFCF
        padding: 17px 20px 12px 0
        &.js-hide
            display: none
        &::after 
            content: ""
            position: absolute
            right: 0
            top: calc(50% - 8px)
            width: 16px
            height: 16px
            background: url(../img/profile/focus.svg) center no-repeat
    &__form-label
        margin-top: 8px
        font-weight: 400
        text-align: left
        font-size: 12px
        line-height: 1.4
        color: #CFCFCF
        margin-bottom: 16px
        &--red 
            color: #FD5454
        &--blue 
            color: #40A7EA
            cursor: pointer
    &__form-error
        position: absolute
        z-index: -1
        opacity: 0
        font-weight: 500
        font-size: 12px
        line-height: 1.25
        color: #FD5454
        text-align: left
    &__form-link
        display: block
        margin-bottom: 24px
        font-size: 12px
        line-height: 1.4
        color: #40A7EA
    &__form-select
        color: #fff
        text-align: left
        position: relative
        &.select-show .select-dropdown 
            display: block
        &.select-show .select-toggle::after
            transform: rotate(180deg)
        &-img
            width: 24px
            height: 24px
            margin-right: 8px
            & img 
                width: 100%
        & .select-toggle
            color: #fff
            position: relative
            display: flex
            align-items: center
            top: 0
            left: 0
            background: #213151
            border: 2px solid #3D5480
            border-radius: 10px
            outline: none
            padding: 10px 46px 10px 16px
            min-height: 48px
            width: 100%
            text-align: left
            font-weight: 400
            font-size: 14px
            line-height: 17px
            transition: all .3s linear
            & p
                max-width: 100%
                text-overflow: ellipsis
                overflow: hidden
                white-space: nowrap
            & .del
                display: none
            &::after
                transition: all .3s linear
                position: absolute
                content: ""
                top: 10px
                right: 16px
                width: 24px
                height: 24px
                background-image: url(../img/wallet/arrow-select.svg)
                background-position: center
                background-repeat: no-repeat
        & .select-dropdown
            padding: 8px 16px
            text-align: left
            flex-direction: column
            z-index: 10
            display: none
            position: absolute
            top: calc( 100% + 4px )
            right: 0
            width: 100%
            max-width: 100%
            height: auto
            box-sizing: border-box
            background: #213151
            border: 2px solid #3D5480
            box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.16)
            border-radius: 10px
            max-height: 220px
            & .simplebar-track.simplebar-vertical 
                width: 7px
                top: 0px
                right: 2px
                pointer-events: auto
            & .simplebar-scrollbar:before
                height: 95%
                background: #40A7EA
            & .select-option
                display: flex
                align-items: center
                font-size: 14px
                line-height: 17px
                list-style-type: none
                cursor: pointer
                padding: 8px 0
                transition: all .3s linear
                position: relative
                &:not(:last-child)
                    border-bottom: 1px solid #3D5480
                &:first-of-type:not(:last-of-type)
                    padding-top: 0
                    & .del
                        top: calc( 50% - 16px )
                &:last-of-type:not(:first-of-type)
                    padding-bottom: 0
                    & .del
                        top: calc( 50% - 8px )
                &:hover
                    color: #40A7EA 
                & p 
                    max-width: calc( 100% - 30px )
                    text-overflow: ellipsis
                    overflow: hidden
                    white-space: nowrap
                & .del 
                    border: 0
                    outline: 0
                    background: transparent
                    width: 24px
                    height: 24px
                    background: url(../img/wallet/del-red.svg) center no-repeat
                    background-size: cover
                    position: absolute
                    right: 0
                    top: calc( 50% - 12px )
    &__fio
        position: relative
    &__fio-input
        color: #fff
        position: relative
        display: flex
        align-items: center
        top: 0
        left: 0
        background: #213151
        border: 2px solid #3D5480
        border-radius: 10px
        outline: none
        padding: 10px 35px 10px 16px
        min-height: 48px
        width: 100%
        text-align: left
        font-weight: 600
        font-size: 14px
        line-height: 17px
        transition: all .3s linear
        text-overflow: ellipsis
        overflow: hidden
        white-space: nowrap
    &__fio-more
        display: none
        transition: all .3s linear
        position: absolute
        content: ""
        top: 0
        right: 12px
        width: 30px
        height: 100% 
        background-image: url(../img/wallet/arrow-select.svg)
        background-position: center
        background-repeat: no-repeat
        background-size: 24px
        cursor: pointer
        &.show
            display: block
        &.active
            transform: rotate(180deg)
    &__fio-list
        display: none
        list-style-type: none
        padding: 8px 16px
        text-align: left
        flex-direction: column
        z-index: 10
        position: absolute
        top: calc( 100% + 4px )
        right: 0
        width: 100%
        max-width: 100%
        height: auto
        box-sizing: border-box
        background: #213151
        border: 2px solid #3D5480
        box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.16)
        border-radius: 10px
        max-height: 220px
        &.show
            display: block
        & .simplebar-track.simplebar-vertical 
            width: 7px
            top: 0px
            right: 2px
            pointer-events: auto
        & .simplebar-scrollbar:before
            height: 95%
            background: #40A7EA
    &__fio-item
        display: flex
        align-items: center
        font-weight: 600
        font-size: 14px
        line-height: 17px
        list-style-type: none
        cursor: pointer
        padding: 11px 0
        transition: color .3s linear
        position: relative
        &:not(:last-child)
            border-bottom: 1px solid #3D5480
        &:first-of-type:not(:last-of-type)
            padding-top: 3px
            & .del
                top: calc( 50% - 16px )
        &:last-of-type:not(:first-of-type)
            padding-bottom: 3px
            & .del
                top: calc( 50% - 8px )
        &:hover
            color: #40A7EA 
        & p 
            max-width: calc( 100% - 30px )
            text-overflow: ellipsis
            overflow: hidden
            white-space: nowrap
        & .del 
            border: 0
            outline: 0
            background: transparent
            width: 24px
            height: 24px
            background: url(../img/wallet/del-red.svg) center no-repeat
            background-size: cover
            position: absolute
            right: 0
            top: calc( 50% - 12px )
    &__subtitle 
        text-align: center
        margin-top: 16px
        margin-bottom: 24px
        font-weight: 400
        font-size: 12px
        line-height: 1.4
        color: #CFCFCF
        &--bold
            font-weight: 600
            font-size: 14px
            margin-bottom: 2px
            color: #ffffff
    &__link
        display: block
        margin-top: 8px
        font-weight: 400
        font-size: 16px
        line-height: 1.4
        color: #40A7EA
    &__or
        margin: 12px 0
        font-size: 12px
        line-height: 1.4
        color: #fff
        & + *
            margin-top: 0
    &__confirm
        width: 100%
        border: none
        outline: none
        padding: 12px
        background: linear-gradient(180deg, rgba(45, 152, 222, 0.4) 0%, rgba(68, 170, 236, 0.4) 100%)
        border-radius: 10px
        font-weight: 600
        font-size: 16px
        line-height: 1.5
        color: #fff
        &[disabled]
            opacity: 0.7
    &__btn
        cursor: pointer
        text-align: center
        margin-top: 24px
        width: 100%
        border: none
        outline: none
        padding: 12px
        background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)  
        border-radius: 10px
        font-weight: 600
        font-size: 16px
        line-height: 1.5
        color: #fff
        &:hover, &:active 
            text-fill-color: initial
            -webkit-text-fill-color: initial
            color: #fff
            background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
    &__subtext 
        margin-top: 16px
        font-weight: 400
        font-size: 12px
        line-height: 1.4
        color: #fff
        & span 
            color: #40A7EA
            cursor: pointer
    .n-profile-table__label
        position: relative
        font-weight: 400
        font-size: 12px
        line-height: 1.4
        padding: 3.5px 0 3.5px 32px
        cursor: pointer
        text-align: left
        & a 
            color: #40A7EA
        &::before
            position: absolute
            content: ""
            left: 0
            top: calc(50% - 12px)
            width: 24px
            height: 24px
            border: 2px solid #3D5481
            border-radius: 3px
            transition: border-color 0.3s linear
        &--blue
            color: #40A7EA
        &--disabled
            pointer-events: none
            color: #8D94A1
            &::before
                border: 2px solid #8D94A1 !important
                background-image: url(../img/profile/check-gray.svg) !important
        & span
            color: #FD5454
    .n-profile-table__label.check
        &::before
            border: 2px solid #40A7EA
            background-image: url(../img/profile/check-blue.svg) 
            background-repeat: no-repeat
            background-position: center
    .n-profile-table__checkbox
        max-width: 384px
        margin-top: 24px
        & + .n-profile-table__checkbox
            margin-top: 8px
.light .n-popup
    .n-popup__close
        background: url(../img/profile/popup-close.svg) center no-repeat
    .n-popup__inner
        background: #FCFEFF
        &--dark
            background: #FFFFFF
            border: 1px solid #89B0FD
        &--green
            background: linear-gradient(104.92deg, #14B467 0%, #1DE083 100%)
            & .n-popup__title
                color: #fff
            & .n-popup__text
                color: #fff
        &--red
            background: linear-gradient(106.36deg, #FC5155 0%, #EE2861 100%)
            & .n-popup__title
                color: #fff
            & .n-popup__text
                color: #fff
        &--blue
            background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
            & .n-popup__title
                color: #fff
            & .n-popup__text
                color: #fff
    .n-popup__title
        color: #142546
    .n-popup__subtitle
        color: #142546
    .n-popup__form-title
        color: #142546
    .n-popup__form-input
        border: 2px solid #DEEAFF
        color: #142546
    .n-popup__form-label
        color: #61759D
        &--red 
            color: #FD5454
        &--blue 
            color: #40A7EA
    .n-popup__label
        color: #61759D
    .n-popup__text
        color: #142546
    .n-popup__list
        color: #142546
    .n-popup__list li::before
        background-color: #142546
    .n-popup__qr
        background: #FFFFFF
    .n-popup__confirm
        background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
    .n-popup__or
        color: #142546
    .n-popup__subtext
        color: #142546
    .n-profile-table__label::before
        border: 2px solid #DEEAFF
    .n-profile-table__label.check::before
        border: 2px solid #40A7EA
    .n-profile-table__label
        color: #142546
    .n-popup__form-select .select-toggle
        background: #FCFEFF
        border: 2px solid #DEEAFF
        color: #142546
    .n-popup__fio-input
        background: #FCFEFF
        border: 2px solid #DEEAFF
        color: #142546
    .n-popup__form-select.select-show .select-dropdown
        background: #FCFEFF
