.n-market__feed
	grid-column: 3/4
	grid-row: 4/6
	padding: 12px 11px 12px 12px
	&-title 
		font-weight: 700
		font-size: 14px
		line-height: 1.21
		color: #40A7EA
	&-head
		display: flex
		justify-content: space-between
		margin-top: 16px
		list-style-type: none
		font-weight: 400
		font-size: 12px
		line-height: 1.21
		text-align: center
		margin-bottom: 12px
		padding-right: 7px
	&-body
		padding-right: 8px
		height: 383px
	&-row
		display: flex
		justify-content: space-between
		list-style-type: none
		font-weight: 400
		font-size: 12px
		line-height: 1.21
		text-align: center
		&:not(:first-child)
			margin-top: 8.5px
		& .n-market__feed-price 
			color: #1BD079
		& .n-market__feed-price--red
			color: #FD5454
		& li
			white-space: nowrap
	&-time
		width: 13.5%
	&-price
		width: 12.8%
		text-align: left
	&-volume
		width: 25.2%
		text-align: right
	&-total
		width: 31.5%
		text-align: right