.n-p2p-ad-list
  background: rgba(33, 59, 86, 0.39)
  border: 1px solid #89B0FD
  backdrop-filter: blur(10px)
  border-radius: 20px
  display: block !important
  & .n-p2p-table__column-status
    text-align: center
    padding: 0
  & .n-p2p-table__column-status::after 
    display: none
  &__header
    display: flex
    justify-content: space-between
    align-items: center
    padding: 12px 24px
  &__title
    font-weight: 600
    font-size: 20px
    line-height: 24px
    color: #FFFFFF
  &__btn
    .n-p2p-add-payment-methods
      padding: 0
    button
      margin: 0
      max-width: 129px
      padding: 0 6px
      color: #FFFFFF
      text-align: center
      & a
        color: inherit
      @media all and (max-width: 420px)
         font-size: 0
         min-width: 33px
         max-width: 33px
         padding: 7px
         background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%), url("../../../img/p2p-add-list.svg") center center/contain no-repeat
         background-size: 24px
         background-blend-mode: overlay
         color: inherit
      @media all and (max-width: 420px)
         display: inline-block
         vertical-align: middle
         width: 100%
         height: 100%
  .n-p2p-table__block.ad
    border: 1px solid #4f689b
  .n-p2p-table__block.ad:not(:last-child)
    border-bottom: 0 !important
  &__table-title-id
    font-weight: 500
    font-size: 12px
    line-height: 17px
    color: #FFFFFF
  &__table-title
    font-weight: 400
    font-size: 12px
    line-height: 17px
    color: #FFFFFF
  .n-p2p-table__column
    &:nth-child(1)
        width: 6.8%
        text-align: left
    &:nth-child(2)
        width: 11.1%
        text-align: center
    &:nth-child(3)
        width: 25.3%
        text-align: center
    &:nth-child(4)
        width: 13%
        text-align: center
    &:nth-child(5)
        width: 17.5%
        text-align: center
    &:nth-child(6)
        width: 13.7%
        text-align: center
    &:nth-child(7)
        width: 8.66%
        text-align: right
        display: flex
        justify-content: center
        .n-p2p-ad-list__column-icons
          align-items: center
          display: flex
    &-status
      font-weight: 500
      font-size: 14px
      line-height: 17px
      color: #FD5454
      &.red
        color: #FD5454
      &.green
        color: #14B467
  &__column-change
      margin-right: 12px
    
body.light .n-p2p-ad-list
  &__title
    color: #142546
  &__table-title-id
    color: #142546
  &__table-title
    color: #142546
  .n-p2p-table__block
    border: none !important
    border-bottom: 1px solid #89B0FD57 !important


.n-p2p-ad-list
  @media screen and (max-width: 900px)
    .n-p2p-table__column:nth-child(7)
      justify-content: space-between
      .n-p2p-ad-list__column-icons
        max-width: 60px
        display: flex
.n-p2p-no-data
  width: 100%
  justify-content: center
  align-items: center
  display: flex
  h4
    font-weight: 400
    font-size: 14px
    line-height: 20px
    color: #b1cbda
    padding: 230px 0 272px
  @media screen and (max-width: 768px)
    h4
      padding: 165px 0 207px
  &--short
    h4
      padding: 70px 0 72px
      @media screen and (max-width: 768px)
        padding: 65px 0 40px
  


#p2p-attention, #p2p-settings, #p2p-delete
  .n-popup__inner
    width: 100%
    .n-popup__p2p-head
      justify-content: center
  .n-popup__p2p-text
    max-width: 234px
  .n-popup__p2p-text-delete
    max-width: 398px
  img, .n-popup__p2p-img
    max-width: 91px
  .n-popup__control
    color: #fff
    -webkit-text-fill-color: #fff
    border: 2px solid #FFFFFF
    background: transparent
    max-width: 188px
  .n-popup__control:hover
    background: transparent
  .n-popup__controls
    justify-content: center
  .n-popup__control:hover
    background: #FFFFFF
    color: #FD5454
    -webkit-text-fill-color: #FD5454
  .n-popup__content.blue
    background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
    .n-popup__control:hover
      color: #3AA3E6
      -webkit-text-fill-color: #3AA3E6
    .n-popup__p2p-head
        border-bottom: 1px solid #44AAEC

#p2p-attention, #p2p-settings, #p2p-delete
  @media screen and (max-width: 590px)
    .n-popup__inner
      padding: 0 8px
      background: transparent
  @media screen and (max-width: 768px)
    .n-popup__content
      max-width: 100%
      padding: 32px 28px 16px
    .n-popup__controls
      display: flex
      .n-popup__control:not(:first-child)
        margin-top: 0
        margin-left: 8px
  @media screen and (max-width: 420px)
    .n-popup__p2p-img
      display: none
    .n-popup__p2p-head
      .n-popup__p2p-title, .n-popup__p2p-subtitle
        text-align: center
    .n-popup__controls
      flex-direction: column
      .n-popup__control
        max-width: 312px
      .n-popup__control:not(:first-child)
        margin-top: 13px
        margin-left: 0

#p2p-ad-ls-okey, #p2p-p2p-ad-ls-error
  @media screen and (max-width: 768px)
    .n-popup__inner
      margin: 0 8px
      .n-popup__content
        max-width: 100%
  @media screen and (max-width: 540px)
    .n-popup__content
      padding: 32px 28px 16px
  @media screen and (max-width: 460px)
    .n-popup__p2p-img
      display: none
    .n-popup__p2p-text
      margin-left: 0
      .n-popup__p2p-title
        text-align: center
        font-size: 28px
        line-height: 34px
    .n-popup__content
      padding: 32px 16px 16px