.n-about-us
  background: url(../img/main/aboutUsBack2.png) no-repeat bottom center
  background-size: 100% 100%
  font-family: 'Montserrat', sans-serif
  padding: 361px 0 64px
  margin-top: -296px
  @media screen and (max-width:  768px)
    margin-top: 0
    padding: 60px 0
    overflow: hidden
    position: relative
    background: linear-gradient(180deg, #131D33 -53.4%, #131D33 100%)
  &_title
    text-align: center
    font-weight: 700
    line-height: 44px
    font-size: 36px
    color: #FFFFFF
    margin-bottom: 32px
    @media screen and (max-width: 1023px)
      font-size: 30px
      line-height: 37px
    span
      color: #40A7EA
  &__circle
    position: relative
    height: 704px
    padding-top: 37px
    @media screen and (max-width:  1023px)
      height: 504px
    @media screen and (max-width:  768px)
      height: auto
      padding-top: 0
      padding-left: 236px
    &_telephone
      position: absolute
      top: 60px
      left: 50%
      transform: translateX(-50%)
      @media screen and (max-width: 1023px)
        width: 156px
        height: 315px
      @media screen and (max-width: 768px)
        position: absolute
        top: -8px
        left: 0
        transform: translateX(0)
        width: 170px
        height: 355px
        transform: translateX(-40%)
        left: -38px
      &.light
        display: none
    &_ellipse
      position: absolute
      top: 0
      left: 50%
      transform: translateX(-50%)
      @media screen and (max-width: 1023px)
        width: 424px
        height: 424px
      &.light
        display: none
    &-row
      display: flex
      justify-content: space-between
      .n-about-us__circle-item:first-child p
        text-align: right
      .n-about-us__circle-item:last-child p
        text-align: left
      &.first
        margin: 0 0 143px
        padding: 0 187px
        @media screen and (max-width: 1400px) 
            padding: 0 89px
        @media screen and (max-width: 1200px) 
          padding: 0 60px
        @media screen and (max-width: 1023px)    
          padding: 0
          max-width: 750px
          margin: 0 auto 70px
        p
          width: 215px
      &.second
        margin-bottom: 126px
        padding: 0 108px 0 125px
        @media screen and (max-width: 1400px) 
            padding: 0 42px 0 34px
        @media screen and (max-width: 1200px) 
          padding: 0 0
          margin-left: -25px
          margin-right: -25px
        @media screen and (max-width: 1023px)    
          padding: 0
          max-width: 820px
          margin: 0 auto 100px
        p
          width: 182px
      &.third
        padding: 0 136px
        @media screen and (max-width: 1400px) 
          padding: 0 38px
        @media screen and (max-width: 1023px) 
          max-width: 750px   
          margin: 0 auto
        p
          width: 266px
          box-sizing: border-box
        .n-about-us__circle-item:last-child
          p
            padding-right: 60px
      &.fourth
        justify-content: center
        .n-about-us__circle-item
          flex-direction: column
          margin-top: -17px
          img
            margin: 0 auto 24px
          p
            width: 384px
            text-align: center
    &-item
      display: flex
      position: relative
      z-index: 4
      p
        font-weight: 600
        font-size: 16px
        line-height: 20px
        margin: auto 0
        color: #FFFFFF

      img
        width: 94px
        margin: 0 24px
        &.light
          display: none
@media screen and (max-width: 1200px)
  .n-about-us
    &__circle-item img
      width: 64px
      margin: 0 0 0 8px
    &__circle-item p
      font-size: 14px
      max-width: 200px
    &__circle-row.third
      padding: 0 0
@media screen and (max-width: 1023px)
    .n-about-us
      & .container
        max-width: 100%
        padding: 0 30px
@media screen and (max-width: 768px)
  .n-about-us
    &_title
      line-height: 37px
      font-size: 30px
      margin-bottom: 32px
    &__circle
      &_ellipse
        display: none !important
      &-row
        justify-content: flex-start
        flex-direction: column
        .n-about-us__circle-item:first-child p
          text-align: left
        &.first
          margin: 0
          padding: 0
          p
            width: 100%
            max-width: 100%
        &.second
          margin-bottom: 0
          padding: 0
          p
            width: 100%
            max-width: 100%
        &.third
          padding: 0
          p
            width: 100%
            max-width: 100%
          .n-about-us__circle-item:last-child
            p
              padding-right: 60px
        &.fourth
          .n-about-us__circle-item
            flex-direction: row
            margin-top: 0
            img
              margin: auto 12px auto 0
            p
              width: 100%
              max-width: 100%
              text-align: left
      &-item
        margin-bottom: 16px
        p
          font-size: 13px
          line-height: 16px
          order: 2
        img
          order: 1
          width: 42px
          min-width: 42px
          height: 42px
          margin: auto 12px auto 0

@media screen and (max-width: 768px)
  .n-about-us
    .container
      max-width: 100%
    &__circle
      padding-left: 104px

@media screen and (max-width: 340px)
  .n-about-us
    margin-top: -60px
