.n-p2p-main 
    width: 100%
    display: grid
    grid-template-areas: "A A A A  A A A A  A A A A" "B B B B  B B B B  C C C C" "D D D D  D D F F  F F F F"
    gap: 20px
    grid-template-columns: repeat(12, 1fr)
    position: relative
    z-index: 1
    min-height: 200px
    @media (max-width: 768px)
        display: block
    &__head
        padding: 24px
        color: #fff
        grid-area: A
        @media (max-width: 1200px)
            padding: 12px
    &__row
        display: flex
        align-items: center
        &:first-child
            min-height: 33px
        @media (max-width: 768px)
            display: grid
            grid-template-rows: repeat(3, auto)
            grid-template-columns: 33px auto auto
            justify-content: start
        @media (max-width: 413px)
            display: grid
            grid-template-rows: repeat(3, auto)
            grid-template-columns: 33px auto 
        &:not(:first-child)
            justify-content: space-between
            margin-top: 15px
            padding-top: 15px
            border-top: 1px solid rgba(64, 167, 234, 0.2)
            @media (max-width: 768px)
                margin-top: 12px
                padding: 0
                border-top: 0
                display: flex
                flex-direction: column
                align-items: flex-start
    &__avatar
        width: 33px
        text-align: center
        text-transform: uppercase
        background: #182C52
        font-weight: 600
        font-size: 12px
        line-height: 15px
        margin-right: 8px
        border-radius: 50%
        padding: 9px 2px
        color: #fff
        &.verified
            background: linear-gradient(135deg, #FFAE49 0%, #FF74DB 100%)
        @media (max-width: 768px)
            margin-right: 0
            grid-row: 1
            grid-column: 1
            width: 33px
            height: 33px
    &__name
        font-weight: 500
        font-size: 14px
        line-height: 17px
        display: flex   
        align-items: center
        @media (max-width: 768px)
            margin-left: 8px
    &__status
        margin-left: 16px
        margin-right: 16px
        font-weight: 500
        font-size: 10px
        line-height: 12px
        color: #fff
        background: #FD5454
        border-radius: 100px
        padding: 4px 8px
        @media (max-width: 768px)
            grid-row: 2
            grid-column: 1/4
            width: max-content
            margin-top: 7px
            margin-left: 0
        &.green
            color: #fff
            background: #0ebf6b
        &.verified
            color: #FFFFFF
            background: linear-gradient(90.8deg, #FFAF47 0.62%, #FF6FE8 100%)
    &__verifications
        margin-left: 0
        list-style-type: none
        display: flex
        @media (max-width: 768px)
            margin-left: 13px
        @media (max-width: 413px)
            margin-left: 0
            margin-top: 7px
            grid-row: 3/4
            grid-column: 1/3
    &__verification
        position: relative
        font-size: 12px
        line-height: 21px
        padding-left: 23px
        margin-left: 13.5px
        @media (max-width: 768px)
            font-size: 10px
        &:first-child
            margin-left: 0
        &::before 
            position: absolute
            content: ""
            width: 18px
            height: 18px
            background-image: url(../img/profile/uncheck.svg)
            background-repeat: no-repeat
            background-position: center
            background-size: cover
            left: 0
            top: 1px
        &.check::before
            background-image: url(../img/profile/check.svg)
        &.validate::before
            background-image: url(../img/profile/validate.svg)
    &__review
        width: 32%
        @media (max-width: 768px)
            padding: 14px 0
            border-top: 1px solid rgba(64, 167, 234, 0.2)
            border-bottom: 1px solid rgba(64, 167, 234, 0.2)
            display: flex
            align-items: center
            width: 100%
            justify-content: space-between
            text-align: center
        &:nth-child(2), &:nth-child(3)
            text-align: center
        &:nth-child(4)
            text-align: center
            width: 15%
            @media (max-width: 768px)
                width: 100%
                text-align: left
                border-bottom: 0
                padding-bottom: 2px
        &-name
            font-weight: 600
            font-size: 16px
            line-height: 1.21
            color: #fff
            @media (max-width: 768px)
                font-size: 14px
        &-count
            margin-top: 16px
            font-weight: 600
            font-size: 16px
            line-height: 1.21
            color: #fff
            text-align: center
            @media (max-width: 768px)
                margin-top: 0
                font-size: 14px
    &__rules
        grid-area: C
        max-width: 284px
        padding: 16px 21px
        display: flex
        flex-direction: column
        text-align: center
        background-image: url(../img/p2p/main/shadow.png)
        background-repeat: no-repeat
        background-position: center 
        @media (max-width: 1200px)
            padding: 12px
            max-width: initial
        @media (max-width: 768px)
            padding: 16px
            width: 100%
            max-width: 100%
        &-title
            font-weight: 600
            font-size: 18px
            line-height: 1.2
            color: #fff
        &-img
            margin: auto
            width: 122px
            height: 122px
            @media (max-width: 768px)
                margin: 27px auto
        &-link
            display: inline-block
            align-self: center
            font-weight: 600
            font-size: 16px
            line-height: 1.5
            background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
            -webkit-background-clip: text
            -webkit-text-fill-color: transparent
            background-clip: text
            text-fill-color: transparent
            padding-right: 21px
            position: relative
            &::after
                content: ""
                position: absolute
                right: 0
                top: calc(50% - 8px)
                width: 16px
                height: 16px
                background-image: url(../img/p2p/main/arrow.svg)
                background-repeat: no-repeat
                background-position: center 
    &__stats
        grid-area: B
        padding: 24px 19px 24px 24px
        color: #fff
        @media (max-width: 1200px)
            padding: 12px
        &-head
            display: flex
            justify-content: space-between
        &-title
            font-weight: 600
            font-size: 18px
            line-height: 1.2
            @media (max-width: 360px)
                max-width: 100px
        &-tab
            z-index: -1
            opacity: 0
            width: 0
            height: 0
            & + label
                cursor: pointer
                margin-left: 12px
                font-weight: 700
                font-size: 14px
                line-height: 1.2
                color: #AFB6BA
            &:checked + label
                color: #40A7EA
        &-chart
            display: none
        &-chart.active
            display: block
        &-content
            margin-top: 8px
            display: flex 
            @media (max-width: 1200px)
                flex-direction: column
        &-info 
            margin-right: 51px
            @media (max-width: 1200px)
                margin-top: 5px
                margin-bottom: 10px
                margin-right: 0
                display: flex
                align-items: center
            @media (max-width: 768px)
                display: block
        &-list 
            list-style-type: none
            margin-top: 20px
            @media (max-width: 1200px)
                width: 100%
                margin-top: 0
                display: flex
                justify-content: space-between
                margin-right: 30px
            @media (max-width: 768px)
                display: block
        &-item
            display: flex
            justify-content: space-between 
            @media (max-width: 1200px)
                flex-direction: column
            @media (max-width: 768px)
                flex-direction: row
            &:not(:first-child)
                margin-top: 15px
                @media (max-width: 1200px)
                    margin-top: 0
                    margin-left: 10px
                @media (max-width: 768px)
                    margin-top: 12px
                    margin-left: 0px
        &-name
            font-weight: 600
            font-size: 14px
            line-height: 20px
            @media (max-width: 1200px)
                font-size: 14px
                text-align: center
        &-value
            margin-left: 51px
            font-size: 18px
            font-weight: 600
            line-height: 1.25
            color: #40A7EA
            @media (max-width: 1200px)
                text-align: center
                margin-left: 0
                font-size: 16px
        &-link
            margin-top: 25px
            padding: 12px 26.5px 12px 54.5px
            background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
            border-radius: 10px
            font-weight: 600
            font-size: 16px
            line-height: 1.5
            position: relative
            cursor: pointer
            color: #fff
            @media (max-width: 1200px)
                margin-top: 0
                padding: 12px 15.5px 12px 28.5px
                margin-left: auto
            @media (max-width: 768px)
                margin-top: 12px
                text-align: center
            &::before
                position: absolute
                left: 29.5px
                top: calc( 50% - 9px )
                content: ""
                width: 18px
                height: 18px
                background-image: url(../img/p2p/plus.svg)
                background-repeat: no-repeat
                background-size: contain  
                @media (max-width: 1200px)
                    left: 7.5px
                @media (max-width: 768px)
                    left: calc( 50% - 70px )
        &-label
            font-weight: 500
            font-size: 14px
            line-height: 1.25      
        &-graph  
            width: 299px
            padding: 0 5px
            height: 133px  
            margin-top: 27px   
            border-bottom: 1px solid#89B0FD
            border-radius: 1px
            background: linear-gradient(rgba(64, 167, 234, 0.2) 1px, transparent 0)
            background-size: 100% 34px
            display: flex
            align-items: flex-end
            list-style-type: none
            column-gap: 12px 
            @media (max-width: 1200px)
                width: 100%
            &--month
                column-gap: 4px 
                & .n-p2p-main__stats-column
                    width: 6px
                    @media (max-width: 1200px)
                        width: calc( 3.03% - 1px )
        &-column
            position: relative
            width: 30.5px
            min-height: 11px
            height: 11px
            background: #AFD2F8
            border-radius: 5px 5px 0 0
            transition: height 0.3s linear
            @media (max-width: 1200px)
                width: calc( 14% - 8.5px )
            &:hover
                background: #9FA4E6
                & .n-p2p-main__stats-count
                    opacity: 1
                    z-index: 10
        &-count 
            opacity: 0
            position: absolute
            top: -48px
            background: #1A2C46
            border: 1px solid #9FA4E6
            border-radius: 8px
            font-size: 12px
            line-height: 1.4
            color: #9FA4E6
            text-align: center
            padding: 10px 4px
            min-width: 36px
            z-index: -1
            left: calc( 50% - 18px )
            transition: all .3s linear
            &::before
                width: 24px
                height: 8px
                content: ""
                position: absolute
                bottom: -8px
                left: calc( 50% - 12px )
                background-image: url(../img/p2p/main/triangle-hint.svg)
                background-size: contain
                background-position: center
                background-repeat: no-repeat
        &-dates
            margin-top: 5px
            list-style-type: none  
            display: flex
            justify-content: space-between
            padding: 0 4px
        &-date
            color: #fff
            font-size: 12px
            line-height: 1.25
            min-width: 31px
            text-align: center
            @media (max-width: 1200px)
                text-align: center
                width: calc( 14% - 8.5px )
    &__recent
        grid-area: D
        color: #fff
        height: 205px
        padding: 24px
        position: relative
        @media (max-width: 1200px)
            padding: 12px
            min-height: 181px
        &.empty 
            .n-p2p-main__recent-link
                display: none
        &:last-child
            grid-area: F
        &-head
            display: flex
            justify-content: space-between
        &-title
            font-weight: 600
            font-size: 18px
            line-height: 1.25
        &-link
            font-weight: 600
            font-size: 16px
            line-height: 1.5
            background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
            -webkit-background-clip: text
            -webkit-text-fill-color: transparent
            background-clip: text
            text-fill-color: transparent
            text-align: right
        &-table
            list-style-type: none
            margin-top: 16px
        &-empty
            display: flex
            flex-direction: column
            align-items: center
            justify-content: center
            font-size: 14px
            line-height: 1.3
            color: #fff
            position: absolute
            height: 100%
            width: 100%
            top: 0
            left: 0
        &-row
            display: flex
            align-items: center
            padding-bottom: 4px
            &:not(:first-child)
                padding: 4px 0
                border-top: 1px solid rgba(64, 167, 234, 0.2)
                border-radius: 1px
        &-avatar
            background: #142443
            border-radius: 50%
            width: 33px
            text-align: center
            text-transform: uppercase
            height: 33px
            font-weight: 600
            font-size: 12px
            line-height: 1.25
            padding: 9px 2px
        &-name
            margin-left: 8px
            font-weight: 500
            font-size: 12px
            line-height: 1.17
            width: 33.33%
            max-width: 33.33%
            overflow: hidden
            text-overflow: ellipsis
        &-date
            margin-right: auto
            padding: 0 7px
            text-align: center
            font-weight: 400
            font-size: 12px
            line-height: 1.17
            width: 33.33%
            max-width: 33.33%
        &-summ
            font-weight: 400
            font-size: 12px
            line-height: 1.17
            width: 33.33%
            max-width: 33.33%
            text-align: right
.light .n-p2p-main 
    .n-p2p-main__avatar
        background: #89B0FD
        &.verified
            background: linear-gradient(135deg, #FFAE49 0%, #FF74DB 100%)
    .n-p2p-main__name
        color: #142546
    .n-p2p-main__status
        &.verified 
            color: #142546
    .n-p2p-main__status-text
        color: #142546
    .n-p2p-main__review-name
        color: #40A7EA
    .n-p2p-main__review-count
        color: #142546
    .n-p2p-main__stats
        color: #142546
    .n-p2p-main__stats-date
        color: #142546
    .n-p2p-main__rules-title
        color: #142546
    .n-p2p-main__recent
        color: #142546
    .n-p2p-main__recent-avatar
        background: #89B0FD
        color: #fff
    .n-p2p-main__recent-empty
        color: #142546
    .n-p2p-main__stats-count
        background: #FFFFFF
    


