.n-wallet-table__head
    &--tabs
        display: flex
        justify-content: center
        @media (max-width: 1104px)
          flex-wrap: nowrap
    &-tab
        font-weight: 700
        font-size: 14px
        line-height: 17px
        color: #AFB6BA
        transition: all .3s linear
        margin: 0 12px
        cursor: pointer
        @media (max-width: 1104px)
          text-align: center
        &.active
            color: #40A7EA
.n-wallet-table__codes 
    position: relative
    height: calc( 100% - 47px )
    &-inner
        padding: 12px
        display: none
        &.show
          display: flex
          @media (max-width: 1104px)
            display: block
    &-form
        flex: 0 0 384px
    &-info
        flex: 0 0 366px
        margin-left: auto        
        @media (max-width: 1104px)
          margin-top: 32px
    &-title
      font-weight: 600
      font-size: 16px
      line-height: 20px
      &:not(:first-of-type)
        margin-top: 32px
    &-list
      margin-top: 16px
    &-text
      padding-left: 18px
      font-weight: 400
      font-size: 12px
      line-height: 150%
      position: relative
      &::before
        position: absolute
        content: ""
        width: 3px
        height: 3px
        border-radius: 50%
        left: 7px
        top: 7px
        background: #fff
      &:not(:first-child)
        margin-top: 4px
    &-way
      color: #fff
      position: relative
      margin-top: 4px
      max-width: 384px
      @media (max-width: 1104px)
        max-width: 100%
    &-select
      text-align: left
      position: relative
      &.select-show .select-dropdown 
        display: block
      &.select-show .select-toggle::after
        transform: rotate(180deg)
    & .select-toggle
        position: relative
        top: 0
        left: 0
        background: none
        border: 2px solid #3D5481
        border-radius: 10px
        outline: none
        padding: 10px 16px
        width: 100%
        text-align: left
        font-weight: 400
        font-size: 14px
        line-height: 24px
        transition: all .3s linear
        &::after
            position: absolute
            content: ""
            top: 11px
            right: 16px
            width: 24px
            height: 24px
            background-image: url(../img/wallet/arrow-select.svg)
            background-position: center
            background-repeat: no-repeat
    & .select-dropdown
        padding: 8px 16px
        text-align: left
        flex-direction: column
        z-index: 10
        display: none
        position: absolute
        top: calc( 100% + 4px )
        right: 0
        width: 100%
        max-width: 100%
        height: auto
        box-sizing: border-box
        background: #131D32
        border: 2px solid #3D5481
        box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.16)
        border-radius: 10px
    & .select-option
        cursor: pointer
        padding: 8px 0
        transition: all .3s linear
        &:not(:last-child)
          border-bottom: 1px solid #14223B
    & .select-dropdown-head
      position: relative
      text-align: center
      margin-bottom: 50px
    &-commission 
        max-width: 314px
        margin-top: 12px
        padding-left: 18px
        font-weight: 500
        font-size: 12px
        line-height: 150%
        color: #ED2661
        position: relative
        filter: blur(0)!important
        @media (max-width: 1104px)
          max-width: 100%
        &::before
            position: absolute
            content: ""
            width: 2px
            height: 2px
            border-radius: 50%
            left: 7px
            top: 8px
            background: #ED2661
    &-inputs 
      display: flex
      justify-content: space-between
      margin-top: 4px
      & input
        width: calc( 50% - 6px )
        margin-top: 0
      & button
        width: calc( 50% - 6px )
    &-input
        margin-top: 4px
        padding: 13.5px 16px
        background: #131D32
        border: 2px solid #3D5481
        border-radius: 10px
        width: 100%
        font-weight: 400
        font-size: 14px
        line-height: 17px
        box-sizing: border-box
        color: #fff
        &:focus-visible
          outline: 0
        &.error
          border: 2px solid #FD5454
        &.disabled
          background: #253962
          border: 2px solid #3D5481
    &-btn
        margin-top: 32px
        background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
        border-radius: 10px
        padding: 12px 66.5px 12px 45.5px
        border: none
        outline: 0
        font-weight: 600
        font-size: 16px
        line-height: 150%
        color: #fff
        position: relative
        @media (max-width: 768px)
            display: block
            margin-left: auto
        &:hover
            background: linear-gradient(180deg, rgba(45, 152, 222, 0.7) 0%, rgba(68, 170, 236, 0.7) 100%)
        &::after
            position: absolute
            content: ""
            width: 16px
            height: 16px
            top: calc( 50% - 8px )
            right: 45.5px
            background-image: url(../img/wallet/arrow-btn.svg)
            background-size: cover
            background-position: center
            background-repeat: no-repeat
    &-all 
      border-radius: 10px
      background: transparent
      color: #40A7EA
      border: 2px solid #40A7EA
      outline: 0
    &-hint
        display: none
        position: absolute
        bottom: 32px
        left: calc(50% - 117.5px)
        background: #213151
        border: 1px solid #40A7EA
        border-radius: 8px
        color: #fff
        text-align: center
        font-size: 12px
        padding: 15px 25px
        max-width: 190px
        @media (max-width: 1104px)
            position: fixed
            left: calc(50% - 95px)

.light .n-wallet
  .n-wallet-table__codes-text::before
    background-color: #142546
  .n-wallet-table__codes-input.disabled
    color: #142546
    background: #FCFEFF
    border: 2px solid #DEEAFF
  .select-toggle
    background: #FCFEFF
    border: 2px solid #DEEAFF
    color: #142546
  .n-wallet-table__codes-input
    background: #FCFEFF
    border: 2px solid #DEEAFF
    color: #142546
  .n-wallet-table__codes-input.error
    border: 2px solid #FD5454
  .n-wallet-table__codes-hint
    background: #FFFFFF
    border: 1px solid #40A7EA
    color: #142546