.container
	width: 100%
	padding-right: 16px
	padding-left: 16px
	margin-right: auto
	margin-left: auto
	@media (min-width: 320px)
		overflow: hidden
	@media (min-width: 576px)
		max-width: 540px
	@media (min-width: 768px)
		max-width: 720px
	@media (min-width: 992px)
		max-width: 960px
	@media (min-width: 1200px)
		max-width: 1140px
	@media (min-width: 1400px)
		max-width: 1320px

	.navbar > &
		display: flex
		flex-wrap: inherit
		align-items: center
		justify-content: space-between
