.n-main-start
  background: radial-gradient(50% 50% at 50% 50%, #C3E3F8 0%, #93C3F2 100%)
  padding: 64px 0
  font-family: 'Montserrat', sans-serif
  .container
    display: flex
    flex-direction: column
  &_title
    font-weight: 700
    width: 600px
    margin: 0 auto 24px
    color: #142546
    font-size: 36px
    line-height: 44px
    text-align: center
  &_prompt
    color: #152340
    width: 495px
    text-align: center
    font-size: 16px
    line-height: 24px
    margin: 0 auto 24px
  a
    padding: 12px 16px
    margin: 0 auto
    display: flex
    background: #142546
    border-radius: 10px
    font-weight: 600
    font-size: 16px
    line-height: 24px
    color: #FFFFFF
    transition: .1s
    svg
      margin: auto 0 auto 5px
    &:hover
      background: rgba(20, 36, 67, 0.9)

@media screen and (max-width: 768px)
  .n-main-start
    padding: 32px 0
    &_title
      width: 100%
      font-size: 30px
      line-height: 37px
      margin-bottom: 24px
    &_prompt
      width: 100%
