.popupBtn
  cursor: pointer
.popupBody
  display: none
  position: fixed
  left: 0
  top: 0
  right: 0
  bottom: 0
  background: rgba(19, 32, 51, 0.7)
  z-index: 10000
  &.active
    display: block
  &__content
    position: absolute
    left: 50%
    top: 50%
    transform: translate(-50%, -50%)
    border-radius: 11px
    background: #fff
    padding: 25px
    h3
      font-weight: 300
      font-size: 24px
      line-height: 28px
      color: #132033
.closePopup
  cursor: pointer
