.input-slider
  width: 100%
  height: 2px
  background: #1a2B44
  border: 0!important
  margin: 25px 0 15px
  .ui-slider-range-max
    background: #1A2B44
  .ui-slider-handle
    width: 12px
    height: 12px
    border-radius: 50%
    background: #1A2B44
    border: 0
    margin-left: -6px
    .percent
      position: absolute
      top: -13px
      left: 45%
      color: #1A2B44
      font-size: 10px
      line-height: 13px
      font-family: IBM Plex , sans-serif
      @media screen and (max-width: 1023px)
        left: 50%
        transform: translateX(-60%)

  &_dot
    width: 10px
    height: 10px
    border: 2px solid #ECF4FF
    background: #869FC3
    position: absolute
    border-radius: 50%
    top: -4px
    z-index: 2
    &.active
      background: #1A2B44
    &[data-dot="0"]
      background: #1A2B44
      left: -3px
    &[data-dot="1"]
      left: calc(25% - 5px)
    &[data-dot="2"]
      left: calc(50% - 5px)
    &[data-dot="3"]
      left: calc(75% - 5px)
    &[data-dot="4"]
      right: -3px