.n-market__pair
	grid-column: 3/4
	grid-row: 2/4
	padding: 12px 11px 12px 0
	border-bottom: 4px solid #14223B 
	&-close 
		display: none
	&-blur 
		display: none
	&-title 
		display: none
	&-search
		position: relative
		padding-left: 12px
	&-input
		width: 100%
		padding: 7.5px 12px 7.5px 46px
		font-weight: 400
		font-size: 14px
		line-height: 1.15
		background: transparent
		border: 1px solid #3D5481
		border-radius: 10px
		color: #fff
		&::placeholder
			color: #7993ad
		&:hover, &:focus-visible
			outline: none
			border: 1px solid #40A7EA
	&-btn 
		position: absolute
		content: ""
		width: 17px
		height: 17px
		top: 9px
		left: 27px
		background: url(../img/market/search.svg) center no-repeat
		border: none
		outline: none
	&-tabs 
		margin-top: 9px
		padding-left: 12px
	&-tab
		position: absolute
		z-index: -1
		opacity: 0
		& + label
			cursor: pointer
			transition: all 0.3s ease-in-out
			margin-right: 20px
			font-weight: 500
			font-size: 14px
			line-height: 17px
			color: #7993ad
			&:hover, &:active
				color: #40A7EA
		&:checked + label
				cursor: default
				color: #40A7EA
	&-content 
		display: none
		&.active 
			display: block
	&-inner
		margin-top: 12px
	&-head
		display: flex
		list-style-type: none
		justify-content: space-between
		font-weight: 400
		font-size: 12px
		line-height: 15px
		padding-right: 8px
		padding-left: 35.5px
	&-name
		min-width: 21%
		text-align: center
	&-price
		min-width: 30%
		text-align: right
	&-changes
		text-align: right
		min-width: 33%
	&-body
		font-weight: 500
		font-size: 12px
		line-height: 15px
		padding-right: 8px
		padding-top: 9px         
		height: 330px
		& .n-market__pair-name
			cursor: pointer
			text-align: left
			min-width: 29%
			position: relative         
			z-index: 2
			color: #ffffff
			&:hover
				color: #40A7EA
		& .n-market__pair-price
			position: relative         
			z-index: 2
			color: #ffffff
		& .n-market__pair-changes
			padding-right: 4px
			min-width: 42% 
			position: relative         
			z-index: 2
			color: #1BD079!important
	&-row
		position: relative
		padding: 2px 0 4px
		display: flex         
		list-style-type: none
		padding-left: 35.5px
		display: none
		&.active
			display: flex         
		&:hover, &:active
			background-image: linear-gradient(90deg, #175D8A 0%, #213151 100%)
			background-position:   0 0
		&--red
			& .n-market__pair-changes
				color: #FD5454
	&-favorite
		position: absolute
		z-index: 0
		opacity: 0
		& + label
			cursor: pointer
			z-index: 1
			width: 16px
			height: 20px
			left: 12px
			top: -1px
			position: absolute
			content: ""
			height: 100%
			background-image: url(../img/market/favorite.svg)
			background-repeat: no-repeat
			background-position: center
		&:checked + label
			background-image: url(../img/market/favorite-check.svg)