// 
@mixin flexing($js: stretch, $ai: $js)
	display: flex
	justify-content: $js
	align-items: $ai

@mixin iflexing($js: stretch, $ai: $js)
	display: inline-flex
	justify-content: $js
	align-items: $ai

@mixin colflexing($js: stretch, $ai: $js)
	display: flex
	flex-direction: column
	justify-content: $js
	align-items: $ai

@mixin icolflexing($js: stretch, $ai: $js)
	display: inline-flex
	flex-direction: column
	justify-content: $js
	align-items: $ai

@mixin colflex
	display: flex
	flex-direction: column
