.n-p2p-add
  .n-p2p-add-tab-content > div:last-child
    display: none
  display: block !important
  background: rgba(33, 59, 86, 0.39)
  border: 1px solid #89B0FD
  backdrop-filter: blur(10px)
  border-radius: 20px
  padding: 12px 24px
  overflow: hidden
  input
    outline: none
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button
    -webkit-appearance: none
    margin: 0
  .n-p2p-add__header
    padding-bottom: 16px
    display: flex
    h2
      font-weight: 600
      font-size: 20px
      line-height: 24px
      color: #FFFFFF
      margin: 0 auto
    .n-p2p-add-back
      width: 24px
      height: 24px
      background: #40A7EA
      border-radius: 5px
      display: flex
      justify-content: center
      align-items: center
      border: none
  .n-wallet-header__links
    border: 1px solid #14B467
    margin: 0 auto
    .n-wallet-header__toggle.active
      background: #14B467
    .n-wallet-header__toggle
      text-align: center
  .n-wallet-header__links.active
    border: 1px solid #FD5454
    .n-wallet-header__toggle.active
      background: #FD5454
  .n-p2p-add-p2p-trade__inputs
    margin-top: 24px
    display: grid
    grid-template-columns: 2fr auto 2fr
    grid-column-gap: 18px
    justify-content: space-between
  .n-p2p-add-p2p-trade__inputs-label
    font-weight: 600
    font-size: 16px
    line-height: 20px
    color: #FFFFFF
    padding-bottom: 4px
    position: relative
    cursor: pointer
    height: 24px
    img
      width: 15px
      height: 15px
      margin-left: 6px
  .n-p2p-add-multiplicity
    .n-p2p-add-last-label
      opacity: 0
    .n-p2p-add-last-label.active
      color: #FD5454
      opacity: 1
  .n-p2p-add-p2p-trade__inputs-label:first-child:hover .hint
    visibility: visible
    opacity: 1 !important
    background-color: #213151
    & p
      color: #fff
  .n-p2p-add-last-label
    font-weight: 400
    font-size: 12px
    line-height: 140%
    color: #61759D
    padding-top: 4px
    position: static
    flex: 0 0 100
    max-width: 85%
    display: block
    transition: all .3s ease
  .n-p2p-add-last-label.active
    color: #FD5454
  .n-p2p-add-qty
    .n-p2p-add-qty-input
      background: #2A3B5C
      border: 2px solid #5578BC
      border-radius: 10px
      width: 100%
      height: 48px
      display: flex
      align-items: center
      justify-content: space-between
      padding: 0 20px
      .n-p2p-add-product-qty-container
        font-weight: 900
        font-size: 14px
        line-height: 150%
        color: #FFFFFF
        display: flex
        align-items: center
      .n-p2p-add-product-qty
        background: transparent
        border: none
        font-weight: 600
        font-size: 14px
        line-height: 150%
        color: #FFFFFF
        outline: none 
        max-width: 45px
        text-align: center
      .n-p2p-add-qty-count
        border: none
        background: transparent
        border: 1.5px solid #40A7EA
        width: 16px
        height: 16px
        display: flex
        justify-content: center
        align-items: center
        color: #40A7EA
        border-radius: 2px
  .hint
    position: absolute
    width: 206px
    padding: 10px 8px
    height: 54px
    background: #FFFFFF
    border: 1px solid #40A7EA
    border-radius: 8px
    top: -18px
    left: calc(100% + 13px)
    z-index: 10
    visibility: hidden
    opacity: 0
    transition: visibility 0s, opacity 0.5s linear
    p
      font-weight: 400
      font-size: 12px
      line-height: 140%
      text-align: center
      color: #142546
  .hint img
    position: absolute
    top: 15px
    left: -9px
    z-index: 1000
    margin: 0
    width: 8px
    height: 24px


  .n-p2p-add-qty
    margin-top: -2px
    min-width: 176px
    width: 100%
  .n-p2p-add-limits
    display: flex
    justify-content: space-between
    .n-p2p-add-last-label
      opacity: 0
    .n-p2p-add-last-label.active
      color: #FD5454
      opacity: 1
    .n-p2p-add-limits-item
      max-width: 198px
      display: flex
      flex-direction: column
      justify-content: flex-start
    p
      font-weight: 400
      font-size: 16px
      line-height: 150%
      color: #FFFFFF
      padding: 36px 4px
  .n-p2p-add-input
    position: relative
    width: 198px
    input
      height: 48px
      max-width: 198px
      padding: 15px 44px 15px 16px
      background: #2A3B5C
      border: 2px solid #5578BC
      border-radius: 10px
      font-weight: 600
      font-size: 14px
      line-height: 150%
      color: #FFFFFF
      width: 100%
    input::placeholder
      color: #5C7098
    input.active
      border: 2px solid #FD5454 !important
      color: #FD5454 !important
    span
      font-weight: 600
      font-size: 14px
      line-height: 150%
      color: #FFFFFF
      position: absolute
      right: 0
      padding: 15px 16px
      color: #5C7098
.n-p2p-add__textareas
  display: grid
  grid-template-columns: 2fr 2fr
  grid-column-gap: 20px
  margin-top: 24px
  .n-p2p-add-last-label
    opacity: 0
    padding-top: 0
  .n-p2p-add-last-label.active
    color: #FD5454
    opacity: 1
  .n-p2p-add-textarea__item
    position: relative
    &-counter
      position: absolute
      top: 192px
      right: 16px
      color: #61759D
      font-size: 10px
      line-height: 1.4
  p
    font-weight: 600
    font-size: 16px
    line-height: 20px
    color: #FFFFFF
    padding-bottom: 4px
  textarea
    max-width: 411px
    width: 100%
    height: 190px
    background: #2A3B5C
    border: 2px solid #5578BC
    border-radius: 10px
    padding: 12px 16px 36px 16px 
    font-weight: 400
    font-size: 12px
    line-height: 150%
    color: #FFFFFF
    resize: none
    outline: none
    -ms-overflow-style: none
    overflow: -moz-scrollbars-none
    &::-webkit-scrollbar 
      width: 0

  textarea::placeholder
    color: #AFB6BA
.n-p2p-add-submit-buttons
  display: flex
  justify-content: flex-end
  margin-top: 27px
  button
    padding: 12px 32px
    border-radius: 10px
    font-weight: 600
    font-size: 16px
    line-height: 150%
    margin-right: 20px
    background: transparent
    border: none
    width: 100%
    max-width: 213px
  button:last-child
      margin-right: 0
  &__canel
    border: solid 2px #359EE3 !important
    color: #44AAEC
  &__create
    background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%) !important
    color: #fff
.n-p2p-add-payment-methods
  padding-top: 24px
  .n-p2p-add-last-label
    opacity: 0
  .n-p2p-add-last-label.active
    color: #FD5454
    opacity: 1
  .n-p2p-add-ways
    display: none
    .n-p2p-add-item
      height: 20px
      background: rgba(120, 130, 224, 0.19)
      border-radius: 1px
      align-items: center
      display: flex
      justify-content: center
      margin-right: 4px
      cursor: pointer
      padding: 0 8px
      margin-top: 10px
      p
        font-weight: 400
        font-size: 10px
        line-height: 12px
        text-align: center
        padding: 0
  /*   .n-p2p-add-item:nth-child(1) p
      color: #009E32
    .n-p2p-add-item:nth-child(2) p
      color: #F3BE00
    .n-p2p-add-item:nth-child(3) p
      color: #FF1339 */
  .n-p2p-add-ways.active
    display: flex
    flex-wrap: wrap
  h3
    font-weight: 600
    font-size: 16px
    line-height: 20px
    color: #FFFFFF
  p
    color: #fff
    font-weight: 400
    font-size: 14px
    line-height: 17px
    padding-top: 8px
  button
    min-width: 129px
    padding: 8px 16px
    height: 33px
    background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
    border-radius: 7px
    font-weight: 600
    font-size: 14px
    line-height: 17px
    color: #FFFFFF
    text-align: center
    margin-top: 16px
    border: none
    img
      order: -1
      margin-right: 6px



body.light .n-p2p-add
  background: rgba(237, 250, 255, 0.39)
  .n-p2p-add__header
    h2
      color: #142546
  .n-wallet-header__links
    background: #EEFAFF
    .n-wallet-header__toggle:hover
      color: #AFB6BA
  .n-p2p-add-p2p-trade__inputs-label
    color: #142546
  .n-p2p-add-input
    input
      background: #ECF8FE
      color: #142546
      border: 2px solid #A9C8FF
  .n-p2p-add-last-label
    color: #61759D
  .n-p2p-add-ways
    .n-p2p-add-item
      background: rgba(158, 165, 230, 0.09)
  h3
    color: #142546
  p
    color: #142546
  .n-p2p-add__textareas
    textarea
      background: #ECF8FE
      border: 2px solid #A9C8FF
      color: #142546
  .n-p2p-add-qty
    .n-p2p-add-qty-input
      background: #ECF8FE
      border: 2px solid #A9C8FF
      input
        color: #142546
  .n-p2p-add-input
    span
      color: #142546
  .n-p2p-add-p2p-trade__inputs-label:first-child:hover .hint
    background: #fff
    & p 
      color: #142546

.n-p2p-add-popup__inner
  width: 100%
.n-p2p-add-popup
  padding: 32px
  width: 100% !important
  .simplebar-wrapper
    height: 184px
  .simplebar-content
    height: 184px
  .simplebar-content:before
    content: none
  .simplebar-scrollbar:before
    background: #40A7EA
    border-radius: 100px
    opacity: 1
    width: 4px
  h4
    font-weight: 600
    font-size: 16px
    line-height: 20px
    color: #FFFFFF
    text-align: left
  .n-p2p-add-popular
    position: relative
    .n-p2p-add-checkboks
      padding-top: 12px
      form
        display: flex
        .n-profile-table__checkbox
          margin-top: 0
          margin-right: 24px
  .n-p2p-add-popular:before
    content: ''
    width: 100%
    height: 1px
    background: #4B6393
    border-radius: 10px
    position: absolute
    left: 0
    bottom: -16px
    
  .n-p2p-add-all_ways
    padding-top: 33px
    position: relative
    .n-profile-table__checkbox
      display: none
    .n-profile-table__checkbox.active
      display: block
    .n-p2p-add-all__ways_header
      display: flex
      align-items: center
      padding-bottom: 12px
      justify-content: space-between
      .n-p2p-add-input-box
        position: relative
        height: 32px
        width: 100%

        transition: all 0.6s cubic-bezier(0.58, 0, 0.5, 1)

        &.open
          max-width: 177px
      input
        position: relative
        outline: none
        border: none
        height: 100%
        width: 100%
        font-weight: 400
        font-size: 12px
        line-height: 15px
        color: #AFB6BA
        background: #213151
        border: 1px solid #5578BC
        border-radius: 7px
        padding: 8px 10px 9px 29px

      span
        position: absolute
        top: 0
        left: 0
        padding: 10px 8px 10px 9px
        width: 12px
        height: 12px
        img
          transform: translate(0, -60%)
      .n-p2p-add-search-icon
        position: absolute
        top: 50%
        font-size: 30px
        transition: all 0.6s cubic-bezier(0.58, 0, 0.12, 1)

      .n-p2p-add-search-icon
        color: #5c77ff
        transform: translateY(-50%) rotate(90deg)

      .n-p2p-add-close-icon
        right: -38px
        color: #fff
        opacity: 0
        pointer-events: none
        transform: translateY(-50%)

      .n-p2p-add-input-box.open
        opacity: 1
        pointer-events: auto
    form, .simplebar-content
      display: grid
      grid-template-columns: 50% 50%
      grid-row-gap: 8px
      padding: 1px
      padding-top: 12px
      .n-profile-table__checkbox
        margin-top: 0
        transition: all .3s ease
      .n-profile-table__checkbox.trs
        ::before
          border: 1.5px solid #44526D
        div
          color: #44526D
  .n-popup__controls
    margin-top: 36px
    justify-content: center
    .n-popup__control
      max-width: 188px
      transition: all .3s ease
//  .n-p2p-add-all_ways:before
    content: ''
    position: absolute
    z-index: 10
    bottom: 0
    left: 0
    background: linear-gradient(180deg, transparent 0%, rgba(32, 49, 78, 1) 100%)
    width: 100%
    height: 50px


.n-p2p-add-popup__inner
  @media screen and (max-width: 768px)
    .n-p2p-add-popup
      max-width: 100%
    .n-popup__controls
      display: flex 
      justify-content: space-between
      .n-popup__control
        margin-top: 0
        width: calc( 50% - 4px )
        max-width: initial

  @media screen and (max-width: 650px)
    max-width: 96%

  @media screen and (max-width: 550px)
    .n-p2p-add-popular 
      .n-p2p-add-checkboks 
        form
          .n-profile-table__checkbox
            margin-right: 12px
    .n-p2p-add-popup
      padding: 32px 12px 20px
  @media screen and (max-width: 444px)
    .n-popup__controls
      display: block 
      .n-popup__control
        margin-top: 8px
        width: 100%
        max-width: initial
    
  @media screen and (max-width: 408px)
    .n-p2p-add-popup 
      .n-p2p-add-all_ways 
        .n-p2p-add-all__ways_header
          .n-p2p-add-input-box.open
            max-width: 123px
            height: 32px
    .n-profile-table__label
      padding: 3.5px 0 3.5px 28px
    .n-p2p-add-popup__inner
        max-width: calc(100% - 16px)
    .n-p2p-add-popular 
      .n-p2p-add-checkboks 
        form
          .n-profile-table__checkbox
            margin-right: 10px
    .n-popup__title
      font-size: 28px
      line-height: 34px
    .n-p2p-add-popup 
      h4:first-child
        text-align: center
    
    .n-p2p-add-popup 
      .n-popup__controls
        .n-popup__control
          max-width: 100%
  @media screen and (max-width: 360px)
    .n-profile-table__label
      font-size: 10px
    .n-profile-table__label::before
      width: 20px
      height: 20px
    .n-p2p-add-popular .n-p2p-add-checkboks form .n-profile-table__checkbox:last-child
      margin-right: 0
    .n-p2p-add-popup h4
      font-size: 13px

body.light .n-p2p-add-popup__inner
  .n-p2p-add-popup 
    h4
      color: #142546
    .n-p2p-add-all_ways 
      .n-p2p-add-all__ways_header 
        input
          background: #F4F9F8
          border: 1px solid #E2EEEC
    .n-p2p-add-popular:before
      background: #E2EEEC
    .n-p2p-add-all_ways:before
      background: linear-gradient(180deg, transparent 0%, rgba(255, 255, 255, 1) 100%)
  .simplebar-content 
    .n-profile-table__checkbox.trs 
      div
        color: #AFB6BA
      ::before
        border: 1.5px solid #AFB6BA !important

.n-p2p-add
  @media screen and (max-width: 1200px)
    .n-p2p-add-input
      width: auto
      max-width: 198px
  @media screen and (max-width: 1104px)
    .n-p2p-add__textareas textarea
      max-width: 100%
      padding: 12px 16px 22px 16px
      overflow: scroll
      height: 227px
    .n-p2p-add__textareas .n-p2p-add-textarea__item-counter
      top: 230px
  @media screen and (max-width: 880px)
    .n-p2p-add-p2p-trade__inputs-label
      font-size: 14px
  @media screen and (max-width: 840px)
    .n-p2p-add-p2p-trade__inputs
      grid-template-columns: 2fr 2fr
      max-width: 400px
    .n-p2p-add-multiplicity
      grid-row: 1
    .n-p2p-add-limits
      grid-column: 1/3
      display: grid
      grid-template-columns: 2fr 14.11px 2fr
      .n-p2p-add-last-label
        position: absolute
      .n-p2p-add-last-label.active
        position: static
  @media screen and (max-width: 768px)
    .n-p2p-add__textareas
      grid-template-columns: 2fr
      grid-row-gap: 20px
    .n-p2p-add__textareas p
      font-size: 12px
    .n-p2p-add__textareas textarea
      height: 227px
  @media screen and (max-width: 460px)
    .n-p2p-add-submit-buttons
      flex-direction: column
      button
        max-width: 320px !important
      button:last-child
        margin-top: 20px
    .n-p2p-add-p2p-trade__inputs-label:first-child:hover .hint
      width: 168px
      height: 68px
      top: -24px
      & img
        top: 21px
  @media screen and (max-width: 430px)
    .n-p2p-add-submit-buttons 
      button
        margin-right: 10px
        padding: 8px 12px
    .n-p2p-add-submit-buttons 
      button:last-child
        margin-right: 0
    .n-p2p-add__header 
      h2
        font-size: 15px
    .n-p2p-add-last-label
      font-size: 10px
    .n-p2p-add-p2p-trade__inputs-label
      font-size: 12px
    .n-wallet-header__links
      max-width: 100%

    .n-p2p-add-p2p-trade__inputs
      grid-template-columns: 2fr
    .n-p2p-add-limits
      grid-column: 1/2
      grid-row: 2
    .n-p2p-add-input, .n-p2p-add-qty .n-p2p-add-qty-input
      max-width: 320px
      height: 48px
      input
        max-width: 320px
        height: 48px
    .n-p2p-add-multiplicity
      grid-row: 3


  @media screen and (max-width: 350px)
    .n-p2p-add__header 
      h2
        font-size: 13px
    
@media screen and (max-width: 430px)
  .p2p-add
    padding: 12px 15px
  .simplebar-content
    grid-template-columns: 2fr !important