.n-info
  padding: 16px 20px 32px 
  color: #fff
  font-family: 'Montserrat', sans-serif
  margin-top: -80px
  padding-top: 100px
  background: linear-gradient(180deg, #061429 0%, #162844 100%)
  @media screen and (max-width: 1023px)
    padding-left: 0px
    padding-right: 0px 
  /*
    & .text-half 
      max-width: 419px
      position: relative
    & .text-right
      text-align: right 
      @media screen and (max-width: 575px)
        text-align: center 
        margin-left: -32px
    & .manual-img-1 
      margin-top: -50px
      max-width: 100%
      @media screen and (max-width: 1200px)
        max-width: 100%
        margin-top: -25px
      @media screen and (max-width: 575px)
        margin-top: 0
    & .manual-img-2
      position: absolute
      left: 406px
      top: 25px
      max-width: 411px
      @media screen and (max-width: 1200px)
        max-width: 100%
        position: initial
    & .manual-img-3
      @media screen and (max-width: 1200px)
        max-width: 100%
      @media screen and (max-width: 575px)
        max-width: calc( 100% + 32px )
        margin-left: -32px
  /*
  & .manual-bg 
    position: absolute
    pointer-events: none
    z-index: -1
    &--1
      top: 50px
      left: calc( (100vw - 1194px) / 2 - 157px)
    &--2
      right: calc( (100vw - 1194px) / 2 - 70px)
      top: 22px
    &--3
      left: 0
      top: -80px
      z-index: -2
    &--4
      right: 0
      z-index: -2
  &__title
    text-align: center
    font-weight: 700
    font-size: 36px
    line-height: 1.22
  & .n-search 
    margin: 24px auto 0
    width: 320px
    height: 38px
    background: #09172C
    border: 1px solid #3D5481
    font-size: 12px
    @media screen and (max-width: 360px)
      width: auto
      max-width: 100%
  &__content
    -webkit-transform: translate3d(0,0,0)
    margin-top: 24px
    background: rgba(6, 20, 41, 0.81)
    border: 1px solid #89B0FD
    border-radius: 20px
    padding: 32px
    display: flex
    @media screen and (max-width: 900px)
      display: block
      padding: 0
      margin-top: 16px
      border: none
      background: none
      backdrop-filter: none
  &__article
    width: 100%
    position: absolute
    z-index: -100
    opacity: 0
    display: none
    @media screen and (max-width: 900px)
      margin: 16px 0 0
      padding: 32px 12px
      background: rgba(6, 20, 41, 0.81)
      border: 1px solid #89B0FD
      border-radius: 10px
      width: auto
    &.active
      position: initial
      opacity: 1
      display: block
  &__menu
    flex: 0 0 300px
    padding-right: 20px
    @media screen and (max-width: 900px)
      margin: 0 auto
      max-width: 320px
      padding: 12px 16px
      background: rgba(33, 59, 86, 0.39)
      border: 1px solid #89B0FD
      backdrop-filter: blur(10px)
      -webkit-transform: translate3d(0,0,0)
      border-radius: 10px
      position: relative
      z-index: 100
  &__menu-tabs
    @media screen and (max-width: 900px)
      z-index: -1
      padding: 0
      height: 0
      max-height: 0
      position: absolute
      top: calc( 100% + 4px )
      width: calc( 100% + 2px )
      left: -1px
      overflow: hidden
      opacity: 0
      border: 1px solid #89B0FD
      background: rgba(33, 59, 86, 0.89)
      backdrop-filter: blur(10px)
      border-radius: 10px
  &__menu-title 
    font-weight: 600
    font-size: 16px
    line-height: 1.25
    position: relative
    & span 
      display: none
    @media screen and (max-width: 900px)
      font-size: 0
      & span
        display: block
        font-size: 16px
    &::after
      display: none
      position: absolute
      content: ""
      width: 12px
      height: 12px
      top: 6px
      right: 0
      background: url(../img/info/arrow-down-blue.svg) center no-repeat
      transition: all .3s linear
      @media screen and (max-width: 900px)
        display: block
    &.active + .n-info__menu-tabs
      transition: all .5s ease-in-out
      border: 1px solid #89B0FD
      padding: 13px 16px 13px 0
      opacity: 1
      max-height: 1500px
      z-index: 100
      height: auto
    &.active:after
      transform: rotate(180deg)
  &__menu-tab 
    margin-top: 24px
    font-size: 12px
    font-weight: 500
    line-height: 1.21
    cursor: pointer
    @media screen and (max-width: 900px)
      padding-left: 33px
      &:first-child
        margin-top: 0
    &.active
      font-weight: 600
      color: #89B0FD
      position: relative
      &::before
        position: absolute
        content: ""
        width: 16px
        height: 23px
        border-radius: 0 75% 75% 0
        background: #89B0FD
        left: -32px
        top: -5px
        @media screen and (max-width: 900px)
          left: 0
  &__article-head
    display: flex
    align-items: center
  &__article-img
    margin-right: 12px
    & .light
      display: none
  &__article-name
    font-weight: 600
    font-size: 16px
    line-height: 1.25
  &__article-time
    display: block
    font-size: 12px
    line-height: 1.4
    color: #afb6ba
  &__article-text
    margin-top: 32px
    height: 520px
    position: relative
    @media screen and (max-width: 900px)
      height: 65vh
    &::after 
      z-index: 1
      content: ""
      position: absolute
      left: 0
      bottom: -19px
      width: 100%
      height: 38px
      background: linear-gradient(180deg, transparent,  #08162c 100%)
    &--long
      height: auto
      &::after 
        display: none
    &--manual 
      height: auto
      & div
        max-width: 720px
    & .simplebar-scrollbar.simplebar-visible::before 
      opacity: 1
      -webkit-border-radius: 100px
      border-radius: 100px
    & .simplebar-track 
      position: absolute
      padding: 0
    & .simplebar-track.simplebar-vertical 
      width: 7px
      top: -5px
      right: -6px
      pointer-events: auto
    & .simplebar-scrollbar:before
      background: #40A7EA
    & .simplebar-track.simplebar-vertical
      height: calc( 100% - 20px )
      &::before
        display: none
    & .simplebar-track.simplebar-vertical .simplebar-scrollbar:before
        top: 2px
        bottom: 2px
        opacity: 1
        -webkit-border-radius: 100px
        border-radius: 100px
        height: 100%
        cursor: pointer
        @media screen and (max-width: 900px)
          left: 5px
    & .simplebar-wrapper , & .simplebar-height-auto-observer-wrapper, & .simplebar-height-auto-observer, & .simplebar-mask, & .simplebar-offset, & .simplebar-content-wrapper, & .simplebar-placeholder, & .simplebar-content
      margin: 0
      padding: 0
      &::before
        display: none
    & .simplebar-mask, .simplebar-offset
      position: absolute
    & div
      padding-left: 29px
      position: relative
      padding-bottom: 16px
      @media screen and (max-width: 900px)
        padding-left: 24px
        padding-right: 4px
      &::before 
        content: ""
        position: absolute    
        left: 5px
        width: 3px
        bottom: 0
        height: calc( 100% - 24px )  
        width: 2px
        background: rgba(64, 167, 234, 0.2)
        border-radius: 100px
        @media screen and (max-width: 900px)
          left: 8px
      &:not(:first-child)
        & h4 
          margin-top: 4px
      &:last-child::before
        height: calc( 100% - 70px )
        bottom: auto
        top: 24px
    & h4 
      font-weight: 600
      font-size: 16px
      line-height: 1.25
      margin-bottom: 16px
      position: relative
      &::before
        content: ""
        position: absolute
        left: -29px
        top: 3px
        width: 12px
        height: 12px
        background: #40A7EA
        border-radius: 50%
        @media screen and (max-width: 900px)
          left: -21px
      &::after
        content: ""
        position: absolute
        left: -25px
        top: 7px
        width: 4px
        height: 4px
        background: #fff
        border-radius: 50%
        @media screen and (max-width: 900px)
          left: -17px
    & h5
      font-weight: 700
      font-size: 12px
      line-height: 1.5
      margin-bottom: 6px
      &:first-of-type
        margin-bottom: 12px
    & p
      font-size: 12px
      font-weight: 500
      line-height: 1.5
      margin-bottom: 12px
      &:last-child
        margin-bottom: 0
    & b
      font-weight: 600
    & span
      background-color: #40A7EA
      font-weight: 500
    & a
      color: #40A7EA
      font-weight: 500
      & b 
        color: #fff
        font-weight: 700
    & ol
      margin-bottom: 12px
      font-size: 12px
      font-weight: 500
      line-height: 1.5
    & ul
      list-style-type: none
      margin-bottom: 12px
    & ul li
      padding-left: 24px
      position: relative
      font-size: 12px
      font-weight: 500
      line-height: 1.5
      margin-bottom: 6px
      &:last-child
        margin-bottom: 0
      &::before
        content: ""
        position: absolute
        width: 2px
        height: 2px
        border-radius: 50%
        background-color: #fff
        top: 7px
        left: 12px
  & .video-holder 
    overflow: hidden
    z-index: 1
    background: #192C45
    border: 1px solid #40A7EA
    border-radius: 20px
  & .video-block 
    padding-left: 0
    max-width: 100%
    &::before
      display: none
    & div
      padding: 0
      max-width: 100%
      &::before
         display: none
    video
      display: block
      margin: 0 auto
    & span
       background-color: transparent 
    & h4
      text-align: center
      margin-top: 7px
      padding-bottom: 48px
      margin-bottom: 0
      @media screen and (max-width: 900px)
        font-size: 12px
        padding-bottom: 23px
      &::before
        display: none
      &::after
        background-image: url('../img/info/arrows.svg')
        width: 24px
        height: 24px
        background-position: center
        background-size: cover
        bottom: 12px
        top: auto
        left: 50%
        background-color: transparent
        @media screen and (max-width: 900px)
          background-image: url('../img/info/arrows-small.svg')
          width: 10px
          height: 10px
          bottom: 8px
.body-wrapper.light .n-info
  background: linear-gradient(99.76deg, rgba(210, 228, 255, 0.5) 32.17%, rgba(135, 190, 241, 0.5) 96.32%)
  color: #142546
  & .n-search 
    background: #D0E5FA
    border: 1px solid #89B0FD
  & .n-info__article-img img
    display: none
    &.light
      display: block
  .n-info__content
    background: rgba(255, 255, 255, 0.5)
    border: 1px solid #89B0FD
    border-radius: 20px
    @media screen and (max-width: 900px)
      background: none
      border: none
      backdrop-filter: none
      border-radius: none
  .n-info__menu
    @media screen and (max-width: 900px)
      background: rgba(237, 250, 255, 0.39)
  .n-info__menu-tabs
    @media screen and (max-width: 900px)
      background: rgba(237, 250, 255, 0.89)
  .n-info__article
    @media screen and (max-width: 900px)
      background: rgba(255, 255, 255, 0.5)
  .n-info__article-text::after
    background: linear-gradient(rgba(255, 255, 255, 0) 0%, #e8f5fc 100%)
  .simplebar-scrollbar:before
    background: #40A7EA
  .n-info__article-text ul li::before
    background-color: #142546
  .n-info__article-text a b
    color: #142546
  .n-info__article-text h4::after
    background: #09172C