.n-login 
    font-family: 'Montserrat', sans-serif
    margin-top: -80px
    flex: 1
    height: 100%
    display: flex
    &-inner 
        display: grid
        grid-template-columns: 1fr 1fr
        grid-template-rows: 1fr
        width: 100%
        align-items: center
        min-height: 1000px
        @media screen and (max-width: 1200px)
            min-height: 100vh
        @media screen and (max-width: 1023px)
            display: flex
            min-height: auto
    &-slider
        max-width: 100%
        padding: 110px 0
        @media screen and (max-width: 1200px)
            padding: 50px 0
        @media screen and (max-width: 1023px)
            display: none
        & .swiper-pagination
            bottom: 44px
            z-index: 10
            @media screen and (max-width: 1200px)
                bottom: 0
        & .swiper-pagination-bullet
            pointer-events: all
            width: 10px
            height: 10px
            cursor: pointer
            margin: 0 
            background: transparent
            border: 1px solid #4B72A8
        & .swiper-pagination-bullet-active
            background: rgba(255, 255, 255, 0.92)
    &-banner
        align-self: stretch
        display: flex
        flex-direction: column
        align-items: center
        justify-content: center
        &__text
            max-width: 639px
            padding-left: 60px
            padding-right: 15px
            font-weight: 500
            font-size: 41px
            line-height: 1.34
            letter-spacing: -0.015em
            padding-bottom: 40px
            position: relative
            color: #fff
            @media screen and (max-width: 1200px)
                font-size: 36px
            &::after
                position: absolute
                content: ''
                width: 136px
                height: 10px
                bottom: 0
                left: 60px
                background-color: #fff
            & b 
                font-weight: 700
            & span
                font-size: 59px
                line-height: 54px
                color: #FFCB28
                font-weight: 700
                @media screen and (max-width: 1200px)
                    font-size: 45px
        &__img
            margin-top: 64px
            & img 
                max-width: 100%
                max-height: 500px
                @media screen and (max-width: 1200px)
                    max-height: 300px
    &-form
        display: flex
        flex-direction: column
        align-items: center
        justify-content: center
        align-self: stretch
        background-color: #fff
        padding: 80px 0 
        @media screen and (max-width: 1023px)
            justify-content: start
            width: 100%
            padding: 120px 15px 40px
        &--result
            display: none
            &.active
                display: flex
    &__title
        max-width: 380px
        font-weight: 600
        font-size: 36px
        line-height: 1.2
        text-align: center
        color: #061429
    &__subtitle
        margin-top: 4px
        font-weight: 600
        font-size: 16px
        line-height: 1.5
        text-align: center
        color: rgba(6, 20, 41, 0.5)
        max-width: 380px
        &--center 
            text-align: left
    &-block
        margin-top: 15px
        width: 380px
        position: relative
        @media screen and (max-width: 575px)
            width: 100%
        &:first-child
            margin-top: 32px
    &__send
        padding: 10px 4px
        position: absolute
        top: 35px
        right: 8px
        z-index: 2
        border: 0
        outline: 0
        background-color: #fff
        font-weight: 600
        font-size: 16px
        line-height: 1.5
        color: #061429
        &.active 
            color: rgba(6, 20, 41, 0.5)
            font-weight: 400
    &__text 
        margin-bottom: 8px
        font-weight: 600
        font-size: 16px
        line-height: 1.5
        color: #061429
    &__hint 
        position: absolute
        top: -67px
        right: 0
        background: #14191C
        border-radius: 7px
        padding: 11px
        font-weight: 500
        font-size: 12px
        line-height: 1.2
        color: #fff
        z-index: 2
        opacity: 0
        pointer-events: none
        &::before
            content: ""
            position: absolute
            width: 32px
            height: 24px
            bottom: -20px
            left: calc( 50% - 16px )
            background: url(../img/login/hint-triangle.svg) center no-repeat
        & li 
            position: relative
            padding-left: 15px
            &::before
                content: ""
                left: 7px
                top: calc( 50% - 1px )
                position: absolute
                width: 2px
                height: 2px
                border-radius: 50%
                background-color: #fff
    &__input
        padding: 12px
        width: 100%
        border: 1px solid rgba(6, 20, 41, 0.1)
        filter: drop-shadow(0px 1px 1px rgba(255, 255, 255, 0.06))
        border-radius: 8px
        font-weight: 400
        font-size: 16px
        line-height: 150%
        color: #061429
        &--semibold
            font-weight: 600
        &.error 
            border: 1px solid #FE3737
            & + .n-login__error
                max-height: 100px
                height: auto
                margin-top: 8px
        &::placeholder
            color: rgba(6, 20, 41, 0.5)
        &:focus-visible
            border: 1px solid rgba(6, 20, 41, 1)
            & + * + .n-login__hint
                transition: all .3s linear
                opacity: 1
            & + .n-login__hint
                transition: all .3s linear
                opacity: 1
    &__error 
        height: 0
        max-height: 0
        transition: all .3s ease-in-out
        font-weight: 600
        font-size: 10px
        line-height: 1.5
        color: #FE3737
        overflow: hidden
        margin-top: 0
    &__label 
        font-weight: 600
        font-size: 12px
        line-height: 1.5
        position: relative
        padding-left: 24px
        cursor: pointer
        & a 
            color: #40A7EA
        &.error::before
            border: 1px solid #FD5454
        &.check::before
            border: 1px solid #13C244
            background-color: transparent
            box-shadow: none
            background-image: url(../img/login/check.svg)
            background-repeat: no-repeat
            background-position: center
        &::before
            position: absolute
            content: ""
            width: 16px
            height: 16px
            background: rgba(6, 20, 41, 0.1)
            border: 1px solid rgba(6, 20, 41, 0.1)
            box-shadow: 0px 1px 1px rgba(255, 255, 255, 0.06)
            border-radius: 3px
            left: 0
            top: calc( 50% - 8px )
        &--text
            padding-left: 0
            &::before
                display: none
    &__reset
        text-align: left
        font-weight: 600
        font-size: 12px
        line-height: 1.5
        color: #061429
        &:hover 
            color: #061429
    &__reCaptcha
        background: #B0B0B0
        border-radius: 7px
        font-weight: 600
        font-size: 16px
        line-height: 150%
        min-height: 80px 
        text-align: center
        padding: 28px
        color: #fff
    &__button
        width: 100%
        background: #1A3D71
        box-shadow: 0px 1px 1px rgba(255, 255, 255, 0.06)
        border-radius: 8px
        font-weight: 600
        font-size: 16px
        line-height: 1.5
        color: #FFFFFF
        border: 0
        outline: 0
        padding: 12px
            