.n-profile .n-profile-table
    &__reset 
        margin-left: auto
        color: #40A7EA
        font-size: 12px 
        line-height: 1.4
        @media (max-width: 1104px)
            margin-left: 0
            margin-top: 8px
.n-profile-table__security
    padding: 12px 12px 0
    color: #fff
    &-title
        font-weight: 600
        font-size: 16px
        line-height: 20px
        &:not(:first-child)
            margin-top: 32px
    &-list
        margin-top: 8px
        display: block
    &-item
        padding: 16px
        display: grid
        grid-template-columns: 44px 55% 165px auto
        align-items: center
        background: #213151
        border-radius: 10px
        &:not(:first-of-type)
            margin-top: 6px
        @media (max-width: 1104px)
            min-height: 131px
            padding: 12px
            grid-template-columns: 44px 1fr 123px
            grid-template-rows: auto auto
    &-img
        padding: 5.5px 8px
        background: #3D5481
        border-radius: 8px
        margin-right: 12px
        @media (max-width: 1104px)
            align-self: flex-start
            grid-column: 1/2
            grid-row: 1/2
    &-text
        @media (max-width: 1104px)
            align-self: flex-start
            grid-column: 2/4
            grid-row: 1/2
    &-name
        font-size: 14px
        line-height: 17px
    &-description
        margin-top: 4px
        font-size: 12px
        line-height: 15px
        color: #AFB6BA
        & a 
            color: #40a7ea
        &--green
            color: #1BD079
    &-info
        margin-top: auto
        padding: 8px 39px 8px 47px
        background: rgba(27, 208, 121, 0.07)
        border-radius: 100px
        font-size: 12px
        line-height: 15px
        text-align: center
        color: #1BD079
        position: relative
        @media (max-width: 1104px)
            max-width: 165px
            grid-column: 1/3
            grid-row: 2/3
        &::before
            position: absolute
            content: ""
            width: 16px
            height: 16px
            left: 33px
            top: calc(50% - 7px)
            background-image: url(../img/profile/check-green.svg) 
            background-repeat: no-repeat
            background-position: center
        &--error
            padding: 8px 19px 8px 32px
            color: #FD5454
            background: rgba(253, 84, 84, 0.12)
            &::before
                left: 20px
                background-image: url(../img/profile/focus-red.svg) 
    &-link
        display: block
        margin-left: auto
        color: #3aa3e6
        font-weight: 600
        font-size: 14px
        line-height: 1.22
        padding: 7px 10px
        border: 1px solid #44AAEC
        border-radius: 7px
        background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
        -webkit-background-clip: text
        -webkit-text-fill-color: transparent
        background-clip: text
        text-fill-color: transparent
        transition: all 0.3s linear
        cursor: pointer
        &:hover, &:active 
            text-fill-color: initial
            -webkit-text-fill-color: initial
            color: #fff
            background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
        @media (max-width: 1104px)
            margin-top: auto
    &-controls
        margin-left: auto
        @media (max-width: 1104px)
            margin-top: auto
            grid-column: 3/4
            grid-row: 2/3
    &-edit
        border: none
        outline: none
        background-color: transparent
        font-size: 0
        height: 24px
        width: 24px
        background: url(../img/profile/api-edit.svg) center no-repeat
        background-size: cover
    &-del
        margin-left: 12px
        border: none
        outline: none
        background-color: transparent
        font-size: 0
        height: 24px
        width: 24px
        background: url(../img/profile/api-del.svg) center no-repeat
        background-size: cover
    &-active
        margin-top: 32px
        display: flex
        justify-content: space-between
        @media (max-width: 1104px)
            display: block
    &-disable
        font-size: 12px
        line-height: 1.4
        color: #fff
        & a
            color: #40a7ea
        @media (max-width: 1104px)
            margin-top: 8px
            & a
                display: block
    &-table 
        margin-top: 16px
        & .n-table__cell
            height: 47px
        &::before, &::after
            height: 43px
        & .n-table__value--red
            color: #FD5454
            font-weight: 600
        & .n-table__value--green
            color: #1BD079
            font-weight: 600
        & .n-table__body
            @media (max-width: 1104px)
                position: relative
                width: calc(100% + 24px)
                left: -12px
    & .n-pages
        margin-top: 28px
.light .n-profile .n-profile-table__security
    color: #142546
    & .n-profile-table__security-item
        background: #FCFEFF
    & .n-profile-table__security-img
        background: #DEEAFF
    & .n-profile-table__security-disable
        color: #142546