.n-header__theme-toggler--404
  position: fixed
  top: 5px
  right: 5px
  z-index: 100
.n-error-page
  overflow: hidden
  background: #15223B
  position: relative
  font-family: 'Inter'
  text-align: center
  margin: 70px 0 0
  padding: 70px 24px 116px
  display: flex
  flex-direction: column
  color: #fff
  @media screen and (max-width: 767px)
    margin: 0
  @media screen and (max-width: 575px)
    padding: 36px 12px
    justify-content: center
  &__img
    position: absolute
    opacity: 0.5
    @media screen and (max-width: 767px)
      display: none
    &--1
      top: 0
      left: 0
    &--2
      top: 40px
      right: 0
    &--3
      left: 50px
      bottom: 20vh
    &--4
      bottom: -10px
      right: 40px
  &__title
    font-weight: 700
    font-size: 180px
    line-height: 1.2
    letter-spacing: -0.5px
    opacity: 0.9
    @media screen and (max-width: 575px)
      font-size: 110px
  &__subtitle
    margin-top: 30px
    font-weight: 700
    font-size: 48px
    line-height: 1.2
    letter-spacing: 0.25px
    opacity: 0.9
    @media screen and (max-width: 575px)
      margin-top: 16px
      font-size: 28px
  &__text
    margin: 33px auto 0
    max-width: 540px
    font-weight: 400
    font-size: 20px
    line-height: 28px
    opacity: 0.75
    @media screen and (max-width: 575px)
      margin: 15px auto 0
  &__buttons
    display: flex
    margin-top: 40px
    justify-content: center
    list-style-type: none
    @media screen and (max-width: 575px)
      margin-top: 24px
      flex-direction: column
      row-gap: 16px
  &__button 
    cursor: pointer
    font-weight: 700
    font-size: 20px
    line-height: 1.2
    background: #FFFFFF
    border-radius: 32px
    color: #000
    border: 1px solid transparent
    outline: 0
    @media screen and (max-width: 575px)
      width: 100%
    &:hover
      color: #fff
      background: linear-gradient(224.77deg, #40A7EA 17.06%, #294F96 87.23%)
    & a 
      padding: 20px 48px
      display: block
      border: 0
      outline: 0
    &:not(:first-child)
      margin-left: 24px
      @media screen and (max-width: 575px)
        margin-left: 0
  &__nav
    margin-top: 115px
    @media screen and (max-width: 575px)
      margin-top: 30px
  &__links
    display: flex
    flex-wrap: wrap
    justify-content: center
    list-style-type: none
    column-gap: 43px
    row-gap: 30px
    @media screen and (max-width: 575px)
      column-gap: 20px
      row-gap: 25px
  &__link
    cursor: pointer
    font-weight: 500
    font-size: 18px
    line-height: 1.2
    border: 0
    outline: 0
    position: relative
    @media screen and (max-width: 575px)
      font-size: 14px
    &::after
      display: block
      position: absolute
      content: ""
      width: 0
      max-width: 0
      height: 1px
      left: 0
      bottom: -2px
      background-color: #398ACE
      transition: all .3s linear
    &:hover
      color: #398ACE
      &::after
        max-width: 300px
        width: 100%
.light .n-error-page
  background: linear-gradient(0deg, #BDE1F7 0, #EAF7F9 100%)
  color: #15223B
  & .n-error-page__button
    border: 1px solid #3270B5