.n-p2p
   background-color: #061429
   font-family: 'Montserrat', sans-serif
   padding-bottom: 32px
   margin-top: -72px
   padding-top: 72px
   & ul
      list-style-type: none
   &-inner
      display: flex
      @media (max-width: 1104px)
         display: block
   &-block
      background: rgba(33, 59, 86, 0.39)
      border: 1px solid #89B0FD
      /* backdrop-filter: blur(10px) */
      border-radius: 20px
      @media (max-width: 1104px)
         border-radius: 10px
      @media (max-width: 768px)
         &:not(:first-child)
            margin-top: 20px
   &-content
      @media (max-width: 1200px)
         padding: 0 20px
      @media (max-width: 1024px)
         & .container 
            padding: 0
      @media (max-width: 768px)
         padding: 0 16px
         & .container 
            overflow: visible
   & .n-p2p-table
      max-width: 100%
      overflow: hidden
      @media (max-width: 900px)
         margin-top: 12px
         position: relative
         // left: -20px
         // width: calc(100% + 40px)
         width: 100%
   &-color-sber
      color: #68FA96
   &-color-tink
      color: #FFE176
   &-color-alpha
      color: #FF7E93
   &-color-cash
      color: #FFFFFF
   &-color-rosselhoz
      color: #ABFF69
   &-color-gazprom
      color: #5ACAED
   &-color-sbp
      color: #A7FFB0
   &-color-uralsib
      color: #FFFFFF
   &-color-vtb
      color: #8BBAFF
   &-color-raiffaizen
      color: #FFFA7E
   &-color-otkrytie
      color: #5ACAED
   &-color-mkb
      color: #FF8493
   &-color-rosbank
      color: #FFFFFF
.light
   .n-p2p
      background-color: #ffffff
   .n-p2p-block
      background: rgba(237, 250, 255, 0.39)
      border: 1px solid #89B0FD
   .n-p2p-color-sber
      color: #009E32
   .n-p2p-color-tink
      color: #C09C1B
   .n-p2p-color-alpha
      color: #ED1616
   .n-p2p-color-cash
      color: #000000
   .n-p2p-color-rosselhoz
      color: #67B604
   .n-p2p-color-gazprom
      color: #1380FF
   .n-p2p-color-sbp
      color: #3BC649
   .n-p2p-color-uralsib
      color: #000000
   .n-p2p-color-vtb
      color: #0070F3
   .n-p2p-color-raiffaizen
      color: #CEAC33
   .n-p2p-color-otkrytie
      color: #1380FF
   .n-p2p-color-mkb
      color: #FF1339
   .n-p2p-color-rosbank
