.n-wallet 
  & .container
    overflow-y: visible
    .n-table_none
      position: relative
      & .n-table__no-contect
        top: 75%
        @media (max-width: 1104px)
            top: 45%
      & .n-table__blok
        height: 120px
    .n-table
      overflow: hidden
    .n-search__inp 
      height: 34px
    .n-search
      min-width: 271px
    .n-menu__list
      padding-top: 9px
    .n-pages
      margin-top: 16px
    .n-toolbar__list.disabled
      opacity: 0.15
      cursor: auto
      pointer-events: none
    .n-toolbar__cards.disabled
      opacity: 0.15
      cursor: auto
      pointer-events: none
    .n-toolbar__favorite.disabled
      opacity: 0.15
      cursor: auto
      pointer-events: none
    .n-toolbar__balance.disabled
      opacity: 0.15
      cursor: auto
      pointer-events: none
    .n-toolbar .n-search.disabled
      opacity: 0.15
      cursor: default
      position: relative
      z-index: 0
      & button
        cursor: default
      &::after
        position: absolute
        content: ""
        width: 100%
        height: 100%
        top: 0
        left: 0
        z-index: 5
        pointer-events: none
        cursor: default
    .n-toolbar .n-filter.disabled
      opacity: 0.15
      cursor: auto
      pointer-events: none
.n-wallet .n-profile-table
    & .container.main
        @media (max-width: 1104px)
            padding: 0
.n-wallet-table__main
  padding: 12px 7px 
  height: calc(100% - 47px)
.n-wallet
  font-family: 'Montserrat', sans-serif
  & img 
    max-width: 100%
    padding: 0
    margin: 0
  & ul, & ol 
    list-style-type: none
  & .main 
    grid-auto-rows: auto 822px
  & .n-table 
    margin-bottom: 0
    margin-top: 8px
    @media (max-width: 1104px)
      padding: 0
      height: auto
      margin-top: 0
  &-header
    margin-top: 20px
    text-align: center
    color: #ffffff
  &__title
    font-weight: 700
    font-size: 36px 
    @media (max-width: 1104px)
      font-size: 30px 
  &__subtitle
    font-weight: 500
    margin-top: 8px
    font-size: 14px
    line-height: 1.2
  &__total
    display: flex
    justify-content: center
    .hide
      filter: blur(5px)
  &__count
    font-weight: 500
    margin-top: 8px
    font-size: 12px
    line-height: 1.2
    & span 
      color: #AFB6BA
  &__hide 
    margin-left: 4px
  &-table 
    margin-top: 20px
    color: #fff
    & .container.main
        @media (max-width: 1104px)
            padding: 0
    &__head
        display: flex
        align-items: center
        justify-content: space-between
        padding: 7px 24px
        height: 47px
        overflow: hidden
        background: #213151
        box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05)
        border-radius: 14px 14px 0px 0px
        @media (max-width: 1104px)
            flex-wrap: wrap
            border-radius: 0px
            height: auto
            padding: 14px 20px
        &::before, &::after
          content: ''
          position: absolute
          top: 0
          width: 18px
          height: 47px
          background-color: #213151
        &::before
            left: -0
            border-top-left-radius: 14px
            @media (max-width: 1104px)
                display: none
        &::after
            right: -0
            border-top-right-radius: 14px
            @media (max-width: 1104px)
                display: none
        &-text 
          font-weight: 700
          font-size: 14px
          line-height: 17px
          text-align: center
          color: #40A7EA
          max-width: 105px
        &--main
          min-height: 49px
          display: grid
          grid-template-columns: repeat(3, 1fr)    
          align-items: center
          justify-items: center
          padding-left: 182px
          padding-right: 210px
          &::before, &::after
            min-height: 49px
          @media (max-width: 1104px)
              display: none
    &__title 
      font-weight: 700
      font-size: 14px
      line-height: 17px
      color: #40A7EA
      &:not(:first-of-type)
        margin-top: 24px
    &__list
      margin-top: 8px
      @media (max-width: 1104px)
        display: grid
        gap: 6px
    &__list-item
      background: #213151
      border-radius: 10px
      padding: 11.5px
      display: grid
      grid-template-columns: 24px 140px 1fr 1fr 1fr max-content max-content 16px
      align-items: center
      font-size: 14px
      line-height: 17px
      @media (max-width: 1104px)
        position: relative
        padding: 8px 8px 12px 11px
        margin-top: 0
        grid-template-columns: 24px auto auto
        grid-template-rows: repeat(5,auto)
      &:not(:first-of-type)
        margin-top: 6px
      &.zero
        display: none
      &.unfavorite
        display: none
      &--soon
        background: rgba(33, 49, 81, 0.41)
        border: 1px dashed rgba(64, 167, 234, 0.2)
        border-radius: 10px
        font-weight: 400
        font-size: 12px
        line-height: 23.5px
        text-align: center
        color: #8D94A1
        display: block
        text-align: center
        height: 47px
        @media (max-width: 1104px)
          min-height: 217px
          display: flex
          flex-direction: column
          justify-content: center
    &__list-img
      @media (max-width: 1104px)
        grid-column: 1/2
        grid-row: 1/3
    &__list-name
      margin-left: 8px
      margin-right: 15px
      @media (max-width: 1104px)
        margin-right: 0
        grid-column: 2/4
        grid-row: 1/3
        font-size: 14px
    &__list-summ
      text-align: right
      padding-right: 40px
      @media (max-width: 1104px)
        margin-top: 4px
        margin-left: 8px
        color: #8D94A1
        grid-column: 2/4
        grid-row: 2/3
        text-align: left
    &__list-balance
      text-align: center
      padding: 0 10px
      @media (max-width: 1104px)
        font-weight: 600
        margin-top: 8px
        padding-top: 16px
        padding-left: 0
        border-top: 1px solid #131D32
        grid-column: 1/4
        grid-row: 3/4
        text-align: left
      & .cards-text
        display: none
        @media (max-width: 1104px)
          display: block
          font-weight: 700
          font-size: 14px
          line-height: 17px
          color: #40A7EA
          margin-bottom: 4px
    &__list-blocked
      text-align: center
      padding: 0 10px 0 25px
      @media (max-width: 1104px)
        font-weight: 600
        margin-top: 12px
        grid-column: 1/4
        grid-row: 4/5
        text-align: left
        padding-left: 15px
      & .cards-text
        display: none
        @media (max-width: 1104px)
          display: block
          font-weight: 700
          font-size: 14px
          line-height: 17px
          color: #40A7EA
          margin-bottom: 4px
          margin-left: -15px
      & span
        position: relative
        &::before
          position: absolute
          content: ""
          width: 11px
          height: 14px
          top: 1px
          left: -15px
          background: url(../img/wallet/lock.svg) center no-repeat
      & p
        display: inline-block
        position: relative
        &::before
          position: absolute
          content: ""
          width: 11px
          height: 14px
          top: 1px
          left: -15px
          background: url(../img/wallet/lock.svg) center no-repeat
    &__list-credit
      text-align: center
      padding: 0 10px
      & .cards-text
        display: none
        @media (max-width: 1104px)
          display: block
          font-weight: 700
          font-size: 14px
          line-height: 17px
          color: #40A7EA
          margin-bottom: 4px
      @media (max-width: 1104px)
        font-weight: 600
        margin-top: 12px
        padding-bottom: 16px
        border-bottom: 1px solid #131D32
        grid-column: 1/4
        grid-row: 5/6
        text-align: left
        padding-left: 0
    &__list-deposit
        margin-right: 16px
        font-weight: 400
        font-size: 14px
        line-height: 17px
        text-align: right
        text-decoration: none
        color: #40A7EA
        transition: color .3s linear
        cursor: pointer
        &:hover,&:active 
          color: #40A7EA
          text-decoration: underline
        @media (max-width: 1104px)
          grid-column: 1/3
          grid-row: 6/7
          margin-top: 8px
          text-align: left
    &__list-take
        margin-right: 16px
        font-size: 14px
        line-height: 17px
        text-align: right
        text-decoration: none
        color: #40A7EA
        transition: color .3s linear
        cursor: pointer
        &:hover,&:active 
          color: #40A7EA
          text-decoration: underline
        @media (max-width: 1104px)
          grid-column: 3/4
          grid-row: 6/7
          margin-top: 8px
          text-align: right
          margin-right: 0
    &__list-favorite
        width: 16px
        height: 16px
        background: url(../img/wallet/favorite-small.svg) center no-repeat
        position: relative
        cursor: pointer
        @media (max-width: 1104px)
          position: absolute
          top: 8px
          right: 8px
        &.check 
          background: url(../img/wallet/favorite-check.svg) center no-repeat
.n-wallet-table .n-table.cards
    .n-wallet-table
      &__head-text
        display: none
      &__list
        display: grid
        grid-template-columns: repeat(4, 1fr)
        gap: 6px
        @media (max-width: 1104px)
          display: grid
          gap: 6px
          grid-template-columns: 1fr
      &__list-item
        position: relative
        padding: 8px 8px 12px 11px
        margin-top: 0
        grid-template-columns: 24px auto auto
        grid-template-rows: repeat(5,auto)
        &--soon
          min-height: 217px
          display: flex
          flex-direction: column
          justify-content: center
      &__list-img
        grid-column: 1/2
        grid-row: 1/3
      &__list-name
        margin-right: 0
        grid-column: 2/4
        grid-row: 1/2
        font-size: 14px
      &__list-summ
        margin-left: 8px
        margin-top: 4px
        color: #8D94A1
        grid-column: 2/4
        grid-row: 2/3
      &__list-balance
        margin-top: 8px
        padding-top: 16px
        border-top: 1px solid #131D32
        grid-column: 1/4
        grid-row: 3/4
        text-align: left
        & .cards-text
          display: block
          font-weight: 700
          font-size: 14px
          line-height: 17px
          color: #40A7EA
          margin-bottom: 4px
      &__list-blocked
        margin-top: 12px
        grid-column: 1/4
        grid-row: 4/5
        text-align: left
        padding-left: 15px
        padding-bottom: 16px
        border-bottom: 1px solid #131D32
        & .cards-text
          display: block
          font-weight: 700
          font-size: 14px
          line-height: 17px
          color: #40A7EA
          margin-bottom: 4px
          margin-left: -15px
      &__list-deposit
        grid-column: 1/3
        grid-row: 5/6
        margin-top: 8px
        text-align: left
      &__list-take
        grid-column: 3/4
        grid-row: 5/6
        margin-top: 8px
        text-align: right
        margin-right: 0
      &__list-favorite
        position: absolute
        top: 8px
        right: 8px
.light .n-profile
  .n-profile-header
    color: #142546
.light .n-wallet
  .n-wallet-header
    color: #142546
  .n-menu .n-menu__list
    background: #FCFEFF
  .n-wallet-table
    color: #142546
  .n-wallet-table__head
    background: #FCFEFF
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05)
    &::before, &::after
      background: #FCFEFF
  .n-toolbar
    color: #142546
  .n-toolbar__cards
    background-image: url(../img/wallet/cards-light.svg)
  .n-toolbar__cards.active
    background-image: url(../img/wallet/cards-check-light.svg)
  .n-toolbar__list
    background-image: url(../img/wallet/list-light.svg)
  .n-toolbar__list.active
    background-image: url(../img/wallet/list-active-light.svg)
  .n-toolbar__favorite-label
    background-image: url(../img/wallet/favorite-light.svg)
  .n-wallet-table__list-item
    background: #FCFEFF
  .n-wallet-table__list-balance
    @media (max-width: 1104px)
      border-top: 1px solid #BAE7F6
  .n-wallet-table__list-blocked
    @media (max-width: 1104px)
      border-bottom: 1px solid #BAE7F6 
  .n-wallet-table__list-favorite
    background-image: url(../img/wallet/favorite-small-light.svg)
  .n-wallet-table__list-favorite.check
    background-image: url(../img/wallet/favorite-check.svg)
  .n-wallet-table .n-table.cards .n-wallet-table__list-balance
    border-top: 1px solid #BAE7F6
  .n-wallet-table .n-table.cards .n-wallet-table__list-blocked
    border-bottom: 1px solid #BAE7F6
  .n-wallet-table .n-table.cards .n-wallet-table__list-item--soon
    border: 1px dashed #BAE7F6
    background: rgba(252, 254, 255, 0.77)