.n-profile-table__verification
    padding: 12px 12px 0
    color: #fff
    &-title
        font-weight: 600
        font-size: 16px
        line-height: 20px
        &:not(:first-child)
            margin-top: 32px
    &-limits
        margin-top: 8px
        display: grid
        column-gap: 7px
        row-gap: 7px
        grid-template-rows: 1fr 1fr
        grid-template-columns: 1fr 1fr
        @media (max-width: 768px)
            display: block
    &-limit
        padding: 16px
        display: flex
        align-items: center
        background: #213151
        border-radius: 10px
        @media (max-width: 768px)
            &:not(:first-of-type)
                margin-top: 6px
    &-img
        padding: 8px
        background: #3D5481
        border-radius: 8px
        margin-right: 12px
    &-count
        font-size: 14px
        line-height: 17px
    &-name
        margin-top: 4px
        font-size: 12px
        line-height: 15px
        color: #AFB6BA
        &--green
            color: #1BD079
    &-list
        margin-top: 8px
    &-item
        display: flex
        align-items: center
        padding: 8px 25px 
        background: #213151
        border-radius: 10px
        &:not(:first-child)
            margin-top: 6px
        @media (max-width: 768px)
            flex-wrap: wrap
            padding: 12px 16px
    &-icon
        width: 24px
        margin-right: 8px
        &--light
            display: none
        @media (max-width: 768px)
            margin-bottom: 23px
    &-target
        font-size: 14px
        line-height: 17px
        margin-right: auto
        @media (max-width: 768px)
            width: calc(100% - 32px)
            margin-bottom: 23px
    &-info
        padding: 8px 31px 8px 39px
        background: rgba(27, 208, 121, 0.07)
        border-radius: 100px
        font-size: 12px
        line-height: 15px
        color: #1BD079
        position: relative
        &::before
            position: absolute
            content: ""
            width: 16px
            height: 16px
            left: 19px
            top: calc(50% - 8px)
            background-image: url(../img/profile/check-green.svg) 
            background-repeat: no-repeat
            background-position: center
        &--half
            padding: 8px 28px 8px 48px
            color: #F3BE00
            background: #3C4546
            &::before
                left: 28px
                background-image: url(../img/profile/focus-yellow.svg) 
        &--error
            padding: 8px 19px 8px 32px
            color: #FD5454
            background: rgba(253, 84, 84, 0.12)
            &::before
                left: 12px
                background-image: url(../img/profile/focus-red.svg) 
    &-link
        display: block
        margin-left: 12px
        color: #3aa3e6
        font-weight: 600
        font-size: 14px
        line-height: 1.22
        padding: 7px 10px
        border: 1px solid #44AAEC
        border-radius: 7px
        background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
        -webkit-background-clip: text
        -webkit-text-fill-color: transparent
        background-clip: text
        text-fill-color: transparent
        transition: all 0.3s linear
        cursor: pointer
        @media (max-width: 360px)
            margin-top: 10px
            margin-left: 0
            width: 50%
            text-align: center
        &:hover, &:active 
            text-fill-color: initial
            -webkit-text-fill-color: initial
            color: #fff
            background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
.light .n-profile .n-profile-table__verification
    color: #142546
    .n-profile-table__verification-limit
        background: #FCFEFF
    .n-profile-table__verification-item
        background: #FCFEFF
    .n-profile-table__verification-icon
        display: none
    .n-profile-table__verification-icon--light
        display: block
    .n-profile-table__verification-img
        background: #DEEAFF
    .n-profile-table__verification-info--half
        background: #FBFAED