@mixin historycolsizes()
	&:nth-child(1)
		width: 5.5%
	&:nth-child(2)
		width: 8.7%
	&:nth-child(3)
		width: 10%
		text-align: right
	&:nth-child(4)
		width: 14%
		text-align: right

.n-market
	&__history
		grid-column: 1/3
		grid-row: 5
		border-right: 4px solid #14223B
		padding: 8px 0 32px
		@media screen and (max-width: 1200px)
			border-bottom: 4px solid #14223b
			border-right: none
			.light &
				border-bottom: 4px solid #e5f4f8
				border-right: none
		@media screen and (max-width: 900px)
			border-bottom: 0
			padding: 12px 0 20px
		&-tabs
			padding-left: 12px
			padding-right: 12px
			@media screen and (max-width: 900px)
				&::after
					content: ''
					display: block
					width: 100%
					height: 1px
					background-color: rgba(64, 167, 234, 0.35)
					border-radius: 1px
					margin-top: 8px
					overflow: hidden
		&-tab
			position: absolute
			z-index: -1
			opacity: 0
			& + label
				cursor: pointer
				transition: all 0.3s ease-in-out
				margin-right: 20px
				font-weight: 700
				font-size: 14px
				line-height: 17px
				color: #7993ad
				&:hover, &:active
					color: #40A7EA
				@media (max-width: 360px)
					margin: 0
					font-size: 13px
					&:not(:last-child)
						margin-right: 5px
			&:checked + label
				cursor: default
				color: #40A7EA!important
			&--mobile
				& + label
					@media screen and (min-width: 901px)
						display: none
					@media screen and (max-width: 900px)
						margin-right: 0
		&-content
			display: none
			filter: blur(0)!important
			&.active
				display: block
			@media screen and (max-width: 900px)
				& .simplebar-content
					&:after
						display: none
		&-inner
			overflow-y: hidden
			@media (min-width: 692px)
				margin-top: 13px
			&--less
				margin-top: 13px
				@media screen and (max-width: 900px)
					max-width: 100%
				@media (max-width: 691.5px)
					& .n-market__history-body
						height: 241px
				& .n-market__history-col
					@media screen and (min-width: 901px)
						@include historycolsizes()
					@media screen and (max-width: 900px)
						.n-market__history-content:not([data-tab="history-tab-1"]) &
							@include historycolsizes()
					@media (max-width: 576px)
						padding-top: 0
		&-head
			margin-right: 18px
			padding-right: 12px
			padding-left: 12px
			margin-bottom: 12px
			display: flex
			list-style-type: none
			justify-content: space-between
			font-weight: 400
			font-size: 12px
			line-height: 1.21
			@media screen and (max-width: 900px)
				.n-market__history-content[data-tab="history-tab-1"] &
					display: none
		&-body
			margin-right: 18px
			padding-right: 12px
			padding-left: 12px
			height: 200px
			font-weight: 400
			font-size: 12px
			line-height: 1.21
			-ms-overflow-style: none
			scrollbar-width: none
			&::-webkit-scrollbar 
				-webkit-appearance: none
				width: 0
				height: 0
				display: none
			@media (max-width: 900px)
				height: 423px
				.n-market__history-content[data-tab="history-tab-1"] &
					padding-right: 0
					margin-right: 12px
			@media (max-width: 691.5px)
				height: 400px
				margin-right: 8px
				.n-market__history-content[data-tab="history-tab-1"] &
					padding-right: 0
					padding-top: 7px
			&_wrapper
				@media (max-width: 900px)
					display: grid
					grid-template-columns: 1fr
					gap: 10px
					max-width: 100%
					@media (max-width: 691.5px)
						row-gap: 0
					@media (max-width: 360px)
						grid-template-columns: repeat(auto-fit, calc(100%))
		&-login
			margin-top: 103px
			text-align: center
			max-width: 100vw
			& p
				font-weight: 500
				font-size: 12px
				line-height: 15px
				& a
					color: #40a7ea
		&-row
			display: flex
			justify-content: space-between
			list-style-type: none
			align-items: center
			padding: 2px 11px 2px 7px
			margin-left: -7px
			margin-right: -11px
			@media screen and (min-width: 901px)
				&:nth-child(odd)
					background-color: #263a5f
				&:not(:first-child)
					margin-top: 4.5px
				&:hover, &:active
					background-color: #304875
			@media screen and (max-width: 900px)
				.n-market__history-content[data-tab="history-tab-1"] &
					display: grid
					grid-template-columns: auto minmax(100px, max-content) minmax(120px, max-content) minmax(120px, max-content) minmax(120px, max-content) 1fr   
					grid-template-rows: auto auto  
					justify-content: start
					align-self: center
					padding-bottom: 8px
					padding: 7px 8px 7px 0
					margin: 0
					overflow: hidden
					border-top: 0
					border-bottom: 1px solid #5270A7
					border-radius: 0
					&:first-child
						padding-top: 0
			@media screen and (max-width: 691.5px)
				&:not(:last-child)
					position: relative
					&::after
						content: ''
						display: block
						position: absolute
						bottom: 0
						left: 0px
						width: 100%
						height: 1px
						background: rgba(64, 167, 234, 0.35)
				.n-market__history-content[data-tab="history-tab-1"] &
					border: none
					border-radius: 0
					padding: 8.5px 0
					grid-template: [start] "pair .data" auto [row2] "type type type" auto [row3] "direction direction direction" auto [row4] ".volume ." auto [row5] ".price ." auto [row6] ".total action" auto [end] / 1fr 1fr 1fr
		&-col
			white-space: nowrap
			word-break: break-all
			@media screen and (min-width: 901px)
				text-align: center
			@media screen and (max-width: 900px)
				font-size: 12px
				font-weight: 500
				line-height: 1.25
				padding-top: 3px
				padding-bottom: 3px
				white-space: initial
			@media (max-width: 691.5px)
				&:nth-child(7), &:nth-child(8)
					padding-bottom: 0
					padding-top: 0
			&--red
				color: #FD5454
			&--green
				color: #1BD079
			&:nth-child(1)
				@media screen and (min-width: 901px)
					width: 12.3%
					text-align: left
				@media screen and (max-width: 900px)
					.n-market__history-content[data-tab="history-tab-1"] &
						grid-row: 1
						grid-column: 6/7
						justify-self: end
				@media screen and (max-width: 691.5px)
					.n-market__history-content[data-tab="history-tab-1"] &
						grid-area: data
			&:nth-child(2)
				@media screen and (min-width: 901px)
					width: 12.9%
				@media screen and (max-width: 900px)
					.n-market__history-content[data-tab="history-tab-1"] &
						grid-row: 1
						grid-column: 1/3
				@media screen and (max-width: 691.5px)
					.n-market__history-content[data-tab="history-tab-1"] &
						grid-area: pair
						font-size: 14px
						font-weight: 600
						line-height: 1.2
			&:nth-child(3)
				@media screen and (min-width: 901px)
					width: 10.4%
				@media screen and (max-width: 900px)
					padding-bottom: 0
					white-space: nowrap
					.n-market__history-content[data-tab="history-tab-1"] &
						grid-row: 2/3
						grid-column: 1
						&::after
							content: '/'
							display: inline-block
				@media screen and (max-width: 691.5px)
					.n-market__history-content[data-tab="history-tab-1"] &
						grid-area: type
			&:nth-child(4)
				@media screen and (min-width: 901px)
					width: 15.5%
				@media screen and (max-width: 900px)
					.n-market__history-content[data-tab="history-tab-1"] &
						grid-row: 2
						grid-column: 2/3
						padding-bottom: 0
				@media screen and (max-width: 691.5px)
					padding-top: 0
					.n-market__history-content[data-tab="history-tab-1"] &
						padding-bottom: 4px
						grid-area: direction
			&:nth-child(5)
				@media screen and (min-width: 901px)
					width: 13.9%
					text-align: right
				@media screen and (max-width: 900px)
					.n-market__history-content[data-tab="history-tab-1"] &
						grid-row: 2
						grid-column: 4/5
				@media screen and (max-width: 691.5px)
					.n-market__history-content[data-tab="history-tab-1"] &
						grid-area: price
			&:nth-child(6)
				@media screen and (min-width: 901px)
					width: 13.7%
					text-align: right
				@media screen and (max-width: 900px)
					.n-market__history-content[data-tab="history-tab-1"] &
						grid-row: 2
						grid-column: 3/4
				@media screen and (max-width: 691.5px)
					.n-market__history-content[data-tab="history-tab-1"] &
						grid-area: volume
			&:nth-child(7)
				@media screen and (min-width: 901px)
					width: 11%
					text-align: right
				@media screen and (max-width: 900px)
					.n-market__history-content[data-tab="history-tab-1"] &
						grid-row: 2
						grid-column: 5/6
				@media screen and (max-width: 691.5px)
					.n-market__history-content[data-tab="history-tab-1"] &
						grid-area: total
			&:nth-child(8)
				@media screen and (min-width: 901px)
					width: 8.4%
					margin-left: auto
				@media screen and (max-width: 900px)
					.n-market__history-content[data-tab="history-tab-1"] &
						justify-self: end
						font-weight: 600
						line-height: 1.5
						grid-row: 2
						grid-column: 6/7
				@media screen and (max-width: 691.5px)
					.n-market__history-content[data-tab="history-tab-1"] &
						grid-area: action
			&:nth-child(5), &:nth-child(6), &:nth-child(7)
				@media screen and (max-width: 900px)
					.n-market__history-content[data-tab="history-tab-1"] &
						position: relative
						padding-right: 10px
						&::before
							content: attr(data-title) ":"
							display: inline-block
							width: 100%
							position: absolute
							top: -27px
							bottom: 0
							left: 0
							font-size: 12px
							font-weight: 500
							padding: 3px 0
				@media screen and (max-width: 691.5px)
					.n-market__history-content[data-tab="history-tab-1"] &
						&::before
							top: 0
							bottom: 0
							left: -100%
							margin: auto 0

			&:nth-child(7)
				@media (max-width: 691.5px)
					.n-market__history-content[data-tab="history-tab-1"] &
						&::before
							padding-top: 0
							padding-bottom: 0
		&-btn
			border: none
			outline: none
			background: transparent
			font-size: 12px
			color: #40A7EA
			@media screen and (min-width: 901px)
				padding: 0
				font-weight: 500
				line-height: 1.21
				text-decoration: underline
			@media screen and (max-width: 900px)
				.n-market__history-content[data-tab="history-tab-1"] &
					box-sizing: border-box
					font-weight: 600
					line-height: 1.5
					border: 2px solid #40A7EA
					border-radius: 10px
					padding: 3px 7px
	&__inner
		@media screen and (max-width: 900px)
			overflow-x: hidden
