.dark-icon
  display: none
.light
  .n-first-screen__market
    background: rgba(255, 255, 255, 0.44)
    color: #0F1C35
  .n-first-screen__market .n-market__bidding::before
    background-color: #A6D2FA
  .n-market__bidding-balance
    color: #123D56
  .n-first-screen__market .n-market__bidding-toggle
    background: #FFFFFF
    border: 1px solid #BFD5FF
    color: #142546
  .n-first-screen__market .n-market__bidding-tabs
    background: #FFFFFF
    border: 1px solid #3D5481
  .n-first-screen__market .n-market__bidding-tab + label
    color: #0F1C35
  .n-first-screen__market .n-market__bidding-tab.active + label
    color: #40A7EA
  .n-first-screen__market .n-market__bidding-input
    border: 2px solid #BFD5FF
    color: #0F1C35
    background: #F3F7FF
    &:focus-visible
      border: 2px solid #40A7EA
  .n-first-screen__text h1, .n-first-screen__text p
    color: #142546
  .n-first-screen
    background: url(../img/main/lightBack2.jpg) no-repeat bottom center
    background-size: 100% 100%
  .n-first-screen__text a
    background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
  .first-screen__text a
    background: linear-gradient(180deg, #2D98DE 0%, #44AAEC 100%)
  .n-mainTables
    background: url(../img/main/tablesLightBack.png) no-repeat bottom center
    background-size: 100% 100%
  .n-mainTables__item-head
    border-bottom: 1px solid #AFE0FF
    background: #FCFEFF
  .n-mainTables__item-body
    background: #FCFEFF

  .n-mainTables__item-head-text p.name,
  .n-mainTables__item-head-text p.volume,
  .n-mainTables__item-body-head p
    color: #142546
  .n-mainTables__item-body-item p,
  .n-mainTables__item-body-item a
    color: #142546!important
    &.sell
      color: #FD5454!important
    &.buy
      color: #1BD079!important
  .n-mainTables__item-head-right a
    color: #90CDF1
  .n-mainTables__item-body-table
    &::-webkit-scrollbar-track
      background: #FCFEFF
    &::-webkit-scrollbar
      width: 3px
    &::-webkit-scrollbar-thumb
      background: #DEF2FA
  .n-counters
    background: #DCE8FF
  .n-counters_item_prompt
    color: #142546
  .n-exchange
    background: #ECF8F9
  .n-exchange_title,
  .n-exchange__list-item p
    color: #142546
  .n-exchange__list-item img,
  .n-exchange_img
    display: none
    &.light
      display: block
  .n-exchange__bidding-bg
    background: url(../img/main/exchaneLight2.png) no-repeat center
  .n-exchange__payment img
    display: none
    &.light 
      display: block
  .n-exchange
    background-image: url(../img/main/exchangeBackLight2.png) 
    background-color: #A8C8F4
    background-size: 100% 100%
  .n-exchange__payment
    img
      display: none
      &.light
        display: block
  .n-exchange .n-market__bidding
    background: rgba(255, 255, 255, 0.64)
  .n-about-us
    background: url(../img/main/lightBack.png) no-repeat bottom center
    background-size: 100% 100%
  .n-about-us__circle-item p,
  .n-about-us_title
    color: #142546
  .n-about-us__circle-item img,
  .n-about-us__circle_ellipse,
  .n-about-us__circle_telephone
    display: none
    &.light
      display: block
  .n-for-beginner
    background: linear-gradient(0deg, #EAF7F9 0%, #BDE1F7 100%)
  .n-for-beginner__item-content
    background: #EDFAFF
  .n-for-beginner__item-content p
    color: #142546
  .n-main-help
    background: #E8F6F9
  .n-main-help__content
    background: #EDFAFF63
  .n-main-help__content_title,
  .n-main-help__content_prompt,
  .n-main-help__content-card_prompt,
  .n-main-help__content-card a
    color: #142546
  .n-main-help__content-card
    border: 0
    box-shadow: 0 15px 30px 0 #00000025
  .n-footer__col ul li a,
  .n-footer__docs-wrapper a,
  .n-footer__col-social a,
  .n-for-beginner_title
    color: #142546
  .n-main-help__content .n-main-help__content-card
    background: #FCFEFF
  .n-mainTables__tabs-item
    border-bottom: 1px solid #C6E9FD
@media screen and (max-width: 1200px)
  .light
    .n-first-screen
      background: url(../img/main/fsMLightBack.png) no-repeat bottom center
      background-size: 100% 100%
      @media screen and (max-width: 767px)
        background-image: url(../img/main/firstScreenMob-l.png)
        background-repeat: no-repeat
        background-position: top center
        background-size: 100% 38%
        background-color: #c6e6ff
    .n-first-screen__text .n-first-screen__link
      color: #142546
      border: 2px solid #1BD079
      background: #BAE1FF
      &::before
        background-color: #142546
      &::after
        background-color: #142546
    .n-first-screen__text .n-first-screen__link--p2p
      border: 2px solid #40A7EA
      
    .n-about-us
      background: url(../img/main/aboutUsMLightBack.png) no-repeat bottom center
      background-size: 100% 100%
    .n-exchange
      background: #ECF8F9
    .n-footer
      background: url(../img/main/footerMLightBack.png) no-repeat bottom center
      background-size: 100% 100%
    .n-mainTables__tabs
      background: #FCFEFF
    .n-mainTables__tabs-head p,
    .n-mainTables__tabs-item .n-mainTables__tabs-name-col p,
    .n-mainTables__tabs-item .n-mainTables__tabs-price-col p,
    .n-mainTables_title
      color: #304B57
    .n-mainTables
      background: linear-gradient(178.57deg, #AFE0FF -39.57%, #ECF8F9 101.58%)

