.n-p2p-table
    display: flex
    flex-direction: column
    & .n-pages 
        margin-top: auto
    &__blocks
        margin-top: 12px
        padding: 0 6px
        @media (max-width: 900px)
            padding: 0
            margin-top: 0
        &:not(:first-child)
            margin-top: 8px
        &:last-of-type
            margin-bottom: 17px
    &__block
        border: 1px solid #4f689b
        position: relative
        background: #304368
        @media (max-width: 900px)
            margin-top: 24px
            border-radius: 0!important
            border-left: 0
            border-right: 0
            border-top: 0
            border-bottom: 0
        &:not(:last-child)
            border-bottom: 0
        &:first-child:last-child
            border-radius: 10px
        &:first-child
            border-radius: 10px 10px 0 0
            @media (max-width: 900px)
                margin-top: 0
        &:last-child
            border-radius: 0 0 10px 10px 
        &.blocked 
            background: #2A3B5C
            & .n-p2p-table__details
                background: #2A3B5C
                & .n-p2p-table__details-button.green
                    opacity: 0.5
                & .n-p2p-table__details-button.red
                    opacity: 0.5
        &.ad 
            border: 1px solid #F3BE00
            &:not(:last-child)
                border-bottom: 0
                @media (max-width: 900px)
                    border-bottom: 1px solid #F3BE00
            & .n-p2p-table__row + .n-p2p-table__details 
                border-bottom: 0
            & .n-p2p-table__details + .n-p2p-table__row
                margin-top: 8px
                border-top: 0 solid rgba(137, 176, 253, 0.34)
            &::before
                z-index: 1
                content: ""
                position: absolute
                top: 6px
                left: 6px
                width: 16px
                height: 16px
                background: url('../img/p2p/ad.svg') center no-repeat
    &__row
        display: flex
        align-items: center
        justify-content: space-between
        position: relative
        padding: 17px 14px
        font-size: 12px
        @media (max-width: 900px)
            display: block
            padding: 14px 
            font-weight: 500
        & + .n-p2p-table__row
            border-top: 1px solid rgba(137, 176, 253, 0.34)
        &--head
            border-radius: 0
            padding: 16px 18px
            background: #273755
            box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05)
            font-weight: 700
            font-size: 14px
            line-height: 17px
            color: #40A7EA
            @media (max-width: 900px)
                display: none
        &.active 
            border-bottom: 0
            position: relative
            &::after
                position: absolute
                content: ""
                height: 1px
                width: calc(100% - 28px)
                left: 14px
                bottom: 0
                background: #4E689B
            & + .n-p2p-table__details 
                border-bottom: 1px solid #4E689B
                display: block
                padding: 12px 0 16px
            & .n-p2p-table__column-action
                display: none
    &__column
        @media (max-width: 900px)
            display: flex
            align-items: center
            width: 100%!important
            justify-content: space-between
            padding: 14px 0
            position: relative
            min-height: 60px
            &:first-child
                padding-top: 0
            &:last-child
                padding-bottom: 0
                &::after
                    display: none
            &::after
                content: ""
                position: absolute
                bottom: 0
                left: 0
                width: 100%
                height: 1px
                background: #4b6393
                border-radius: 10px
        &:nth-child(1)
            width: 22%
            text-align: left
        &:nth-child(2)
            width: 13%
            text-align: center
        &:nth-child(3)
            width: 26%
            text-align: center
        &:nth-child(4)
            width: 17.5%
            text-align: center
        &:nth-child(5)
            width: 16%
            text-align: right
            cursor: pointer
    &__column-title
        display: none
        font-weight: 700
        font-size: 14px
        line-height: 1.2
        color: #40A7EA
        @media (max-width: 900px)
            display: block
            text-align: left
    &__column-seller
        display: grid
        grid-template-columns: 33px auto
        grid-template-rows: auto auto
        align-items: center
        justify-items: start
        column-gap: 8px
        @media (max-width: 900px)
            display: flex
            flex-direction: column
            align-items: flex-end
    &__column-avatar
        text-align: center
        grid-column: 1/2
        grid-row: 1/3
        width: 33px
        height: 33px
        background: #142443
        border-radius: 50%
        font-weight: 600
        font-size: 12px
        line-height: 15px
        padding: 9px 2px
        @media (max-width: 900px)
            display: none
        &.gradient
           background: linear-gradient(90.8deg, #FFAF47 0.62%, #FF6FE8 100%)
    &__column-name
        font-weight: 500
        font-size: 12px
        line-height: 15px 
        &:hover
            & .n-p2p-table__verified
                opacity: 1
                z-index: 10
        &.verified
            align-self: start
            position: relative
            padding-right: 14px
            @media (max-width: 900px)
                align-self: initial
            &::after
                content: ""
                width: 12px
                height: 12px
                right: 0
                top: 2.5px
                position: absolute
                background-image: url(../img/profile/check.svg)
    &__verified
        opacity: 0
        position: absolute
        top: -52px
        color: #fff
        background: #213151
        border: 1px solid #40a7ea
        border-radius: 8px
        font-size: 12px
        line-height: 1.3
        text-align: center
        padding: 15px 9px
        min-width: 36px
        z-index: -1
        right: -49%
        transition: all .3s linear
        &::before
            width: 24px
            height: 8px
            content: ""
            position: absolute
            bottom: -8px
            left: calc( 50% - 12px )
            background-image: url(../img/p2p/main/triangle-hint.svg)
            background-size: contain
            background-position: center
            background-repeat: no-repeat
    &__column-stat
        margin-top: 6px
        font-weight: 400
        font-size: 12px
        line-height: 15px 
        color: #40A7EA
        position: relative
        @media (max-width: 900px)
            text-align: right
        &:hover , &:active 
            & .n-p2p-table__column-popup
                opacity: 1
                z-index: 10
    &__column-popup
        opacity: 0
        position: absolute
        bottom: 20px
        color: #fff
        background: #213151
        border: 1px solid #40a7ea
        border-radius: 8px
        font-size: 12px
        line-height: 1.3
        text-align: center
        padding: 15px 9px
        min-width: 156px
        max-width: 168px
        z-index: -1
        right: -40px
        transition: all .3s linear
        text-align: left
        @media (max-width: 900px)
            right: calc( 100% + 12px )
            bottom: 50%
            transform: translateY(50%)
        & dl 
            font-size: 12px
            margin-bottom: 8px
        & dt
            font-weight: 400
            margin-right: 3px
            margin-bottom: 3px
            float: left
            &::after
                content: '\0a'
                white-space: pre
        & dd
            min-width: 18px
            font-weight: 500
            margin-bottom: 3px
            margin-left: 0
        &::before
            width: 24px
            height: 8px
            content: ""
            position: absolute
            bottom: -8px
            left: calc( 50% - 12px )
            background-image: url(../img/p2p/main/triangle-hint.svg)
            background-size: contain
            background-position: center
            background-repeat: no-repeat
            @media (max-width: 900px)
                bottom: calc( 50% - 5px )
                left: auto
                right: -16px
                transform: rotate(-90deg)
        & a
            color: #40A7EA
            font-weight: 600
            font-size: 12px
            line-height: 1.5
    &__column-price
        text-align: center
        font-weight: 500
        font-size: 12px
        line-height: 15px
        @media (max-width: 900px)
            text-align: right
    &__column-block
        text-align: left
        @media (max-width: 900px)
            text-align: right
    &__column-extra
        margin-top: 5px
        font-size: 12px
        line-height: 15px
        color: #AFB6BA
    &__column-limit
        font-size: 12px
        line-height: 15px
    &__column-methods
        font-size: 10px
        line-height: 12px
        display: flex
        flex-wrap: wrap
        gap: 4px
        @media (max-width: 900px)
            justify-content: flex-end
    &__column-method
        padding: 4px 8px
        background: rgba(120, 130, 224, 0.19)
        border-radius: 1px
        position: relative
        &:hover
            & .n-p2p-table__bank
                opacity: 1
                z-index: 10
    &__column-type
        font-size: 14px
    &__column-time
        margin-top: 5px
        color: #AFB6BA
        font-size: 12px
        line-height: 1.3
    &__column-amount
        font-size: 14px
    &__column-contractor
        font-size: 14px
    &__column-status
        font-size: 14px
        position: relative
        padding-right: 42px
        &.timer
            padding-right: 0
            padding-right: 38px
            &::after
                position: absolute
                content: " "
                width: 16px
                height: 16px
                background: url(../img/profile/hint.svg) center no-repeat
                left: 44px
                top: calc( 50% - 9px )
                pointer-events: none
        &::after
            position: absolute
            content: " "
            width: 16px
            height: 16px
            background: url(../img/p2p/arrow-next.svg) center no-repeat
            right: 0
            top: calc( 50% - 8px )
    &__column-timer
        margin-right: 9px
        padding-right: 20px
        width: 56px
        &:hover
            & + .n-p2p-table__hint
                opacity: 1
                z-index: 10
    &__column-controls
        @media (max-width: 900px)
            display: flex
            align-items: center
    &__hint 
        pointer-events: none
        opacity: 0
        position: absolute 
        top: calc( 50% - 35.5px )
        color: #fff
        background: #213151
        border: 1px solid #40a7ea
        border-radius: 8px
        font-size: 12px
        line-height: 1.3
        text-align: center
        padding: 10px 17.5px
        z-index: -1
        left: -214px
        width: 206px
        transition: all .3s linear
        &::before
            width: 24px
            height: 8px
            content: ""
            position: absolute
            bottom: calc( 50% - 4px )
            right: -17px
            background-image: url(../img/p2p/main/triangle-hint.svg)
            background-size: contain
            background-position: center
            background-repeat: no-repeat
            transform: rotate(-90deg)
    &__bank
        opacity: 0
        position: absolute
        top: -58px
        color: #fff
        background: #213151
        border: 1px solid #40a7ea
        border-radius: 8px
        font-size: 12px
        line-height: 1.3
        text-align: center
        padding: 15px
        min-width: 36px
        z-index: -1
        right: -20%
        transition: all .3s linear
        &::before
            width: 24px
            height: 8px
            content: ""
            position: absolute
            bottom: -8px
            left: calc( 50% - 12px )
            background-image: url(../img/p2p/main/triangle-hint.svg)
            background-size: contain
            background-position: center
            background-repeat: no-repeat
    &__column-action
        text-align: center
        font-weight: 600
        font-size: 14px
        line-height: 17px
        padding: 8px
        background: #14B467
        border-radius: 7px
        cursor: pointer
        &.red
            background: #FD5454
        @media (max-width: 900px)
            min-width: 129px
    &__details
        display: none
        background: #304368
        border-radius: 10px 
        & + .n-p2p-table__row
            border-top: 0
    &__details-inner 
        display: flex
        column-gap: 20px
        margin: 0 14px
        @media (max-width: 900px)
            display: block
    &__details-column
        @media (max-width: 900px)
            width: 100%
    &__details-column:not(:first-child)
        flex: 1 0
        display: flex
        flex-direction: column
        justify-content: space-between
        @media (max-width: 900px)
            margin-top: 8px
            flex: 1 0 100%
    &__details-textarea
        margin: 0
        position: relative
        width: 465px
        height: 100%
        background: #2C3E61
        border: 2px solid #4A6393
        border-radius: 10px
        padding: 34px 15px 6px
        display: flex
        @media (max-width: 1200px)
            width: 405px
        @media (max-width: 900px)
            width: 100%
            height: 200px
        & p
            outline: none
            width: 100%
            height: 100%
            max-height: 260px
            font-size: 12px
            line-height: 15px
            color: #fff
            resize: none
            &::-webkit-scrollbar
                width: 0
                -ms-overflow-style: none
                overflow: -moz-scrollbars-none
            & .simplebar-scrollbar:before
                background: #40A7EA
            & .simplebar-scrollbar.simplebar-visible:before
                opacity: 1
                border-radius: 100px
            .simplebar-track.simplebar-vertical
                width: 8px
                right: -12px
            &::-webkit-scrollbar
                width: 0
                -ms-overflow-style: none
                overflow: -moz-scrollbars-none
        & textarea
            background: none
            border: 0
            font-size: 12px
            color: #ffffff
            width: 100%
            height: calc( 100% - 12px)
            resize: none
            padding-right: 5px
            margin-right: 5px
            &:focus-visible
                outline: none
                border: none
            &::-webkit-scrollbar 
                width: 4px
                height: 4px
            &::-webkit-scrollbar-button:start:decrement,
            &::-webkit-scrollbar-button:end:increment  
                height: 4px
                background-color: transparent
            &::-webkit-scrollbar-track-piece  
                background-color: transparent
                -webkit-border-radius: 16px
            &::-webkit-scrollbar-thumb:vertical 
                height: 4px
                background-color: #3570a3
                border: 0 solid #eee
                -webkit-border-radius: 16px
        & label
            position: absolute
            z-index: 10
            font-weight: 600
            color: #fff
            font-size: 12px
            line-height: 15px
            top: 12px
            left: 15px
    &__details-deal
        width: 100%
        max-width: 365px
        padding: 16px
        background: #2C3E61
        border: 2px solid #4A6393
        border-radius: 10px
        @media (max-width: 1200px)
            max-width: 100%
    &__details-blocked
        color: #fff
        text-align: center
        & h5 
            font-weight: 600
            font-size: 14px
            line-height: 1.2
            color: #FD5454
        & p
            margin-top: 12px 
            font-weight: 600
            font-size: 12px
            line-height: 1.4
        & ul
            text-align: left
            font-size: 12px
        & li
            padding-left: 15px
            position: relative
            & b
                font-weight: 500
            &::before
                position: absolute
                content: ""
                width: 2px
                height: 2px
                top: 7.5px
                left: 6px
                background-color: #fff
                border-radius: 50%
    &__details-input
        display: flex
        flex-direction: column
        position: relative
        &:not(:first-child)
            margin-top: 24px
        & label
            font-weight: 600
            font-size: 12px
            line-height: 15px
            color: #fff
            margin-bottom: 4px
        & input
            background: #2A3B5C
            border: 2px solid #5578BC
            border-radius: 10px
            padding: 12px 12px 12px 16px
            font-size: 12px
            line-height: 1.5
            color: #fff
            &:focus-visible
                outline: none
            &::placeholder
                color: #AFB6BA
        & span
            position: absolute
            top: 33px
            right: 12px
            font-size: 12px
            line-height: 1.5
            color: #fff
        & p
            margin-top: 4px
            color: #FD5454
            font-size: 12px
            line-height: 1.4
    &__details-buttons
        display: flex
        margin-top: 12px
        column-gap: 10px
    &__details-button
        flex: 1 0 calc( 50% - 5px )
        border: 2px solid #4A6393
        padding: 8px
        border-radius: 7px
        text-align: center
        font-weight: 600
        font-size: 14px
        line-height: 1.2
        color: #2D98DE
        cursor: pointer
        &.red 
            color: #fff
            background: #FD5454
            border: 2px solid #FD5454
        &.green 
            color: #fff
            background: #14B467
            border: 2px solid #14B467

