.n-main-help
  //background: linear-gradient(0deg, #131D32 0%, #223D58 100%)
  background: #131D32
  font-family: 'Montserrat', sans-serif
  padding: 64px 0
  overflow: hidden
  .container
    position: relative
    display: flex
  &__content
    overflow: hidden
    -webkit-transform: translate3d(0,0,0)
    border: 1px solid #89B0FD
    background: #213B5663
    border-radius: 10px
    width: 985px
    padding: 42px 0
    display: flex
    flex-direction: column
    margin: 0 auto
    position: relative
    z-index: 2
    &::after
      z-index: -1
      position: absolute
      content: ""
      width: 100%
      height: 100%
      backdrop-filter: blur(10px)
      top: 0
      left: 0
    &_title
      text-align: center
      font-weight: 700
      font-size: 36px
      line-height: 44px
      color: #FFFFFF
      margin-bottom: 12px
    &_prompt
      font-size: 16px
      line-height: 24px
      text-align: center
      color: #FFFFFF
      margin: 0 auto 33px
      width: 384px
    &-card
      box-shadow: 0 30px 30px 0 #00000030
      border-radius: 10px
      border: 1px solid #213B56
      width: calc((100% - 40px) / 2)
      &-wrapper
        display: flex
        margin: 0 auto
        width: 600px
        justify-content: space-between
      &-info
        border-radius: 10px
        box-shadow: 0px -4px 0px 0px #40A7EA inset
        padding: 25px 0
        display: flex
        flex-direction: column
        & > svg
          margin: 0 auto 10px
      &_title
        color: #40A7EA
        font-size: 24px
        font-weight: 500
        line-height: 30px
        margin-bottom: 10px
        text-align: center
      &_prompt
        width: 235px
        color: #FFFFFF
        font-size: 16px
        line-height: 24px
        text-align: center
        margin: 0 auto 13px
      a
        display: flex
        margin: 0 auto
        font-size: 16px
        line-height: 24px
        font-weight: 500
        color: #FFFFFF
        transition: .1s
        svg
          margin: auto 0 auto 5px
        &:hover
          color: rgba(255,255,255,.8)
    .n-main-help__content-card
      background: #213B56
      &:first-child
        transform: rotate(-4deg)
        .n-main-help__content-card_icon
          transform: rotate(4deg)
      &:last-child
        transform: rotate(4deg)
        .n-main-help__content-card_icon
          transform: rotate(-4deg)
  &_rocket
    position: absolute
    left: -39px
    top: 187px
    width: 370px
  &_message
    position: absolute
    right: 43px
    top: 15px
  &_red-ball
    position: absolute
    left: 282px
    top: 149px
  &_green-ball
    position: absolute
    right: 305px
    bottom: -4px

@media screen and (max-width: 1400px)
  .n-main-help
    &_rocket
      left: -130px
    &_message
      right: -50px
    &_red-ball
      left: 195px
    &_green-ball
      right: 200px

@media screen and (max-width: 1200px)
  .n-main-help
    background: #131D32
    margin-top: -2px
    padding: 64px 0 32px
    &_rocket
      left: -208px
    &_message
      right: -132px
    &_red-ball
      left: 109px
    &_green-ball
      right: 106px

@media screen and (max-width: 991px)
  .n-main-help
    &_rocket
      left: -177px
    &_message
      right: -99px

@media screen and (max-width: 768px)
  .n-main-help
    background: linear-gradient(97.39deg, #142546 0.62%, #142341 100%)
    margin-top: -2px
    padding: 64px 0 16px
    .container
      max-width: 100%
    &__content
      width: 100%
      padding: 32px 18px 16px
      border-radius: 20px
      &_title
        font-weight: 700
        font-size: 30px
        line-height: 37px
        margin-bottom: 8px
      &_prompt
        margin: 0 auto 12px
        width: 100%
      &-card
        width: 100%
        margin-bottom: 16px
        &-wrapper
          width: 100%
          flex-direction: column
          justify-content: flex-start
        &_title
          margin-bottom: 26px
        &_prompt
          margin: 0 auto 26px
      .n-main-help__content-card
        &:first-child
          transform: rotate(0)
        &:last-child
          transform: rotate(0)
    &_rocket
      left: -147px
      top: 103px
    &_message
      width: 134px
      right: -21px
      top: -44px
      transform: rotate(-65deg)
    &_red-ball
      left: 68px
      top: 353px
    &_green-ball
      right: 33px
      top: 91px
      bottom: auto

@media screen and (max-width: 576px)
  .n-main-help
    overflow: hidden
    &_message
      right: -33px


@media screen and (max-width: 340px)
  .n-main-help
    &_green-ball
      right: 150px