$colorlist: (#40A7EA, #ED2661, #1BD079, #89B0FD)

.n-for-beginner
  padding: 64px 0
  background: linear-gradient(0deg, #131D32 0%, #223D58 100%)
  font-family: 'Montserrat', sans-serif
  &_title
      text-align: center
      font-weight: 700
      font-size: 36px
      line-height: 44px
      color: #FFFFFF
      margin-bottom: 32px
  &__list
      display: flex
      justify-content: space-between
  &__item
    width: calc((100% - 60px) / 4)
    border-radius: 10px
    box-shadow: 0 30px 30px 0 #00000030
    overflow: hidden
    transition: .2s
    @for $i from 1 through length($colorlist)
      &:nth-child(#{$i})
         .n-for-beginner__item-content
            box-shadow: 0px -4px 0px 0px nth($colorlist, $i) inset
            span, a, svg
               color: nth($colorlist, $i)
    &-content
      padding: 24px 24px 0
      display: flex
      flex-direction: column
      background: #152034
      & > svg
         margin: 0 0 34px auto
      p
         font-weight: 700
         font-size: 26px
         line-height: 38px
         text-align: center
         color: #FFFFFF
         margin-bottom: 42px
         span
            display: block
      a
         margin: auto 0 0 auto
    &.registration
      &:hover
         box-shadow: 0 30px 30px 0 #40A7EA36
    &.wallet
      &:hover
         box-shadow: 0 30px 30px 0 #ED266136
    &.change
      &:hover
         box-shadow: 0 30px 30px 0 #1BD07936
    &.ask
      &:hover
         box-shadow: 0 30px 30px 0 #89B0FD36

@media screen and (min-width: 1200px) and (max-width: 1500px)
  .n-for-beginner__item-content p
    font-size: 25px

@media screen and (max-width: 1200px)
  .n-for-beginner
    background: linear-gradient(0deg, #141F34 0%, #213B55 100%)
    margin-top: -2px
    padding: 32px 0 12px
    &_title
      font-size: 30px
      line-height: 37px
      margin-bottom: 24px
    &__list
      flex-wrap: wrap
      justify-content: center
    &__item
      width: 150px
      height: 153px
      margin: 0 10px 20px
      &-content
        padding: 14px 6px 12px
        height: 100%
        & > svg
         margin: 0 8px 0 auto
         width: 16px
         height: 16px
        p
         font-size: 13px
         line-height: 19px
         margin: auto 0
        a
         margin: 0 0 0 auto
         svg
            width: 16px
            height: 16px
@media screen and (max-width: 992px)
  .n-for-beginner
   &__list
      padding: 0
   .container
      max-width: 100%

@media screen and (max-width: 750px)
  .n-for-beginner__list
      padding: 0 100px

@media screen and (max-width: 600px)
  .n-for-beginner__list
      padding: 0 20px

@media screen and (max-width: 465px)
  .n-for-beginner__list
      padding: 0
